import React from 'react'
import "./Producto.css";

const ProductoCard = ({productos , cargando , verificarAccion }) => {
    if(cargando){
        return <h2>Cargando...</h2>
    }

    return (
        <ul>
            <div className="row justify-content-center">
                {productos.map( (producto,index) => {
                    return(    
                        <div className="col-9 col-sm-6 col-md-3 productoItem">
                            <li key={index}>
                                <div className="card cardProducto">
                                    <div className="card-header">
                                        <div className="nombreProducto">
                                            {producto.descripcionProducto}
                                        </div>
                                    </div>
                                    <div className="card-body infoProducto">
                                        <div className="cardImagen">
                                            <img src={producto.urlMedia} alt={producto.descripcionProducto} className="imagenProducto img-fluid"/>
                                        </div>
                                        <div>Marca: {producto.detalleProducto}</div>
                                        <div hidden={true}>Vence: {producto.fechaVencimiento.substring(0,10)}</div>   
                                        <div>{producto.nombreTipoProducto}</div>
                                        <div>{producto.descripcionPresentacion}</div>  
                                        <div className="form-group mt-2">
                                            <button type="button" 
                                                class="btn btn-outline-success"
                                                onClick={() => verificarAccion(producto.codigoProducto)}
                                            >Datos</button>
                                        </div>                                             
                                    </div>
                                </div>      
                            </li>
                        </div>
                    )
                })}
            </div>
        </ul>
    )
}

export default ProductoCard
