// GET CLIENTE API FROM SERVER NODE EXPRESS

import data from '../../db_data';
const BaseUrl = data + '/api/clientes/';

// MOSTRAR CLIENTE CODIGO
export function get_Cliente(codigoCliente){
    return new Promise((resolve,reject)=>{
        fetch(BaseUrl + codigoCliente)
        .then((Cliente) => Cliente.json())
        .then((ClienteJson) => {
            resolve(ClienteJson);
        })
        .catch((error)=>{
            reject(error);
        });
    });
}

// MOSTRAR CLIENTES
export function get_Cliente_Lista(){
    return new Promise((resolve,reject)=>{
        fetch(BaseUrl)
        .then((Clientes) => Clientes.json())
        .then((ClientesJson) => {
            resolve(ClientesJson);
        })
        .catch((error)=>{
            reject(error);
        });
    });
}

//ELIMINAR CLIENTE CODIGO

export function del_Cliente(codigoCliente){
    return new Promise((resolve,reject)=>{
        fetch(BaseUrl + codigoCliente,{
            method:'DELETE',
            headers: new Headers({
                'Content-type':'application/json'
            })
        })
        .then((response) => response.json())
        .then((responseJson) => {
            resolve(responseJson);
        })
        .catch((error)=>{
            reject(error);
        });
    });
}

// AGREGAR CLIENTE
export function post_Cliente(ClienteData){
        
    return new Promise((resolve,reject)=>{
        fetch(BaseUrl,{
            method:'POST',
            body: JSON.stringify(ClienteData),
            headers: new Headers({
                'Content-type':'application/json'
            })
        })
        .then((response) => response.json())
        .then((responseJson) => {
            resolve(responseJson);
        })
        .catch((error)=>{
            reject(error);
        });
    });
}

