// TRASLADO_DETALLE API FROM SERVER NODE EXPRESS

import data from '../../db_data';
const BaseUrl = data + '/api/traslados/detalles/';

// ----------------------------------------------------------------------------------------------------------------



// ELIMINAR DETALLES DE TRASLADO POR CODIGO DE TRASLADO ENVIO
export function del_Traslado_Detalle_CodigoTD(codigoTD){
    let urlFetch = BaseUrl + codigoTD;
    return new Promise((resolve,reject)=>{
        fetch(urlFetch,{
            method:'DELETE',
            headers: new Headers({
                'Content-type':'application/json'
            })
        })
        .then((response) => response.json())
        .then((responseJson) => {
            resolve(responseJson);
        })
        .catch((error)=>{
            reject(error);
        });
    });
}

