//CREAR DETALLE DE TRASLADO TEMPORAL
export function post_Traslado_Detalle_Temporal(detalleTraslado){ 
    const urlFetch = BaseUrl + "temporal";
    return new Promise((resolve,reject)=>{
        fetch(urlFetch,{
            method:'POST',
            body: JSON.stringify(detalleTraslado),
            headers: new Headers({
                'Content-type':'application/json'
            })
        })
        .then((response) => response.json())
        .then((responseJson) => {
            resolve(responseJson);
        })
        .catch((error)=>{
            reject(error);
        });
    });
}

