import React, { Component} from 'react';
import ReactTable from 'react-table';
import "react-table/react-table.css";

import ReactHTMLTableExcel from 'react-html-table-to-excel';

import { Modal , ModalHeader, ModalBody, Alert } from 'reactstrap';

import data from '../../../db_data';
import ExportToExcel from "../../Export/excel/tipoDocumento";
import { post_Tipo_Documento, put_Tipo_Documento, del_Tipo_Documento , get_Tipo_Documento , get_Tipo_Documento_Lista} from '../../Data/tiposData';

import Menu from '../../Pages/menu';

export default class tipoDocumentoLista extends Component {

    BaseUrl = data + '/api/tipos/documento';

    constructor (props){
        super(props);
        this.state = {
            tipoDocumento:[],
            filtered:[],
            tipoDocumentoExcel:[],
            message : {
                text:'',
                alert: ''
            },
            messageActivo:false,

            codigoTipoDocumento: "",
            nombreTipoDocumento: "",
            cantidadCaracteres: 0,

            tipoAccion:"post",
            modalOpen: false,
            tituloModal:"",


            modalConfirmarOpen: false,
            tipoEliminar : "",
            codigoTPEliminar : "",
        }
    }

    toogleModalConfirmar = () => {
        this.setState({ modalConfirmarOpen : ! this.state.modalConfirmarOpen });
    }

    mostrarModal = (codigoTipoDocumento) => {
        this.setState({ codigoTPEliminar : codigoTipoDocumento });
        this.toogleModalConfirmar();
    }

    borrarConfirmado = () => {
        const { codigoTPEliminar } = this.state;
        this.eliminarTipoDocumento(codigoTPEliminar);
        this.toogleModalConfirmar();
    }


    mostrarAlerta = (tiempo) => {
        setTimeout(()=>{
            this.setState({ message : { text : '' , alert : ''} });            
        },tiempo);
    }

    onFilteredChangeCustom = (value, accessor) => {
        let filtered = this.state.filtered;
        let insertNewFilter = 1;
    
        if (filtered.length) {
          filtered.forEach((filter, i) => {
            if (filter["id"] === accessor) {
              if (value === "" || !value.length) filtered.splice(i, 1);
              else filter["value"] = value;
              insertNewFilter = 0;
            }
          });
        }
    
        if (insertNewFilter) {
          filtered.push({ id: accessor, value: value });
        }
    
        this.setState({ filtered: filtered });
      };
    

    listarTipoDocumento(codigoTipoDocumento){
        if(codigoTipoDocumento !== "" && codigoTipoDocumento !== null){
            get_Tipo_Documento(codigoTipoDocumento).then(res => {
                this.setState({ tipoDocumento : res })
            });
        }else{
            get_Tipo_Documento_Lista().then(res => {
                this.setState({ tipoDocumento : res})
            });
        }
    }

    cambiarAlerta(message){
        this.setState({ message:message });
        this.mostrarAlerta(3000);  
    }

    guardarVerificacion = () =>{
        if(this.state.nombreTipoDocumento){
            if(this.state.cantidadCaracteres){
                this.guardarTipoDocumento();
            }else{
                alert("Ingrese Caracteres");
            }
        }else{
            alert("Ingrese el Nombre");
        }
    }

    guardarTipoDocumento = () =>{
        const { nombreTipoDocumento, cantidadCaracteres, codigoTipoDocumento } = this.state;
        const tipoDocumento = {nombreTipoDocumento, cantidadCaracteres, codigoTipoDocumento };
        const message = {
            text:"",
            alert:""  
        };        
        switch (this.state.tipoAccion) {
            case "post":
                post_Tipo_Documento(tipoDocumento).then((result)=>{
                    message.alert = "success";
                    message.text = "Se guardo correctamente";
                    this.toogleModal();
                });
                break;
            case "put":
                put_Tipo_Documento(tipoDocumento).then((result)=>{
                    message.alert = "success";
                    message.text = "Se modifico correctamente";
                    this.toogleModal();
                });
                break;
            default:
                break;
        }
        this.cambiarAlerta(message);
    }

    eliminarTipoDocumento(codigoTipoDocumento){
        const message = {
            text:"Algo salio mal vuelve a intertarlo",
            alert:"danger"  
        };
        if(codigoTipoDocumento !== "" && codigoTipoDocumento !== null){
            del_Tipo_Documento(codigoTipoDocumento).then(res => {
                message.alert = "success";
                message.text = "Se elimino correctamente";
                this.listarTipoDocumento("");
            });
        }
        this.cambiarAlerta(message);
    }

    setValues = (e , field) => {       
        this.setState({ [field]: e.target.value });
    }

    verificarAccion(codigoTipoDocumento,tituloModal){
        if(codigoTipoDocumento !== null && codigoTipoDocumento !== ""){
            get_Tipo_Documento(codigoTipoDocumento).then(res =>{
                if(res[0]){    
                    this.setState({
                        nombreTipoDocumento:res[0].nombreTipoDocumento,
                        codigoTipoDocumento:res[0].codigoTipoDocumento,
                        cantidadCaracteres:res[0].cantidadCaracteres,
                        tipoAccion:"put",        
                        tituloModal: tituloModal
                    });
                }
            });
        }else{
            this.setState({ 
                nombreTipoDocumento:"",
                codigoTipoDocumento:"",
                cantidadCaracteres:0,
                tipoAccion:"post",
                tituloModal: tituloModal
            });
        }
        this.toogleModal();
    }
    toogleModal(){   
        this.setState({
            modalOpen : ! this.state.modalOpen,
        });
        if(!this.state.modalOpen){
            this.listarTipoDocumento("");
        }    
    }

    exportarToExcel(){
        
    }

    componentDidMount(){
        this.listarTipoDocumento("");
    }
    render() {
        const columns = [
            {
                Header: "ID",
                accessor: "id",
                style:{
                    textAlign: "center"
                },
                show:false
            },
            {
                Header: "CODIGO",
                accessor: "codigoTipoDocumento",
                style:{
                    textAlign: "center"
                }
            },
            {
                Header: "DOCUMENTO",
                accessor: "nombreTipoDocumento",
                style:{
                    textAlign: "center"
                }
            },
            {
                Header: "N¬∞ CARACTERES",
                accessor: "cantidadCaracteres",
                sortable: false,
                filterable: false,
                style:{
                    textAlign: "center"
                }
            },
            {
                Header:"ACCIONES",
                Cell: props => {
                    return(
                        <div className="container">
                            <div className="row justify-content-center">
                                <div className="col-md-5">
                                    <button className="btn btn-danger"
                                        onClick={() =>{
                                            this.mostrarModal(props.original.codigoTipoDocumento);
                                        }}
                                    >Borrar</button>
                                </div>
                                <div className="col-md-5 ml-auto">
                                    <button className="btn btn-warning"
                                    onClick={
                                        e => this.verificarAccion(props.original.codigoTipoDocumento,"Modificar")                                           
                                    }>Editar</button>
                                </div>
                            </div>

                        </div>
                    )
                },                
                sortable: false,
                filterable: false
            }
        ]
        let buttonCard = {
            cursor: 'pointer'
        };
        return (
            <Menu>
            <div className="container p-4">
                {/*<MaterialTable></MaterialTable>*/}
                <div className="row">
                    <div className="col-md-11 mx-auto">
                        <div className="card ">
                            <div className="card-header">
                                <h3>Tipo de Documentos en Existencia</h3>
                            </div>
                            <div className="m-2">
                                { this.state.message.text !==''?( <Alert color={this.state.message.alert}>
                                { this.state.message.text} </Alert> ): '' }
                            </div>                            
                            <div className="row m-2">
                                <div className="col-sm" style={buttonCard}>
                                    <div className="card text-white bg-info"
                                        onClick={()=>{
                                            this.verificarAccion("","Registrar")
                                            //this.ver();
                                    }}>
                                    <div className="card-header">Registrar</div>
                                        <button className="btn card-body text-white text-justify">Documento de indentidad.</button>
                                    </div>
                                </div>
                                
                                <div className="col-sm" style={buttonCard}>
                                    <div className="card bg-success text-white">
                                        <div className="card-header">Exportar</div>
                                        <ReactHTMLTableExcel
                                            id="test-table-xls-button"
                                            className="btn card-body text-white text-justify"
                                            table="table-to-xls"
                                            filename="tipoDocumentoExcel"
                                            sheet="hoja 1"
                                            buttonText="A Documento Excel."
                                        />
                                    </div>
                                </div>
                            </div>
                            <div className="card-body">
                                <ReactTable
                                    columns = {columns}
                                    noDataText = {"Cargando Datos..."}
                                    data = {this.state.tipoDocumento}
                                    filterable
                                    filtered = {this.state.filtered}
                                    onFilteredChange = {(filtered, column, value) => {
                                        this.onFilteredChangeCustom(value, column.id || column.accessor);
                                      }}
                                      defaultFilterMethod={(filter, row, column) => {
                                        const id = filter.pivotId || filter.id;
                                        if (typeof filter.value === "object") {
                                          return row[id] !== undefined
                                            ? filter.value.indexOf(row[id]) > -1
                                            : true;
                                        } else {
                                          return row[id] !== undefined
                                            ? String(row[id]).indexOf(filter.value) > -1
                                            : true;
                                        }
                                      }}
                                    defaultPageSize={5}
                                    showPaginationTop={true}
                                    showPaginationBottom={false}
                                >
                                {(state, filtredData, instance)=>{
                                    this.reactTable = state.pageRows.map(
                                        tipoDocumento => {return tipoDocumento._original});
                                    return(
                                        <div>
                                            {filtredData()}
                                            <ExportToExcel tpDocumento={this.reactTable}/>
                                        </div>
                                    )
                                }}
                                </ReactTable>
                            </div>
                            <Modal 
                                isOpen={this.state.modalOpen}
                                centered={true}
                                size={"xm"}
                            >
                                <ModalHeader toggle={this.toogleModal.bind(this)}>{this.state.tituloModal}</ModalHeader>    

                                <ModalBody>
                                    <div className="card ">
                                        <div className="card-header text-center">
                                            <h3>Documento de Indentidad</h3>
                                        </div>
                                        <div className="card-body ">
                                            {/*<form>*/}
                                                <div className="form-group">
                                                    <label for="nombreTipoDocumento"> Nombre Documento: </label>
                                                    <input className="form-control" id= "nombreTipoDocumento" type="text" placeholder="Ejemplo: DNI"
                                                        value = {this.state.nombreTipoDocumento} 
                                                        onChange = { e => this.setValues(e,'nombreTipoDocumento') }
                                                    />
                                                </div>
                                                <div className="form-group">
                                                    <label for="cantidadCaracteres"> Caracteres: </label>
                                                    <input className="form-control" id= "cantidadCaracteres" type="text" placeholder="Ejemplo: DNI"
                                                        value={this.state.cantidadCaracteres}
                                                        onChange = { e => this.setValues(e,'cantidadCaracteres') }
                                                    />
                                                </div>
                                                <div className="text-center">
                                                    <button className="btn btn-primary"
                                                        onClick={this.guardarVerificacion}
                                                    > REGISTRAR </button>
                                                </div>
                                            {/*</form>*/}
                                        </div>
                                    </div>
                                </ModalBody>
                            </Modal>

                            <Modal 
                                    isOpen={this.state.modalConfirmarOpen}
                                    centered={true}
                                    size={"xm"}
                            >
                                <ModalHeader toggle={this.toogleModalConfirmar.bind(this)}>Alerta</ModalHeader>    
                                <ModalBody>
                                    <div className="container">
                                        <div className="row justify-content-center text-center">
                                            <div className="col-md-12 mb-4">
                                                <span>Se eliminar√° el Registro, ¬øDesea Continuar? </span>
                                            </div>
                                            <div className="col-md-3">
                                                <button className="btn btn-danger"
                                                    onClick={this.borrarConfirmado}
                                                >Eliminar</button>
                                            </div>
                                            <div className="col-md-3">
                                                <button className="btn btn-info"
                                                    onClick = {this.toogleModalConfirmar.bind(this)}
                                                >Cancelar</button>
                                            </div>
                                        </div>
                                    </div>
                                </ModalBody>
                            </Modal>
                        </div>
                    </div>
                </div>
            </div>
            </Menu>
        )
    }
}
