import React, { Component } from 'react';

class ExportExcel extends Component{
    render(){
        return(
            <div className="container row">
                <div>
                    <table hidden={true} id="table-to-xls">
                        <thead>
                            <tr>
                                <th>TPD ID</th>
                                <th>Codigo</th>
                                <th>Producto</th>
                                <th>Tipo</th>
                                <th>Presentaci√≥n</th>
                                <th>Ubicaci√≥n</th> 
                                <th>Existencias</th>
                                <th>Minimo</th> 
                                <th>estado</th> 
                                <th>Caducidad</th> 
                            </tr>                                           
                        </thead>
                        <tbody>
                        {
                            this.props.productos.map(pro =>{
                                return(
                                    <tr key={pro.id}>
                                        <td>{pro.id}</td>
                                        <td>{pro.codigoProducto}</td>
                                        <td>{pro.descripcionProducto}</td>
                                        <td>{pro.nombreTipoProducto}</td>  
                                        <td>{pro.descripcionPresentacion}</td>    
                                        <td>{pro.nombreLocal}</td>          
                                        <td>{pro.stockExistente}</td>    
                                        <td>{pro.stockMinimo}</td>    
                                        <td>{pro.estadoProducto}</td>    
                                        <td>{pro.fechaVencimiento}</td>   
                                    </tr>
                                );    
                            })
                        }
                        </tbody>
                    </table>
                </div>
            </div>
        );
    }
}

export default ExportExcel