import React, { Component } from 'react';
import ReactTable from 'react-table';
import "react-table/react-table.css";

import { Modal , ModalHeader, ModalBody } from 'reactstrap';

import { get_Traslado_Pendiente_Local , get_Traslado_Local , put_Traslado_Confirmar ,
    get_Traslado_Local_Envio , get_Traslado_Local_Ingreso } from '../../Data/TrasladoData';

import { get_Traslado_Detalle_Codigo_Traslado_Vista } from '../../Data/TrasladoDetalleData';
import { get_Producto_Traslado, post_Producto_Ingreso } from '../../Data/productoData';

import Menu from '../../Pages/menu';

import './Ingreso.css';

export class Ingresos extends Component {
    
    constructor(props){
        super(props);
        this.state = {
            trasladosPendientes : [],

            trasladosDetallesPendientes : [],

            productoIngreso : [],
        
            cantidadDetalles : 0,
        
            observacionDestino : "",
        
            modalDetalleOpen : false,
        
            codigoLocal : localStorage.getItem("codigoLocal"),
        
            codigoTraslado : "",

            tipoVista : props.tipoVista,

            tipoAccion : false,

            acciones : props.acciones,

            titulo: props.titulo
        };
    }

    confirmarIngreso = () => {
        const trasladoPut = {
            codigoTraslado : this.state.codigoTraslado,
            codigoUsuarioConfirmacion : localStorage.getItem("codigoUsuario"),
            observacionDestino : this.state.observacionDestino
        }
        this.ingresarProducto();
        put_Traslado_Confirmar(trasladoPut).then( res => {
            this.llenarListas();
            this.toogleModalDetalle();
        });
    }

    setIngreso = (e) => {
        this.setState({
            [e.target.id] : [e.target.value]
        });
    }

    ingresarProducto = () =>{
        const { productoIngreso } = this.state;

        productoIngreso.forEach(producto => {
            producto["codigoLocal"] = localStorage.getItem("codigoLocal");
            producto["stockExistente"] = producto.cantidadProducto;
            producto["fechaVencimiento"] = producto.fechaVencimiento.substring(0,10);
            post_Producto_Ingreso(producto).then(_=> {});
        });
    }

    listarTrasladosLocalEnvios = () => {
        get_Traslado_Local_Envio(this.state.codigoLocal).then( res => {
            this.setState({
                trasladosPendientes : res
            });
        });
    }

    listarTrasladosLocalIngresos = () => {
        get_Traslado_Local_Ingreso(this.state.codigoLocal).then( res => {
            this.setState({
                trasladosPendientes : res
            });
        });
    }

    listarTrasladosLocalPendientes = () =>{
        get_Traslado_Pendiente_Local(this.state.codigoLocal).then( res => {
            this.setState({
                trasladosPendientes : res
            });
        });
    }

    listarTraslados(){
        get_Traslado_Local(this.state.codigoLocal).then( res => {
            this.setState({
                trasladosPendientes : res
            });
        });
    }

    listarTrasladosDetallesPendientes (codigoTraslado,tipoAccion) {
        if(tipoAccion === "CONFIRMAR"){
            this.setState({ tipoAccion : true });
        }else{
            this.setState({ tipoAccion : false });
        }

        this.listarProductosIngresantes(codigoTraslado);
        get_Traslado_Detalle_Codigo_Traslado_Vista(codigoTraslado).then( res => {
            this.setState({
                codigoTraslado : codigoTraslado,
                cantidadDetalles : res.length,
                trasladosDetallesPendientes : res
            }, () => {
                this.setState({
                    modalDetalleOpen : ! this.state.modalDetalleOpen
                });
            });
        });
    }

    listarProductosIngresantes = (codigoTraslado) => {
        get_Producto_Traslado(codigoTraslado).then(res =>{
            this.setState({ productoIngreso : res });
        });
    }

    llenarListas(){
        if(this.state.tipoVista === "enviados"){
            this.listarTrasladosLocalEnvios();
        }
        if(this.state.tipoVista === "ingresados"){
            this.listarTrasladosLocalIngresos();
        }
        if(this.state.tipoVista === "pendientes"){
            this.listarTrasladosLocalPendientes();
        }
    }

    toogleModalDetalle = () => {
        this.setState({
            modalDetalleOpen : ! this.state.modalDetalleOpen
        });
    }

    componentDidMount(){
        this.llenarListas();
    }

    render() {
        const columnsDetalles = [
            {
                Header:"IMAGEN",
                Cell: props =>{
                    return(
                        <div className="">
                            <img src={props.original.urlMedia} alt={props.original.descripcionProducto} className="card-img-top media_producto"/>
                        </div>
                    )
                },
                width:100,
                minWidth:100,
                maxWidth:100
            },
            {
                Header: "PRODUCTO",
                accessor:"descripcionProducto",
                style:{
                    textAlign: "center"
                },
                Cell: props => {
                    return(
                        <div className="mt-2">
                            <div>{props.original.descripcionProducto}</div>
                            <div>{props.original.detalleProducto}</div>
                            <div>{props.original.nombreTipoProducto}</div>
                            <div>{props.original.descripcionPresentacion}</div>
                            <div>Precio: S/.{(props.original.valorPrecio).toFixed(2)}</div> 
                            <div>CANT.:{props.original.cantidadProducto}</div>
                        </div>
                    )
                },
            }
        ];
        const columnsTraslados = [
            {
                Header: "PENDIENTES",
                style:{
                    textAlign: "center"
                },
                Cell: props =>{
                    return(
                        <div className="container">
                            <div className="row">
                                <div className="col-md-12">
                                    {props.original.detalles} Paquete(s)
                                </div>
                                <div className="col-md-12">
                                    De {props.original.nombreLocalOrigen}
                                </div>
                                <div className="col-md-12">
                                    Registrado Por:
                                </div>
                                <div className="col-md-12">
                                    {props.original.nombreTrabajador}
                                </div>
                                <div className="col-md-12">
                                    A {props.original.nombreLocalDestino}
                                </div>
                            </div>
                        </div>
                    )
                },
            },
            {
                Header : "OBSE. ORIGEN",
                accessor : "observacionOrigen",
                style : {
                    textAlign : "center"
                }
            },
            {
                Header : "OBSE. DESTINO",
                accessor : "observacionDestino",
                style : {
                    textAlign : "center"
                }
            },
            {
                Header: "ESTADO",
                style:{
                    textAlign: "center"
                },
                Cell: props =>{
                    var classCard = "";
                    var nombreCard = "";
                    if(props.original.confirmacion){
                        classCard = "card bg-success";
                        nombreCard = "CONFIRMADO";
                    }else{
                        classCard = "card bg-danger";
                        nombreCard = "SIN CONFIRMAR";
                    }
                    return(
                        <div className="container text-center">
                            <div className={classCard}>
                                <div className="card-header">
                                    <span>{nombreCard}</span>
                                </div>
                                <div className="card-body">
                                    <div>
                                        <label for="fechaRegistro">Enviado el :  </label>
                                    </div>
                                    <div>
                                        <span>
                                            {
                                                new Intl.DateTimeFormat( 'default',{
                                                    year: 'numeric',
                                                    month:'numeric',
                                                    day:'numeric',
                                                    hour:'numeric',
                                                    minute:'numeric'
                                                }).format(Date.parse(props.original.fechaRegistro))
                                            }
                                        </span>
                                    </div>
                                </div>
                            </div>
                        </div>
                    )
                },
            },
            {
                Header: "ACCIONES",
                style:{
                    textAlign: "center"
                },
                show:this.state.acciones,

                Cell: props =>{
                    var mostrarConfirmar = true;
                    if(this.state.tipoVista === "enviados"){
                        mostrarConfirmar = false;
                    }
                    if(props.original.confirmacion){
                        mostrarConfirmar = false;
                    }
                    return(
                        <div className="container">
                            <div className="row align-items-center mt-3">
                                <div hidden={!mostrarConfirmar} className="form-group col-md-12">
                                    <button className="btn btn-success"
                                        onClick={ () => {
                                            this.listarTrasladosDetallesPendientes(props.original.codigoTraslado,"CONFIRMAR");
                                        }}
                                    >
                                        CONFIRMAR
                                    </button>
                                </div>
                                <div className="form-group col-md-12">
                                    <button className="btn btn-info"
                                        onClick={ () => { 
                                            this.listarTrasladosDetallesPendientes(props.original.codigoTraslado,"DETALLE");
                                        }}
                                    >
                                        DETALLES
                                    </button>
                                </div>
                            </div>

                        </div>
                    )
                },
            },
        ]

        return (
            <Menu>
            <div className="container">
                <div className="card">
                    <div className="card-header">
                        <h3>{this.state.titulo}</h3>
                    </div>
                    <div className="card-body">
                        <ReactTable
                            columns = {columnsTraslados}
                            noDataText = {"Sin Datos..."}
                            data = {this.state.trasladosPendientes}
                            filterable={false}
                            defaultPageSize={this.state.trasladosPendientes.length || 3}
                            showPaginationTop={false}
                            showPaginationBottom={false}
                        >
                        </ReactTable>
                    </div>
                </div>
                <div>
                    <Modal 
                        isOpen={this.state.modalDetalleOpen}
                        centered={true}
                    >
                        <ModalHeader toggle={this.toogleModalDetalle.bind(this)}><h3>Productos Enviados</h3></ModalHeader>    
                        <ModalBody>
                            <div className="container">
                                <div className="row d-flex justify-content-center">
                                    <div className="col-md-12">
                                        <ReactTable
                                        columns = {columnsDetalles}
                                        noDataText = {"Cargando Datos..."}
                                        data = {this.state.trasladosDetallesPendientes}
                                        filterable={false}
                                        defaultPageSize={this.state.cantidadDetalles}
                                        showPaginationTop={false}
                                        showPaginationBottom={false}
                                        >
                                        </ReactTable>
                                    </div>
                                    <div hidden={!this.state.tipoAccion} className="col-md-12 mt-2">
                                        <label for="observacionDestino">Observacion: </label>
                                        <input id= "observacionDestino" type="text" className="form-control"
                                            value = { this.state.observacionDestino }                                            
                                            onChange = { (e) => { this.setIngreso(e) } }
                                        ></input>
                                    </div>
                                    <div hidden={!this.state.tipoAccion} className="col-md-6 mt-3">
                                        <button className="btn btn-success form-control"
                                            onClick={this.confirmarIngreso}
                                        >
                                            CONFIRMAR
                                        </button>
                                    </div>
                                    <div hidden={this.state.tipoAccion} className="col-md-6 mt-3">
                                        <button className="btn btn-info form-control"
                                            onClick={this.toogleModalDetalle}
                                        >
                                            Cerrar
                                        </button>
                                    </div>
                                </div>
                            </div>
                        </ModalBody>
                    </Modal>
                </div>
            </div>
            </Menu>
        )
    }
}

export default Ingresos
