import React, { Component } from 'react';
import { Redirect } from 'react-router-dom';
import { put_Sesion_Cerrar } from '../Data/inicioSesionData';

export class logout extends Component {
    constructor(){
        super();
        this.state = {
            redirect : false
        }
        this.salir = this.salir.bind(this)
    }

    salir() {
        const codigoUsuario = localStorage.getItem("codigoUsuario");
        put_Sesion_Cerrar( { codigoUsuario }).then(_=>{
            localStorage.removeItem('idUsuario');
            localStorage.removeItem('tokenUsuario');
            localStorage.removeItem('codigoUsuario');
            localStorage.removeItem('codigoLocal');
            this.setState({redirect:true});
        });
    }

    render() {
        const codigoUsuario  = localStorage.getItem('codigoUsuario');
        if(codigoUsuario !== null && codigoUsuario !== "" && this.state.redirect === false){
            return (
                <div className="container p-4">
                    <div className="row">
                        <div className="col-md-4 mx-auto">
                            <div className="card text-center">
                                <div className="card-header">
                                    <h3>LOGOUT</h3>
                                </div>
                                <div className="card-body">
                                    <img src="/img/Logo-World-Connect.png" alt="Logo WCS" className="card-img-top mx-auto m-2 rounded-circle w-50"/>
                                    <div>
                                        <div className="form-group">
                                            <button className="btn btn-success btn-block" onClick={this.salir}> 
                                                SALIR
                                            </button>    
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            )
        }else{
            return <Redirect to = '/app/login'/>;
        }
    }
}

export default logout
