// Presentacion API FROM SERVER NODE EXPRESS

import data from '../../db_data';
const BaseUrl = data + '/api/presentaciones/';

// ----------------------------------------------------------------------------------------------------------------
// LISTAR PresentacionS
export function get_Presentacion_Lista(){ 
    const urlFetch = BaseUrl ;
    return new Promise((resolve,reject)=>{
        fetch(urlFetch)
        .then((response) => response.json())
        .then((responseJson) => {
            resolve(responseJson);
        })
        .catch((error)=>{
            reject(error);
        });
    });
}

// MOSTRAR Presentacion CODIGO
export function get_Presentacion(codigoPresentacion){ 
    const urlFetch = BaseUrl + codigoPresentacion;
    return new Promise((resolve,reject)=>{
        fetch(urlFetch)
        .then((response) => response.json())
        .then((responseJson) => {
            resolve(responseJson);
        })
        .catch((error)=>{
            reject(error);
        });
    });
}


//ELIMINAR Presentacion CODIGO
export function del_Presentacion(codigoPresentacion){ 
    const urlFetch = BaseUrl + codigoPresentacion;
    return new Promise((resolve,reject)=>{
        fetch(urlFetch,{
            method:'DELETE',
            headers: new Headers({
                'Content-type':'application/json'
            })
        })
        .then((response) => response.json())
        .then((responseJson) => {
            resolve(responseJson);
        })
        .catch((error)=>{
            reject(error);
        });
    });
}

//GUARDAR Presentacion
export function post_Presentacion(Presentacion){ 
    const urlFetch = BaseUrl;
    return new Promise((resolve,reject)=>{
        fetch(urlFetch,{
            method:'POST',
            body: JSON.stringify(Presentacion),
            headers: new Headers({
                'Content-type':'application/json'
            })
        })
        .then((response) => response.json())
        .then((responseJson) => {
            resolve(responseJson);
        })
        .catch((error)=>{
            reject(error);
        });
    });
}

//MODIFICAR Presentacion
export function put_Presentacion(Presentacion){ 
    const urlFetch = BaseUrl;
    return new Promise((resolve,reject)=>{
        fetch(urlFetch,{
            method:'PUT',
            body: JSON.stringify(Presentacion),
            headers: new Headers({
                'Content-type':'application/json'
            })
        })
        .then((response) => response.json())
        .then((responseJson) => {
            resolve(responseJson);
        })
        .catch((error)=>{
            reject(error);
        });
    });
}