import React, { Component} from 'react';
import ReactTable from 'react-table';
import "react-table/react-table.css";

import ReactHTMLTableExcel from 'react-html-table-to-excel';

import { Modal , ModalHeader, ModalBody, Alert } from 'reactstrap';

import data from '../../../db_data';
import ExportToExcel from "../../Export/excel/Usuario";

import { post_Usuario, put_Usuario, del_Usuario , get_Usuario , get_Usuario_Lista , get_Usuario_Trabajador } from '../../Data/UsuarioData';
import { get_Trabajador , del_Trabajador , post_Trabajador , put_Trabajador  } from '../../Data/TrabajadorData';


import UsuarioConexion from './UsuarioConexiones';
import { get_Tipo_Media_Nombre, get_Tipo_Documento_Lista } from '../../Data/tiposData';
import { post_Media, post_Media_File } from '../../Data/MediaData';


const usuarioIncial = {
    codigoUsuario : "",
    nombreUsuario : "",
    contraUsuario : "",
    codigoTrabajador : "",
    codigoMedia : "MED-00000000001",
    estadoUsuario : 1,
    conexionUsuario : 0,
}

const trabajadorInical = {
    codigoTrabajador : "",
    nombreTrabajador : "",
    apePaTrabajador : "",
    apeMaTrabajador : "",
    codigoTipoDocumento : "TP-DOC-001",
    nombreTipoDocumento :"",
    numDocuTrabajador : "",
    numTeleTrabajador : "",
    correoTrabajador : "",
    direccionTrabajador : "",
    especialidadTrabajador : "",
    estadoTrabajador : true,
    fechaNacimiento : "",
    codigoMedia : "MED-00000000001",
    urlMedia:"",
}



export default class UsuarioLista extends Component {

    BaseUrl = data + '/api/usuarios';

    constructor (props){
        super(props);
        this.state = {

            tiposDocumentos : [],

            conexionesUsuario:[],
            codigoUsuarioConexion:"",
            
            usuarios : [],
            usuario : usuarioIncial,

            trabajadores : [],
            trabajador : trabajadorInical,

            codigoMediaTrabajador:"",
            codigoTrabajadorRegistrado:"",


            media:{
                codigoMedia:"",
                nombreMedia:"",
                altMedia:"",
                urlMedia:"", 
                codigoTipoMedia:"",
                codigoUsuario:"",
            },

            codigoTipoDocumento:"",

            file:null,
            nombreFile:"",

            filtered:[],
            UsuarioExcel:[],
            message : {
                text:'',
                alert: ''
            },
            messageActivo:false,

            tipoAccion:"post",
            modalOpen: false,
            modalUsuarioOpen:false,
            modalConexionesOpen:false,

            mostrarFormTrabajador:false,

            tituloModal:""
        }
    }

    mostrarFormTrabajador = () => {
        this.setState({
            mostrarFormTrabajador : ! this.state.mostrarFormTrabajador
        });
    }

    setFileMedia = e => {
        let file = e.target.files[0];    
        const nombreBusqueda = file.type.replace("/","-");
        const { media } = this.state;
        const codigoUsuario  = localStorage.getItem('codigoUsuario');
        get_Tipo_Media_Nombre(nombreBusqueda).then((result)=>{
            media["codigoTipoMedia"] = result[0].codigoTipoMedia;
            media["nombreMedia"] = file.name;
            media["altMedia"] = file.name;
            media["urlMedia"] = "/img/" + file.name;
            media["codigoUsuario"] = codigoUsuario;
            this.setState({
                file : file,
                media: media,
                nombreFile : file.name
            });
        });
        
    }
    guardarMedia = () =>{
        const { file , media } = this.state;
        if(file !== null ){
            post_Media(media).then((result)=>{
                this.setState({ codigoMediaTrabajador  :result[0][0].codigoMedia });
                post_Media_File(file).then((result)=>{
                });
                this.guardarUsuario();
            });              
        }
    }

    onFilteredChangeCustom = (value, accessor) => {
        let filtered = this.state.filtered;
        let insertNewFilter = 1;
    
        if (filtered.length) {
          filtered.forEach((filter, i) => {
            if (filter["id"] === accessor) {
              if (value === "" || !value.length) filtered.splice(i, 1);
              else filter["value"] = value;
              insertNewFilter = 0;
            }
          });
        }
    
        if (insertNewFilter) {
          filtered.push({ id: accessor, value: value });
        }
    
        this.setState({ filtered: filtered });
    };

    listarTiposDocumentos = () => {
        get_Tipo_Documento_Lista()
        .then( res => {
            this.setState({
                tiposDocumentos : res
            });
        });
    }

    listarTrabajador(){
        //get_Trabajador_Lista()
        //get_Usuario_Trabajador_Conexion("")
        get_Usuario_Trabajador("")
        .then( res => {
            this.setState({ trabajadores : res});
        });
    }
    

    listarUsuario(codigoUsuario){
        if(codigoUsuario !== "" && codigoUsuario !== null){
            get_Usuario(codigoUsuario).then(res => {
                this.setState({ usuarios : res })
            });
        }else{
            get_Usuario_Lista().then(res => {
                this.setState({ usuarios : res})
            });
        }
    }

    llenarListas(){
        this.listarUsuario("");
        this.listarTrabajador();
    }

    cambiarAlerta(message){
        this.setState({ message:message });
        this.mostrarAlerta(3000);  
    }

    guardarUsuario2 = () => {
        this.guardarMedia();
    }

    guardarUsuario = () =>{
        const { usuario , trabajador} = this.state;
        const message = {
            text:"Algo salio mal vuelve a intertarlo",
            alert:"danger"  
        };
        trabajador["codigoMedia"] = this.state.codigoMediaTrabajador;
        trabajador["estadoTrabajador"] = 1;
        switch (this.state.tipoAccion) {
            case "post":
                post_Trabajador(trabajador).then((res) => {
                    this.setState({ 
                        codigoTrabajadorRegistrado:res[0][0].codigoTrabajador
                    },() => { 
                        usuario["codigoTrabajador"] = this.state.codigoTrabajadorRegistrado;
                        usuario["codigoMedia"] = "MED-00000000001";
                        usuario["estadoUsuario"] = 1;
                        usuario["conexionUsuario"] = 0;
                        post_Usuario(usuario).then(res => {
                            message.alert = "success";
                            message.text = "Se modifico correctamente";
                            this.toogleModalUsuario();
                        });
                     });
                });
                break;
            case "put":
                put_Usuario(usuario).then((result) => {
                    put_Trabajador(trabajador).then((res) => {
                        message.alert = "success";
                        message.text = "Se modifico correctamente";
                        this.toogleModalUsuario();
                    });
                });
                break;
            default:
                break;
        }
        this.cambiarAlerta(message);
    }

    eliminarUsuario(usuarioTrabajador){
        const message = {
            text:"",
            alert:""  
        };
        const { codigoUsuario , codigoTrabajador } = usuarioTrabajador;
        if(codigoUsuario !== "" && codigoUsuario !== null){
            del_Usuario(codigoUsuario).then(res => {
                del_Trabajador(codigoTrabajador).then(res => {
                    message.alert = "danger";
                    message.text = "Se elimino correctamente";
                    this.llenarListas();  
                });       
            });
        }
        this.cambiarAlerta(message);
    }

    setUsuario = (e) => {
        const { usuario } = this.state;
        usuario[e.target.id] = e.target.value;
        this.setState({ usuario });
    }

    setTrabajador = (e) => {
        const { trabajador } = this.state;
        trabajador[e.target.id] = e.target.value;
        this.setState({ trabajador }); 
    }

    verificarAccion(usuario,tituloModal){
        const { codigoUsuario , codigoTrabajador } = usuario;
        if(codigoUsuario !== "" && codigoTrabajador !== "" && codigoUsuario !== null && codigoTrabajador !== null){
            get_Usuario(codigoUsuario).then( resUsu => {
                get_Trabajador(codigoTrabajador).then( resTra => {
                    resTra[0].fechaNacimiento = resTra[0].fechaNacimiento.substring(0,10)
                    this.setState({
                        usuario: resUsu[0],
                        trabajador : resTra[0],
                        tipoAccion:"put",        
                        tituloModal: tituloModal
                    });
                });
            });    
        }
        else{
            this.setState({ 
                usuario : usuarioIncial,
                trabajador : trabajadorInical,
                tipoAccion : "post",
                tituloModal : tituloModal
            });
        }
        this.toogleModalUsuario();
    }

    toogleModalUsuario(){   
        this.setState({
            modalUsuarioOpen : ! this.state.modalUsuarioOpen,
        });
        if(!this.state.modalUsuarioOpen){
            this.llenarListas();
        }        
    }

    toogleModalConexiones(){  
        this.setState({
            modalConexionesOpen : ! this.state.modalConexionesOpen,
        });      
    }

    verificarAccionConexiones(codigoUsuario){
        if(codigoUsuario !== "" && codigoUsuario !== null){
            this.setState({ codigoUsuarioConexion : codigoUsuario });
        }
        this.toogleModalConexiones();
    }


    mostrarAlerta = (tiempo) => {
        setTimeout(()=>{
            this.setState({ message : { text : '' , alert : ''} });            
        },tiempo);
    }

    componentDidMount(){
        this.llenarListas();
        this.listarTiposDocumentos();
    }
    render() {
        const columns = [
            {
                Header: "ID_USUARIO",
                style:{
                    textAlign: "center"
                },
                Cell: props => { return(
                    <span>{props.original.nombreUsuario}</span>
                )}
            },
            {
                Header: "NOMBRES",
                //accessor: "nombreTrabajador",
                style:{
                    textAlign: "center"
                },
                Cell: props => { return(
                    <span>{props.original.nombreTrabajador}</span>
                )}
            },
            {
                Header: "APELLIDOS",
                style:{
                    textAlign: "center"
                },
                Cell : props => { return(
                    <div>
                        <span>{props.original.apePaTrabajador +" " + props.original.apeMaTrabajador}</span>
                    </div>
                )}
            },
            {
                Header: "CONTACTO",
                style:{
                    textAlign: "center"
                },
                Cell: props => { return(
                    <div>
                        <span>{ props.original.correoTrabajador + " " + props.original.numTeleTrabajador}</span>
                    </div>
                )}
            },
            {
                Header: "CONEXI√ìN",
                style:{
                    textAlign: "center"
                },
                Cell: props => {
                    
                    if(props.original.conexionUsuario){
                        return(
                            <div>
                                <span>Usuario Conectado</span>
                            </div>
                        )
                    }else{
                        return(
                            <div>
                                <span>Usuario Desconectado</span>
                            </div>
                        )
                    }
                }
            },
            {
                Header:"ACCIONES",
                Cell: props => {
                    return(
                        <div className="container">
                            <div className="row justify-content-center">
                                <div className="col-md-3">
                                    <button className="btn btn-danger"
                                        onClick={() =>{
                                            this.eliminarUsuario(props.original);
                                        }}
                                    >Borrar</button>
                                </div>
                                <div className="col-md-3 ml-auto">
                                    <button className="btn btn-warning"
                                    onClick={
                                        e => this.verificarAccion(props.original,"Modificar")                                           
                                    }>Editar</button>
                                </div>
                                <div className="col-md-3 ml-auto">
                                    <button className="btn btn-info"
                                    onClick={
                                        e => this.verificarAccionConexiones(props.original.codigoUsuario)}
                                    >Conexiones</button>
                                </div>
                            </div>

                        </div>
                    )
                },                
                sortable: false,
                filterable: false
            }
        ]
        let buttonCard = {
            cursor: 'pointer'
        };
        return (
            <div className="container p-4">
                {/*<MaterialTable></MaterialTable>*/}
                <div className="row">
                    <div className="col-md-11 mx-auto">
                        <div className="card ">
                            <div className="card-header">
                                <h3>Usuarios Registrados</h3>
                            </div>
                            <div className="m-2 text-center">
                                { this.state.message.text !==''?( <Alert color={this.state.message.alert}>{ this.state.message.text}</Alert> ) : '' }
                            </div>                            
                            <div className="row m-2">
                                <div className="col-sm" style={buttonCard}>
                                    <div className="card text-white bg-info"
                                        onClick={()=>{
                                            this.verificarAccion("","Registrar")
                                            //this.ver();
                                    }}>
                                    <div className="card-header">Registrar</div>
                                        <button className="btn card-body text-white text-justify">Usuario.</button>
                                    </div>
                                </div>
                                
                                <div className="col-sm" style={buttonCard}>
                                    <div className="card bg-success text-white">
                                        <div className="card-header">Exportar</div>
                                        <ReactHTMLTableExcel
                                            id="test-table-xls-button"
                                            className="btn card-body text-white text-justify"
                                            table="table-to-xls"
                                            filename="UsuarioExcel"
                                            sheet="hoja 1"
                                            buttonText="A Documento Excel."
                                        />
                                    </div>
                                </div>
                            </div>
                            <div className="card-body">
                                <ReactTable
                                    columns = {columns}
                                    noDataText = {"Cargando Datos..."}
                                    data = {this.state.trabajadores}
                                    filterable
                                    filtered = {this.state.filtered}
                                    onFilteredChange = {(filtered, column, value) => {
                                        this.onFilteredChangeCustom(value, column.id || column.accessor);
                                      }}
                                      defaultFilterMethod={(filter, row, column) => {
                                        const id = filter.pivotId || filter.id;
                                        if (typeof filter.value === "object") {
                                          return row[id] !== undefined
                                            ? filter.value.indexOf(row[id]) > -1
                                            : true;
                                        } else {
                                          return row[id] !== undefined
                                            ? String(row[id]).indexOf(filter.value) > -1
                                            : true;
                                        }
                                      }}
                                    defaultPageSize={5}
                                    showPaginationTop={true}
                                    showPaginationBottom={false}
                                >
                                {(state, filtredData, instance)=>{
                                    this.reactTable = state.pageRows.map(
                                        Usuario => {return Usuario._original});
                                    return(
                                        <div>
                                            {filtredData()}
                                            <ExportToExcel usuarios = { this.reactTable }/>
                                        </div>
                                    )
                                }}
                                </ReactTable>
                            </div>
                            <Modal
                                isOpen={this.state.modalConexionesOpen}
                                centered={true}
                                size={"xm"}
                            >
                                <ModalHeader toggle={this.toogleModalConexiones.bind(this)}>Conexiones del Usuario</ModalHeader>
                                <ModalBody>
                                    
                                    <UsuarioConexion codigoUsuario={this.state.codigoUsuarioConexion}></UsuarioConexion>


                                </ModalBody>
                            </Modal>
                            <Modal 
                                isOpen={this.state.modalUsuarioOpen}
                                centered={true}
                                size={"xm"}
                            >
                                <ModalHeader toggle={this.toogleModalUsuario.bind(this)}>{this.state.tituloModal}</ModalHeader>    

                                <ModalBody>
                                    <div className="card ">
                                        <div className="card-header text-center">
                                            <h3>Usuario</h3>
                                        </div>
                                        <div className="card-body ">
                                            <div className="row">
                                                <div className="col-md-6 form-group">
                                                    <label for="nombreUsuario"> ID Usuario: </label>
                                                    <input className="form-control" id= "nombreUsuario" type="text" placeholder="Ejemplo: Jorge"
                                                        value = {this.state.usuario.nombreUsuario} 
                                                        onChange = { e => this.setUsuario(e) }
                                                    />
                                                </div>
                                                <div className="col-md-6 form-group">
                                                    <label for="contraUsuario"> Contrase√±a: </label>
                                                    <input className="form-control" id= "contraUsuario" type="password"
                                                        onChange = { e => this.setUsuario(e) }
                                                    />
                                                </div>
                                                <div className="col-md-12 form-group">
                                                    <div class="form-check">
                                                        <input class="form-check-input position-static" type="checkbox" 
                                                            onChange = {this.mostrarFormTrabajador}
                                                        />
                                                        <label class="form-check-label" for="defaultCheck1">
                                                            Registrar Datos de Trabajador
                                                        </label>
                                                    </div>
                                                </div>
                                                <div className="container" hidden = {!this.state.mostrarFormTrabajador}>
                                                    <div className="row">
                                                        <div className="col-md-6 form-group">
                                                            <label for="apePaTrabajador"> AP Paterno: </label>
                                                            <input className="form-control" id= "apePaTrabajador" type="text"
                                                                value={this.state.trabajador.apePaTrabajador}
                                                                onChange = { e => this.setTrabajador(e) }
                                                            />
                                                        </div>
                                                        <div className="col-md-6 form-group">
                                                            <label for="apeMaTrabajador"> AP Materno: </label>
                                                            <input className="form-control" id= "apeMaTrabajador" type="text"
                                                                value={this.state.trabajador.apeMaTrabajador}
                                                                onChange = { e => this.setTrabajador(e) }
                                                            />
                                                        </div>
                                                        <div className="col-md-6 form-group">
                                                            <label for="nombreTrabajador"> Nombres: </label>
                                                            <input className="form-control" id= "nombreTrabajador" type="text"
                                                                value={this.state.trabajador.nombreTrabajador}
                                                                onChange = { e => this.setTrabajador(e) }
                                                            />
                                                        </div>
                                                        <div className="col-md-6 form-group">
                                                            <label for="numTeleTrabajador"> Telefono: </label>
                                                            <input className="form-control" id= "numTeleTrabajador" type="text"
                                                                value={this.state.trabajador.numTeleTrabajador}
                                                                onChange = { e => this.setTrabajador(e) }
                                                            />
                                                        </div>
                                                        <div className="col-md-6 form-group">
                                                            <label for="codigoTipoDocumento"> Documento: </label>
                                                            <select className="form-control" id="codigoTipoDocumento"
                                                                onChange = { e => this.setTrabajador(e) }
                                                            >
                                                                <option value={this.state.trabajador.codigoTipoDocumento}>{this.state.trabajador.nombreTipoDocumento}</option>
                                                                {this.state.tiposDocumentos.map( tpDocu => { return (
                                                                    <option key={tpDocu.id} value={tpDocu.codigoTipoDocumento}>{tpDocu.nombreTipoDocumento}</option>
                                                                )})}
                                                            </select>
                                                        </div>
                                                        <div className="col-md-6 form-group">
                                                            <label for="numDocuTrabajador"> Numero: </label>
                                                            <input className="form-control" id= "numDocuTrabajador" type="text"
                                                                value={this.state.trabajador.numDocuTrabajador}
                                                                onChange = { e => this.setTrabajador(e) }
                                                            />
                                                        </div>
                                                        <div className="col-md-6 form-group">
                                                            <label for="correoTrabajador"> Correo: </label>
                                                            <input className="form-control" id= "correoTrabajador" type="text"
                                                                value={this.state.trabajador.correoTrabajador}
                                                                onChange = { e => this.setTrabajador(e) }
                                                            />
                                                        </div>
                                                        <div className="col-md-6 form-group">
                                                            <label for="fechaNacimiento"> Fecha Nacimiento: </label>
                                                            <input className="form-control" id= "fechaNacimiento" type="date"
                                                                data-date-format="YYYY MM DD"
                                                                value={this.state.trabajador.fechaNacimiento}
                                                                onChange = { e => this.setTrabajador(e) }
                                                            />
                                                        </div>
                                                        <div className="col-md-6 form-group">
                                                            <label for="direccionTrabajador"> Direccion: </label>
                                                            <input className="form-control" id= "direccionTrabajador" type="text"
                                                                value={this.state.trabajador.direccionTrabajador}
                                                                onChange = { e => this.setTrabajador(e) }
                                                            />
                                                        </div>
                                                        <div className="col-md-6 form-group">
                                                            <label for="especialidadTrabajador"> Especialidad: </label>
                                                            <input className="form-control" id= "especialidadTrabajador" type="text"
                                                               value={this.state.trabajador.especialidadTrabajador}
                                                               onChange = { e => this.setTrabajador(e) }
                                                            />
                                                        </div>
                                                    </div>
                                                </div>
                                                <div className="col-md-12 form-group">
                                                    <label for="mediaUsuario"> Imagen:</label>
                                                    <div class="custom-file">
                                                        <input type="file" class="custom-file-input" id="customFileLang" lang="es"
                                                            onChange = { e => this.setFileMedia(e) }
                                                        />
                                                        <label data-browse="Buscar" class="custom-file-label" for="customFileLang">{ this.state.trabajador.urlMedia}</label>
                                                    </div>
                                                </div>
                                            </div>
                                            <div className="text-center">
                                                <button className="btn btn-primary"
                                                    onClick={this.guardarUsuario2}
                                                > REGISTRAR </button>
                                            </div>
                                        </div>
                                    </div>
                                </ModalBody>
                            </Modal>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
