import React, { Component} from 'react';
import ReactTable from 'react-table';
import "react-table/react-table.css";

import ReactHTMLTableExcel from 'react-html-table-to-excel';

import { Modal , ModalHeader, ModalBody, Alert } from 'reactstrap';

import data from '../../../db_data';
import ExportToExcel from "../../Export/excel/Sucursal";

import { post_Sucursal, put_Sucursal, del_Sucursal , get_Sucursal , get_Sucursal_Lista} from '../../Data/SucursalData';
import Menu from '../../Pages/menu';

export default class SucursalLista extends Component {

    BaseUrl = data + '/api/sucursales';

    constructor (props){
        super(props);
        this.state = {
            sucursal:[],
            filtered:[],
            SucursalExcel:[],
            message : {
                text:'',
                alert: ''
            },
            messageActivo:false,

            codigoSucursal: "",
            nombreSucursal: "",

            tipoAccion:"post",
            modalOpen: false,
            tituloModal:""
        }
    }

    onFilteredChangeCustom = (value, accessor) => {
        let filtered = this.state.filtered;
        let insertNewFilter = 1;
    
        if (filtered.length) {
          filtered.forEach((filter, i) => {
            if (filter["id"] === accessor) {
              if (value === "" || !value.length) filtered.splice(i, 1);
              else filter["value"] = value;
              insertNewFilter = 0;
            }
          });
        }
    
        if (insertNewFilter) {
          filtered.push({ id: accessor, value: value });
        }
    
        this.setState({ filtered: filtered });
      };
    

    listarSucursal(codigoSucursal){
        if(codigoSucursal !== "" && codigoSucursal !== null){
            get_Sucursal(codigoSucursal).then(res => {
                this.setState({ sucursal : res })
            });
        }else{
            get_Sucursal_Lista().then(res => {
                this.setState({ sucursal : res})
            });
        }
    }

    cambiarAlerta(message){
        this.setState({ message:message });
        this.mostrarAlerta(3000);  
    }

    guardarSucursal = () =>{
        const { nombreSucursal, codigoSucursal } = this.state;
        const sucursal = {nombreSucursal, codigoSucursal };
        const message = {
            text:"Algo salio mal vuelve a intertarlo",
            alert:"danger"  
        };        
        switch (this.state.tipoAccion) {
            case "post":
                post_Sucursal(sucursal).then((result)=>{
                    message.alert = "success";
                    message.text = "Se guardo correctamente";
                    this.toogleModal();
                });
                break;
            case "put":
                put_Sucursal(sucursal).then((result)=>{
                    message.alert = "success";
                    message.text = "Se modifico correctamente";
                    this.toogleModal();
                });
                break;
            default:
                break;
        }
        this.cambiarAlerta(message);
    }

    eliminarSucursal(codigoSucursal){
        const message = { text : "" , alert : "" };
        if(codigoSucursal !== "" && codigoSucursal !== null){
            del_Sucursal(codigoSucursal).then(res => {
                message.alert = "danger";
                message.text = "Se elimino correctamente";
                this.listarSucursal("");         
            });
        }
        this.cambiarAlerta(message);
    }

    setValues = (e , field) => {       
        this.setState({ [field]: e.target.value });
    }

    verificarAccion(codigoSucursal,tituloModal){
        if(codigoSucursal !== null && codigoSucursal !== ""){
            get_Sucursal(codigoSucursal).then(res =>{
                if(res[0]){    
                    this.setState({
                        nombreSucursal:res[0].nombreSucursal,
                        codigoSucursal:res[0].codigoSucursal,
                        tipoAccion:"put",        
                        tituloModal: tituloModal
                    });
                }
            });
        }else{
            this.setState({ 
                nombreSucursal:"",
                codigoSucursal:"",
                tipoAccion:"post",
                tituloModal: tituloModal
            });
        }
        this.toogleModal();
    }
    toogleModal(){   
        this.setState({
            modalOpen : ! this.state.modalOpen,
        });
        if(!this.state.modalOpen){
            this.listarSucursal("");
        } 
    }

    mostrarAlerta = (tiempo) => {
        setTimeout(()=>{
            this.setState({ message : { text : '' , alert : ''} });            
        },tiempo);
    }

    componentDidMount(){
        this.listarSucursal("");
    }
    render() {
        const columns = [
            {
                Header: "ID",
                accessor: "id",
                style:{
                    textAlign: "center"
                },
                show:false
            },
            {
                Header: "CODIGO",
                accessor: "codigoSucursal",
                style:{
                    textAlign: "center"
                }
            },
            {
                Header: "Local",
                accessor: "nombreSucursal",
                style:{
                    textAlign: "center"
                }
            },
            {
                Header:"ACCIONES",
                Cell: props => {
                    return(
                        <div className="container">
                            <div className="row justify-content-center">
                                <div className="col-md-5">
                                    <button className="btn btn-danger"
                                        onClick={() =>{
                                            this.eliminarSucursal(props.original.codigoSucursal);
                                        }}
                                    >Borrar</button>
                                </div>
                                <div className="col-md-5 ml-auto">
                                    <button className="btn btn-warning"
                                    onClick={
                                        e => this.verificarAccion(props.original.codigoSucursal,"Modificar")                                           
                                    }>Editar</button>
                                </div>
                            </div>

                        </div>
                    )
                },                
                sortable: false,
                filterable: false
            }
        ]
        let buttonCard = {
            cursor: 'pointer'
        };
        return (
            <Menu>
            <div className="container p-4">
                {/*<MaterialTable></MaterialTable>*/}
                <div className="row">
                    <div className="col-md-11 mx-auto">
                        <div className="card ">
                            <div className="card-header">
                                <h3>Sucursales Registrados</h3>
                            </div>
                            <div className="m-2 text-center">
                                { this.state.message.text !==''?( <Alert color={this.state.message.alert}>{ this.state.message.text}</Alert> ) : '' }
                            </div>                            
                            <div className="row m-2">
                                <div className="col-sm" style={buttonCard}>
                                    <div className="card text-white bg-info"
                                        onClick={()=>{
                                            this.verificarAccion("","Registrar")
                                            //this.ver();
                                    }}>
                                    <div className="card-header">Registrar</div>
                                        <button className="btn card-body text-white text-justify">Sucursal.</button>
                                    </div>
                                </div>
                                
                                <div className="col-sm" style={buttonCard}>
                                    <div className="card bg-success text-white">
                                        <div className="card-header">Exportar</div>
                                        <ReactHTMLTableExcel
                                            id="test-table-xls-button"
                                            className="btn card-body text-white text-justify"
                                            table="table-to-xls"
                                            filename="SucursalExcel"
                                            sheet="hoja 1"
                                            buttonText="A Documento Excel."
                                        />
                                    </div>
                                </div>
                            </div>
                            <div className="card-body">
                                <ReactTable
                                    columns = {columns}
                                    noDataText = {"Cargando Datos..."}
                                    data = {this.state.sucursal}
                                    filterable
                                    filtered = {this.state.filtered}
                                    onFilteredChange = {(filtered, column, value) => {
                                        this.onFilteredChangeCustom(value, column.id || column.accessor);
                                      }}
                                      defaultFilterMethod={(filter, row, column) => {
                                        const id = filter.pivotId || filter.id;
                                        if (typeof filter.value === "object") {
                                          return row[id] !== undefined
                                            ? filter.value.indexOf(row[id]) > -1
                                            : true;
                                        } else {
                                          return row[id] !== undefined
                                            ? String(row[id]).indexOf(filter.value) > -1
                                            : true;
                                        }
                                      }}
                                    defaultPageSize={5}
                                    showPaginationTop={true}
                                    showPaginationBottom={false}
                                >
                                {(state, filtredData, instance)=>{
                                    this.reactTable = state.pageRows.map(
                                        sucursal => {return sucursal._original});
                                    return(
                                        <div>
                                            {filtredData()}
                                            <ExportToExcel sucursales = { this.reactTable }/>
                                        </div>
                                    )
                                }}
                                </ReactTable>
                            </div>
                            <Modal 
                                isOpen={this.state.modalOpen}
                                centered={true}
                                size={"xm"}
                            >
                                <ModalHeader toggle={this.toogleModal.bind(this)}>{this.state.tituloModal}</ModalHeader>    

                                <ModalBody>
                                    <div className="card ">
                                        <div className="card-header text-center">
                                            <h3>Tipo de Local</h3>
                                        </div>
                                        <div className="card-body ">
                                            {/*<form>*/}
                                                <div className="form-group">
                                                    <label for="nombreSucursal"> Nombre Local: </label>
                                                    <input className="form-control" id= "nombreSucursal" type="text" placeholder="Ejemplo: Almacen"
                                                        value = {this.state.nombreSucursal} 
                                                        onChange = { e => this.setValues(e,'nombreSucursal') }
                                                    />
                                                </div>
                                                <div className="text-center">
                                                    <button className="btn btn-primary"
                                                        onClick={this.guardarSucursal}
                                                    > REGISTRAR </button>
                                                </div>
                                            {/*</form>*/}
                                        </div>
                                    </div>
                                </ModalBody>
                            </Modal>
                        </div>
                    </div>
                </div>
            </div>
            </Menu>
        )
    }
}
