import React, { Component } from 'react';
class ExportExcel extends Component{
    render(){
        return(
            <div className="container row">
                <div>
                    <table hidden={true} id="table-to-xls">
                        <thead>
                            <tr>
                                <th>MED ID</th>
                                <th>Codigo</th>
                                <th>Media</th>
                                <th>Url</th>
                                <th>Tipo</th>
                            </tr>                                           
                        </thead>
                        <tbody>
                        {
                            this.props.medias.map(med =>{
                                return(
                                    <tr key={med.id}>
                                        <td>{med.id}</td>
                                        <td>{med.codigoMedia}</td>
                                        <td>{med.nombreMedia}</td>
                                        <td>{med.urlMedia}</td>  
                                        <td>{med.nombreTipoMedia}</td> 
                                    </tr>
                                );    
                            })
                        }
                        </tbody>
                    </table>
                </div>
            </div>
        );
    }
}

export default ExportExcel