// TIPOS API FROM SERVER NODE EXPRESS

import data from '../../db_data';
const BaseUrl = data + '/api/tipos/';

// LISTAR TIPOS DE DOCUMENTO
export function get_Tipo_Documento_Lista(){ 
    const urlFetch = BaseUrl + "documento";
    return new Promise((resolve,reject)=>{
        fetch(urlFetch)
        .then((response) => response.json())
        .then((responseJson) => {
            resolve(responseJson);
        })
        .catch((error)=>{
            reject(error);
        });
    });
}

// MOSTRAR TIPO DE DOCUMENTO CODIGO
export function get_Tipo_Documento(codigoTPDocumento){ 
    const urlFetch = BaseUrl + "documento/" + codigoTPDocumento;
    return new Promise((resolve,reject)=>{
        fetch(urlFetch)
        .then((response) => response.json())
        .then((responseJson) => {
            resolve(responseJson);
        })
        .catch((error)=>{
            reject(error);
        });
    });
}


//ELIMINAR TIPO DE DOCUMENTO
export function del_Tipo_Documento(codigotipoDocumento){ 
    const urlFetch = BaseUrl + "documento/" + codigotipoDocumento;
    return new Promise((resolve,reject)=>{
        fetch(urlFetch,{
            method:'DELETE',
            headers: new Headers({
                'Content-type':'application/json'
            })
        })
        .then((response) => response.json())
        .then((responseJson) => {
            resolve(responseJson);
        })
        .catch((error)=>{
            reject(error);
        });
    });
}

//GUARDAR TIPO DE DOCUMENTO
export function post_Tipo_Documento(tipoDocumento){ 
    const urlFetch = BaseUrl + "documento";
    return new Promise((resolve,reject)=>{
        fetch(urlFetch,{
            method:'POST',
            body: JSON.stringify(tipoDocumento),
            headers: new Headers({
                'Content-type':'application/json'
            })
        })
        .then((response) => response.json())
        .then((responseJson) => {
            resolve(responseJson);
        })
        .catch((error)=>{
            reject(error);
        });
    });
}

//MODIFICAR TIPO DE DOCUMENTO
export function put_Tipo_Documento(tipoDocumento){ 
    const urlFetch = BaseUrl + "documento";
    return new Promise((resolve,reject)=>{
        fetch(urlFetch,{
            method:'PUT',
            body: JSON.stringify(tipoDocumento),
            headers: new Headers({
                'Content-type':'application/json'
            })
        })
        .then((response) => response.json())
        .then((responseJson) => {
            resolve(responseJson);
        })
        .catch((error)=>{
            reject(error);
        });
    });
}
