import React, { Component } from 'react'

export class Footer extends Component {
    constructor(props){
        super(props);
        this.state = { 
            textoFooter : props.textoFooter, //Texto como Copyright  Autor Desarrollador
            nombreFooter : props.nombreFooter,//Autor
        }
    }
    render() {
        return (
            <div>
                <footer className="page-footer font-small blue pt-4">
                    <div className="footer-copyright text-center py-3">
                        ¬© 2020 {this.state.textoFooter} <a href="/app">{this.state.nombreFooter}</a>
                    </div>
                </footer>
            </div>
        )
    }
}


export default Footer
