import React, { Component } from 'react';

class ExportExcel extends Component{
    render(){
        return(
            <div className="container row">

                <div>
                    <table hidden={true} id="table-to-xls">
                        <thead>
                            <tr>
                                <th>TPD ID</th>
                                <th>Codigo</th>
                                <th>Producto</th>  
                                <th>Descripcion</th>
                            </tr>                
                        </thead>
                        <tbody>
                        {
                            this.props.tpProducto.map(tpPro =>{
                                return(
                                    <tr key={tpPro.id}>
                                        <td>{tpPro.id}</td>
                                        <td>{tpPro.codigoTipoProducto}</td>
                                        <td>{tpPro.nombreTipoProducto}</td>                                        
                                        <td>{tpPro.descripcionTipoProducto}</td>       
                                    </tr>
                                );    
                            })
                        }
                        </tbody>
                    </table>
                </div>
            </div>
        );
    }
}

export default ExportExcel