import data from '../../db_data';
const BaseUrl = data + '/api/productos/';

// ----------------------------------------------------------------------------------------------------------------
// LISTAR PRODUCTO
export function get_Producto_Lista(){ 
    const urlFetch = BaseUrl ;
    return new Promise((resolve,reject)=>{
        fetch(urlFetch)
        .then((response) => response.json())
        .then((responseJson) => {
            resolve(responseJson);
        })
        .catch((error)=>{
            reject(error);
        });
    });
}

// LISTAR PRODUCTO POR LOCAL
export function get_Producto_Lista_Local(codigoLocal){
    var urlFetch = BaseUrl + "local/";
    if(codigoLocal !== "" && codigoLocal !== null){
        urlFetch = urlFetch + codigoLocal;
    }
    return new Promise((resolve,reject)=>{
        fetch(urlFetch)
        .then((response) => response.json())
        .then((responseJson) => {
            resolve(responseJson);
        })
        .catch((error)=>{
            reject(error);
        });
    });
}

