import React, { Component } from 'react';
class ExportExcel extends Component{
    render(){
        return(
            <div className="container row">
                <div>
                    <table hidden={true} id="table-to-xls-venta">
                        <thead>
                            <tr>
                                <th>CODIGO</th>
                                <th>LOCAL</th>
                                <th>TRABAJADOR</th>
                                <th>FECHA</th>
                                <th>CLIENTE</th>
                                <th>TOTAL</th>
                            </tr>                                           
                        </thead>
                        <tbody>
                        {
                            this.props.ventas.map(ven =>{
                                return(
                                    <tr key={ven.id}>
                                        <td>{ven.codigoVenta}</td>
                                        <td>{ven.nombreLocal}</td>
                                        <td>{ven.nombreTrabajador}</td>
                                        <td>{ven.fechaRegistro}</td>
                                        <td>{ven.nombreCliente}</td>
                                        <td>{ven.valorTotalVenta}</td>
                                    </tr>
                                );    
                            })
                        }
                        </tbody>
                    </table>
                </div>
            </div>
        );
    }
}

export default ExportExcel