import React, { Component } from 'react'
import { Redirect } from 'react-router-dom';


import { get_Usuario_Trabajador } from '../Data/UsuarioData';
import { get_Local } from '../Data/LocalData';
import data from '../../db_data';
import Grafico from '../Vista/Grafico/Grafico';

import Menu from './menu';

export class profile extends Component {

    BaseUrl = data + '/api/usuarios/';

    constructor(props){
        super(props);
        this.state = {
            usuario:{},
            local:{},
            conectado:0
        }
    }

    setUsuario(usuario){
        this.setState({
            usuario:usuario,
        });
    }

    obtenerTienda(){
        get_Local(localStorage.getItem("codigoLocal")).then( res => {
            this.setState({ local : res[0] });
        });
    }

    componentDidMount(){
        this.obtenerTienda();
        try {
            const codigoUsuario  = localStorage.getItem('codigoUsuario'); 
            if(codigoUsuario !== "" && codigoUsuario !== null){
                get_Usuario_Trabajador(codigoUsuario).then((result)=>{
                    this.setState({ usuario: result[0]});
                }); 
            }
        } catch (e) {
            console.log('ERROR_PROFILE_PRIMER_INICIO_NAVEGADOR__'+ e);
        }

    }
    render() {
        const codigoUsuario  = localStorage.getItem('codigoUsuario');
        let buttonCard = {
            textDecoration: 'none',
        };
        if(codigoUsuario !== "" && codigoUsuario !== null){
            return (
                <Menu>
                <div className="container">
                    {/*<h1>{this.state.local.nombreTipoLocal} EN {this.state.local.nombreSucursal}</h1>
                    <h1>NOMBRE: {this.state.local.nombreLocal}</h1>
                    <h1>USUARIO: {this.state.usuario.nombreTrabajador}</h1>*/}
                    <div hidden={true} className="row my-4 justify-content-center">
                        <div className="col-md-3">
                            <a style={buttonCard} href="/ventas">
                                <div className="card text-white bg-primary">
                                    <div className="card-header">Ventas</div>
                                    <div className="card-body">Realizar una Venta</div>
                                </div>
                            </a>
                        </div>
                        <div className="col-md-3">
                            <a style={buttonCard} href="/app/productos">
                                <div className="card text-white bg-danger">
                                    <div className="card-header">Productos</div>
                                    <div className="card-body">Lista de Productos</div>
                                </div>
                            </a>
                        </div>
                        <div className="col-md-3">
                            <a style={buttonCard} href="/app/envios">
                                <div className="card text-white bg-warning">
                                    <div className="card-header">Envios</div>
                                    <div className="card-body">Registrar un Envio</div>
                                </div>
                            </a>
                        </div>
                        <div className="col-md-3">
                            <a style={buttonCard} href="/app/ingresos/pendientes">
                                <div className="card text-white bg-success">
                                    <div className="card-header">Ingresos</div>
                                    <div className="card-body">Confirmar un Ingreso</div>
                                </div>
                            </a>
                        </div>
                        <div className="col-md-12 mt-4">
                            <div className="bg-info px-3 py-1 text-white">
                                GRAFICOS PERIODO - 2020
                            </div>
                        </div>
                    </div>
                    <Grafico></Grafico>
                </div>
                </Menu>
            )
        }else{
            return(<Redirect to={'/app/login'}/>)
        }
    }
}

export default profile