import React, { Component } from 'react';

class ExportExcel extends Component{
    render(){
        return(
            <div className="container row">
                <div>
                    <table hidden={true} id="table-to-xls">
                        <thead>
                            <tr>
                                <th>TPD ID</th>
                                <th>Codigo</th>
                                <th>Documento</th>
                                <th>Cantidad</th>     
                            </tr>                
                        </thead>
                        <tbody>
                        {
                            this.props.tpDocumento.map(tpDoc =>{
                                return(
                                    <tr key={tpDoc.id}>
                                        <td>{tpDoc.id}</td>
                                        <td>{tpDoc.codigoTipoDocumento}</td>
                                        <td>{tpDoc.nombreTipoDocumento}</td>
                                        <td>{tpDoc.cantidadCaracteres}</td>        
                                    </tr>
                                );    
                            })
                        }
                        </tbody>
                    </table>
                </div>
            </div>
        );
    }
}

export default ExportExcel