import React, { Component} from 'react';

import IconoAdd from '@material-ui/icons/AddToQueue';
import IconoExcel from '@material-ui/icons/ExplicitOutlined';
import IconoPdf from '@material-ui/icons/PictureAsPdf';


import ReactHTMLTableExcel from 'react-html-table-to-excel';
import ExportToExcel from "../../Export/excel/producto";

import { Modal , ModalHeader, ModalBody, Alert } from 'reactstrap';

import { post_Producto, put_Producto, del_Producto , get_Producto, post_Cantidad_Productos, post_Producto_Local_Inventario_Paginado} from '../../Data/productoData';
import "./Producto.css";

import { post_Media, post_Media_File , get_Media_Lista} from '../../Data/MediaData';
import { get_Tipo_Media_Nombre , get_Tipo_Producto_Lista} from '../../Data/tiposData';
import { get_Presentacion_Lista } from '../../Data/PresentacionData';
import { get_Local_Lista } from '../../Data/LocalData';

import { get_Rol_Usuario } from '../../Data/RolUsuarioData';

import Menu from '../../Pages/menu';

import ProductoCard from './ProductoCard';
import Paginacion from '../../Pages/Pagination';

const estadoInicial = {

    message : {
        text:'',
        alert: ''
    },
    messageActivo:false,

    producto:{
        codigoProducto:"",
        descripcionProducto:"",
        detalleProducto:"",
        codigoTipoProducto:"",
        codigoPresentacion:"",
        codigoMedia:"MED-00000000004",
        codigoLocal:"",
        valorPrecio:0,
        stockExistente:0,
        stockMinimo:0,
        estadoProducto:1,
        fechaVencimiento:"",
        nombreTipoProducto:"",
        descripcionPresentacion:"",
        nombreLocal:"",
    },
    
    codigoProducto: "",
    fechaVencimiento:"",

    productos : [],
    sugerenciasProducto : [],

    media:{
        codigoMedia:"",
        nombreMedia:"",
        altMedia:"",
        urlMedia:"", 
        codigoTipoMedia:"",
        codigoUsuario:"",
    },
    medias:[],

    tipoProductos:[],
    presentaciones:[],
    locales:[],

    tipoAccion:"post",
    codigoMedia:"",

    modalOpen: false,
    modalFormMediaOpen:false,
    modalListaMediaOpen:false,

    nombreFile:"",
    file:null,

    fileTemporal:null,
 
    tituloModal:"",

    tipoAdmi:false,

    codigoLocal : localStorage.getItem("codigoLocal"),

    modalConfirmarOpen: false,
    tipoEliminar : "",
    codigoProductoEliminar : "",
    

    
    nombreProductoBuscar : "",
    cargando : true,
    currentPage : 1,
    productosPage : 16,
    cantidadPages : 0,

    cantidadProductos:0,

    heightWindows : "",             
};

export default class Producto extends Component {
   
    constructor (props){
        super(props);
        this.state = estadoInicial;
        
        this.onChange = this.onChange.bind(this);
        this.cambiarPagina = this.cambiarPagina.bind(this);
    }

    cambiarPagina = (pagina) => {
        this.setState({currentPage:pagina},()=>this.listarProducto());//Cambia a la pagina
    }

    toogleModalConfirmar = () => {
        this.setState({ modalConfirmarOpen : ! this.state.modalConfirmarOpen });
    }

    mostrarModal = (codigoProducto) => {
        this.setState({ codigoProductoEliminar : codigoProducto });
        this.toogleModalConfirmar();
    }

    borrarConfirmado = () => {
        const { codigoProductoEliminar } = this.state;
        this.eliminarProducto(codigoProductoEliminar);
        this.toogleModalConfirmar();
    }

    setProducto = (e , field) => {
        const { producto } = this.state;
        producto[field] = e.target.value;
        this.setState({ producto });
    }

    setMedia = (e , field) => {
        const { media } = this.state;
        media[field] = e.target.value;       
        this.setState({ media });
    }
    
    setFileMedia = e => {
        if(e.target.files[0]){
            let file = e.target.files[0];     
            const nombreBusqueda = file.type.replace("/","-");
            const { media } = this.state;
            get_Tipo_Media_Nombre(nombreBusqueda).then((result)=>{
                media["codigoTipoMedia"] = result[0].codigoTipoMedia;
                this.setState({
                    file : file,
                    fileTemporal : URL.createObjectURL(file),
                    media: media,
                    nombreFile : file.name
                });
            });
        }        
    }

    onChange(e){
        const { producto } = this.state;
        producto[e.target.id] = e.target.value;
        this.setState({ producto });
    }

    toogleModal(){
        this.setState({
            modalOpen : ! this.state.modalOpen,
        });    
    }

    mostrarAlerta = (tiempo) => {
        setTimeout(()=>{
            this.setState({ message : { text : '' , alert : ''} });            
        },tiempo);
    }

    guardarMedia = () =>{
        const codigoUsuario  = localStorage.getItem('codigoUsuario');
        if(this.state.file){
            const { file , media } = this.state;
            media["nombreMedia"] = file.name;
            media["altMedia"] = file.name;
            media["urlMedia"] = "/img/" + file.name;
            media["codigoUsuario"] = codigoUsuario;
            this.setState({ media });

            post_Media(media).then((result)=>{
                this.setState({ codigoMedia : result[0][0].codigoMedia });
                post_Media_File(file).then((result)=>{
                    this.guardarProducto(true);
                });
            });           
        }else{
            if(this.state.producto.codigoProducto !== null && this.state.producto.codigoProducto !== ""){
                this.guardarProducto(false);
            }else{
                alert("Ingrese una Imagen");
            }
        }
    }

    ObtenerRolesUsuario = (codigoUsuario) => {
        get_Rol_Usuario(codigoUsuario).then( res => {
            if(res[0].nombreRol === "ROL-SUPERADMIN" || res[0].nombreRol ==="ROL-ADMIN"){
                this.setState({
                    tipoAdmi: !this.state.tipoAdmi,
                });
            }
        });
    }

    listarProducto = () => {
        var inputBusqueda = document.getElementById("busquedaProducto");
        
        var localBusqueda = this.state.codigoLocal;
        var textoBusqueda="";
        if(inputBusqueda.value.length>0){textoBusqueda=inputBusqueda.value}
        
        const Busqueda={codigoLocal:localBusqueda,textoBusqueda:textoBusqueda,inicio:(this.state.productosPage*(this.state.currentPage-1)),resultados:this.state.productosPage};
        post_Producto_Local_Inventario_Paginado(Busqueda).then(productos => {
            post_Cantidad_Productos(Busqueda).then(cantidad => {
                this.setState({cantidadProductos:cantidad[0].cantidadProductos,productos:productos,sugerenciasProducto:productos,cargando:false});
                //document.getElementById("busquedaProducto").value="";
            });
        });
    }

    listarTipoProducto(){
        get_Tipo_Producto_Lista().then((res)=>{
            this.setState({ tipoProductos : res });
        });
    }

    listarPresentacionProducto(){
        get_Presentacion_Lista().then((res) =>{
            this.setState({ presentaciones : res });
        });
    }

    listarMedia(){
        get_Media_Lista().then((res) =>{
            this.setState({ medias : res });
        });
    }

    listarLocales(){
        get_Local_Lista().then((res)=>{
            this.setState({ locales : res });
        });
    } 
    
    cambiarAlerta(message){
        this.setState({ message:message });
        this.mostrarAlerta(3000);  
    }

    guardarProductoPrueba = () => {
        if(this.state.producto.descripcionProducto){
            if(this.state.producto.detalleProducto){
                if(this.state.producto.codigoTipoProducto){
                    if(this.state.producto.codigoPresentacion){          
                        this.guardarMedia();
                    }else{ alert("Ingrese la Presentaci√≥n") }
                }else{ alert("Ingrese el Tipo") }
            }else{ alert("Ingrese el Detalle") }
        }else{ alert("Ingrese una Descripci√≥n") }
    }

    guardarProducto = (ingresoMedia) => {  
        const { producto } = this.state;


        if(ingresoMedia){ //Si se ingresara una Media
            producto["codigoMedia"] = this.state.codigoMedia;
        }

        producto["codigoLocal"] = this.state.codigoLocal;
        producto["fechaVencimiento"] = this.state.fechaVencimiento;
        
        this.setState({ producto });
        const message = {
            text:"Algo salio mal vuelve a intertarlo",
            alert:"danger"  
        };

        switch (this.state.tipoAccion) {
            case "post":
                post_Producto(producto).then((result)=>{
                    message.alert = "success";
                    message.text = "Se guardo correctamente";
                    this.listarProducto();
                    this.cambiarAlerta(message);
                    this.toogleModal();
                });
                break;
            case "put":
                put_Producto(producto).then((result)=>{
                    message.alert = "success";
                    message.text = "Se modifico correctamente";                  
                    this.listarProducto();
                    this.cambiarAlerta(message);
                    this.toogleModal();
                });
                break;
            default:
                break;
        }
        this.setState({
            producto:{},
            media:{},
            codigoMedia:"",
            nombreFile:"",
            file:null
        });
    } 

    eliminarProducto(codigoProducto){
        const message = {
            text:"",
            alert:""  
        };
        if(codigoProducto !== "" && codigoProducto !== null){
            del_Producto(codigoProducto).then(res => {
                message.alert = "danger";
                message.text = "Se elimino correctamente";
                this.listarProducto();         
            });
        }
        this.cambiarAlerta(message);
    }

    obtenerFechaHoy(){
        var fechaHoy = new Date();
        
        var a√±oDate = fechaHoy.getFullYear();
        var mesDate = fechaHoy.getMonth() + 1;
        var diaDate = fechaHoy.getDate();

        var fechaVencimiento = a√±oDate + "-";
        if(mesDate < 10){
            fechaVencimiento = fechaVencimiento + "0" + mesDate; 
        }
        fechaVencimiento = fechaVencimiento + "-";
        if(diaDate < 10){
            fechaVencimiento = fechaVencimiento + "0" + diaDate; 
        }else{
            fechaVencimiento = fechaVencimiento + diaDate; 
        }
        const { producto } = this.state;
        producto["fechaVencimiento"] = fechaVencimiento;

        this.setState({
            fechaVencimiento : fechaVencimiento,
            producto: producto
        });            
    }

    verificarAccion = (codigoProducto) => {
        if(codigoProducto !== null && codigoProducto !== ""){
            get_Producto((codigoProducto + "_" + this.state.codigoLocal)).then(res =>{
                if(res[0]){
                    res[0].fechaVencimiento = res[0].fechaVencimiento.substring(0,10);
                    this.setState({
                        producto:res[0],
                        codigoProducto:res[0].codigoProducto,
                        fechaVencimiento: res[0].fechaVencimiento.substring(0,10),
                        nombreFile : res[0].urlMedia.replace("/img/",""),
                        fileTemporal : res[0].urlMedia,
                        tipoAccion:"put",        
                        tituloModal: "Modificar Producto"
                    });
                }
            });
        }else{
            this.obtenerFechaHoy();
            const productoNuevo = {
                fechaVencimiento: this.state.producto.fechaVencimiento,
                stockExistente:0,
                stockMinimo:0,
                valorPrecio:0,
                nombreTipoProducto:"Seleccione un Tipo",
                codigoTipoProducto:"-",
                descripcionPresentacion:"Seleccione la Presentacion",
                nombreLocal:"Seleccione el Local",
            }

            this.setState({ 
                producto:productoNuevo,
                nombreFile:"",
                fileTemporal:"/img/Imagen-Caja-Presentacion.png",
                codigoProducto:"",
                tipoAccion:"post",
                tituloModal: "Registrar Producto"
            });
        }
        this.toogleModal();
    }

    resetEstado = () => {
        this.setState(estadoInicial);
    };

    componentDidMount(){
        this.ObtenerRolesUsuario(localStorage.getItem("codigoUsuario")); // y LISTAR PRODUCTOS LUEGO DE VER QUIEN
        this.listarTipoProducto();
        this.listarPresentacionProducto();
        this.listarMedia();
        this.listarLocales();
        this.listarProducto();
    }
    
    render() {
        return (
            <Menu>
                <div className="row">
                    <div className="col">
                        <div className="card">
                            <div className="card-header">{/* hidden={!this.state.tipoAdmi*/}
                                <div className="row">
                                    <div className="col-8 col-sm-8 col-md-9">
                                        <h3>Productos en Existencia</h3>
                                    </div>
                                    <div className="col-4 col-sm-4 col-md-3">
                                        <div className="row">
                                            <div className="col-4">
                                                <button 
                                                    className="btn btn-info btn-circle"
                                                    onClick={()=>{this.verificarAccion("")}}
                                                > <IconoAdd /> </button>
                                            </div>
                                            
                                            <div className="col-4">
                                                <ReactHTMLTableExcel
                                                    id="test-table-xls-button"
                                                    className="btn btn-success btn-circle"
                                                    table="table-to-xls"
                                                    filename="productosExcel"
                                                    sheet="hoja 1"
                                                    buttonText={<IconoExcel />}
                                                />
                                            </div>
                                            <div className="col-4">
                                                <button className="btn btn-danger btn-circle"> <IconoPdf /> </button>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div className="m-2 text-center">
                                { this.state.message.text !==''?( <Alert color={this.state.message.alert}>{ this.state.message.text}</Alert> ) : '' }
                            </div>                            
                            <div className="row justify-content-center mx-4">
                                <div className="col-10 col-sm-10 col-md-10 form-group">
                                    <input className = "form-control" type = "text"
                                        placeholder = "Buscar todos los Productos" id="busquedaProducto"
                                    ></input>
                                </div>
                                <div className="col-2 col-sm-2 col-md-2 form-group">
                                    <button className="btn btn-info" onClick={()=>{this.listarProducto()}}>Buscar</button>
                                </div>
                            </div>
                            <div className="container">
                                <Paginacion 
                                    totalElementos = {this.state.cantidadProductos} 
                                    elementosPage = {this.state.productosPage} 
                                    cambiarPagina = {this.cambiarPagina}
                                    paginaActual = {this.state.currentPage}
                                />
                            </div>
                            <div className="mr-3">
                                <ProductoCard productos={this.state.sugerenciasProducto} cargando={this.state.cargando} verificarAccion={this.verificarAccion}/>
                            </div>
                            <div className="container">
                                <Paginacion 
                                    totalElementos = {this.state.cantidadProductos} 
                                    elementosPage = {this.state.productosPage} 
                                    cambiarPagina = {this.cambiarPagina}
                                    paginaActual = {this.state.currentPage}
                                />
                            </div>
                            <ExportToExcel productos={this.state.sugerenciasProducto}/>

                            <Modal 
                                isOpen={this.state.modalOpen}
                                centered={true}
                                size={"xm"}
                            >
                                <ModalHeader toggle={this.toogleModal.bind(this)}>{this.state.tituloModal}</ModalHeader>    

                                <ModalBody>
                                    <div className="card ">
                                        <div className="card-body ">
                                            <div className="row">
                                                <div className="col-md-6 form-group">
                                                    <label for="descripcionProducto"> Nombre: </label>
                                                    <input className="form-control" id= "descripcionProducto" type="text" placeholder="Ejemplo: Aceite"
                                                        value = {this.state.producto.descripcionProducto} 
                                                        onChange = { e => this.setProducto(e,'descripcionProducto') }
                                                    />
                                                </div>
                                                <div className="col-md-6 form-group">
                                                    <label for="detalleProducto"> Marca: </label>
                                                    <input className="form-control" id= "detalleProducto" type="text" placeholder="Ejemplo: De Coco"
                                                        value = {this.state.producto.detalleProducto} 
                                                        onChange = { e => this.setProducto(e,'detalleProducto') }
                                                    />
                                                </div>
                                                <div className="col-md-6 form-group">
                                                    <label for="codigotipoProducto"> Tipo: </label>
                                                    <select className={"form-control custom-select "} id="codigoTipoProducto" onChange={this.onChange}>
                                                        <option value = {this.state.producto.codigoTipoProducto}>{this.state.producto.nombreTipoProducto}</option>
                                                        {this.state.tipoProductos.map((tpPro) => 
                                                        ( <option value={tpPro.codigoTipoProducto} key={tpPro.id}>{tpPro.nombreTipoProducto}</option>))}
                                                    </select>
                                                </div>                    

                                                <div className="col-md-6 form-group">
                                                    <label for="codigoPresentacion"> Presentacion: </label>
                                                    <select className={"form-control custom-select "} id="codigoPresentacion" onChange={this.onChange}>
                                                        <option value = {this.state.producto.codigoPresentacion}>{this.state.producto.descripcionPresentacion}</option>
                                                        {this.state.presentaciones.map((pre) => 
                                                        ( <option value={pre.codigoPresentacion} key={pre.id}>{pre.descripcionPresentacion}</option>))}
                                                    </select>
                                                </div>
                                                <div className="col-md-12 form-group">
                                                    <label for="mediaProducto"> Imagen:</label>
                                                    <div class="custom-file">
                                                        <input type="file" class="custom-file-input" id="customFileLang" lang="es"
                                                            onChange = { e => this.setFileMedia(e) }
                                                        />
                                                        <label data-browse="Buscar" class="custom-file-label" for="customFileLang">{ this.state.nombreFile}</label>
                                                    </div>
                                                </div>
                                                <div className="container">
                                                    <div className="row">
                                                        <div className="col-md-6">
                                                            <div className="cardImagen text-center">
                                                                <img src={this.state.fileTemporal} alt={this.state.producto.descripcionProducto} className="imagenProducto"/>
                                                            </div>
                                                        </div>
                                                        <div className="col-md-6">
                                                            <div className="form-group">
                                                                <label for="valorPrecio"> Precio S/.: </label>
                                                                <input className="form-control" id= "valorPrecio" type="number"
                                                                    placeholder = "0"
                                                                    value1 = {parseFloat(this.state.producto.valorPrecio).toFixed(2)} 
                                                                    value = {this.state.producto.valorPrecio} 
                                                                    onChange = { e => this.setProducto(e,'valorPrecio') }
                                                                />
                                                            </div>
                                                            <div className="form-group">
                                                                <label for="stockExistente"> Stock: </label>
                                                                <input className="form-control" id= "stockExistente" type="number"
                                                                    value = {this.state.producto.stockExistente} 
                                                                    onChange = { e => this.setProducto(e,'stockExistente') }
                                                                />
                                                            </div>
                                                            <div className="form-group">
                                                                <label for="stockMinimo"> Min: </label>
                                                                <input className="form-control" id= "stockMinimo" type="number"
                                                                    value = {this.state.producto.stockMinimo} 
                                                                    onChange = { e => this.setProducto(e,'stockMinimo') }
                                                                />
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                                <div hidden={true} className="col-md-6 form-group">
                                                    <label for="codigoLocal"> Ubicaci√≥n: </label>
                                                    <select className={"form-control custom-select "} id="codigoLocal" onChange={this.onChange}>
                                                        <option value = {this.state.codigoLocal}>
                                                            {this.state.producto.nombreLocal}
                                                        </option>
                                                        {this.state.locales.map((loc) => 
                                                        ( <option value={loc.codigoLocal} key={loc.id}>{loc.nombreLocal}</option>))}
                                                    </select>
                                                </div>
                                                <div hidden={true} className="col-md-6 form-group">
                                                    <label for="fechaVencimiento"> Fecha Vencimiento: </label>
                                                    <input className="form-control" id= "fechaVencimiento" type="date" data-date-format="YYYY MM DD"
                                                        value = {this.state.producto.fechaVencimiento} 
                                                        onChange = { e => this.setProducto(e,'fechaVencimiento') }
                                                    />
                                                </div>

                                            </div>
                                            <div className="text-center">
                                                <button className="btn btn-primary"
                                                    onClick={ this.guardarProductoPrueba}
                                                > GUARDAR </button>
                                                &nbsp;
                                                <button className="btn btn-danger"
                                                    onClick={()=>this.toogleModal()}
                                                > CANCELAR </button>
                                            </div>
                                        </div>
                                    </div>
                                </ModalBody>
                            </Modal>
                            <Modal 
                                    isOpen={this.state.modalConfirmarOpen}
                                    centered={true}
                                    size={"xm"}
                            >
                                <ModalHeader toggle={this.toogleModalConfirmar.bind(this)}>Alerta</ModalHeader>    
                                <ModalBody>
                                    <div className="container">
                                        <div className="row justify-content-center text-center">
                                            <div className="col-md-12 mb-4">
                                                <span>Se eliminar√° el Registro, ¬øDesea Continuar? </span>
                                            </div>
                                            <div className="col-md-3">
                                                <button className="btn btn-danger"
                                                    onClick={this.borrarConfirmado}
                                                >Eliminar</button>
                                            </div>
                                            <div className="col-md-3">
                                                <button className="btn btn-info"
                                                    onClick = {this.toogleModalConfirmar.bind(this)}
                                                >Cancelar</button>
                                            </div>
                                        </div>
                                    </div>
                                </ModalBody>
                            </Modal>
                        </div>
                    </div>
                </div> 
            </Menu>
        )
    }
}