import React, { Component } from 'react'
import { Link } from 'react-router-dom';
import MenuIcon from '@material-ui/icons/Menu';
import InboxIcon from '@material-ui/icons/MoveToInbox';
import MoreVertIcon from '@material-ui/icons/MoreVert';
import StarBorder from "@material-ui/icons/StarBorder";
import ExpandLess from '@material-ui/icons/ExpandLess';
import ExpandMore from '@material-ui/icons/ExpandMore';
import { makeStyles } from '@material-ui/core/styles';
import { 
    AppBar, CssBaseline , Drawer , Hidden , IconButton , ListItemIcon , ListItemText ,
    Toolbar , Menu, MenuList , MenuItem , Typography , Collapse , List , ListItem 
} from '@material-ui/core';

import { get_Usuario_Trabajador } from '../Data/UsuarioData';
import { del_Registro_Vacio } from '../Data/RegistroData';
import { get_Rol_Usuario } from '../Data/RolUsuarioData';
import { get_Local } from '../Data/LocalData';
import Footer from './Footer';

class Dashboard extends Component {
  constructor(props){
    super(props);
    this.state = {
      usuario : {},
      rolUsuario : {},
      local:{},

      rolSuper : false,
      rolAdmi : false,
      rolTienda : false,
      rolAlmace : false,

      conectado : 0
    }
  };

  obtenerTienda(codigoLocal){
    get_Local(codigoLocal).then( res => {
        this.setState({ local : res[0] });
    });
  }

  ObtenerRolesUsuario = (codigoUsuario) => {
    get_Rol_Usuario(codigoUsuario).then( res => {
      switch (res[0].nombreRol) {
        case "ROL-SUPERADMIN":
            this.setState({
              rolSuper : !this.state.rolSuper,
              rolAdmi : !this.state.rolAdmi,
              rolTienda : !this.state.rolTienda,
              rolAlmace : !this.state.rolAlmace
            });
          break;
        case "ROL-ADMIN":
            this.setState({
              rolAdmi : !this.state.rolAdmi,
              rolTienda : !this.state.rolTienda,
              rolAlmace : !this.state.rolAlmace
            });
          break;
        case "ROL-TIENDA":
            this.setState({
              rolTienda : !this.state.rolTienda
            });
          break;
          case "ROL-ALMACEN":
            this.setState({
              rolAlmace : !this.state.rolAlmace
            });
          break;
        default:
          break;
      }
      this.setState({ rolUsuario : res[0] });
    });
  }

  componentDidMount(){
    var codigoUsuarioC = "";
    var codigoLocalC = "";
    try { 
      codigoUsuarioC = localStorage.getItem('codigoUsuario');
      codigoLocalC = localStorage.getItem('codigoLocal');
      if(codigoUsuarioC !== "" && codigoUsuarioC !== null){
        if(codigoLocalC !== "" && codigoLocalC !== null){
          this.obtenerTienda(codigoLocalC);
        }
        get_Usuario_Trabajador(codigoUsuarioC).then((res) => {
          if(res[0]){
            this.ObtenerRolesUsuario(codigoUsuarioC);
            this.setState({ usuario: res[0] , conectado:1});
          }
        });
      }
    } catch (e) {
      console.log('ERROR_NO_CODIGO_USER__' + e);
    }
  }

  render(){
    return(
      <div>
        <Menus 
          usuario={this.state.usuario} 
          local={this.state.local}
          rolSuper = {this.state.rolSuper}
          rolAdmi = {this.state.rolAdmi}
          rolTienda = {this.state.rolTienda}
          rolAlmace = {this.state.rolAlmace}
        >
          {this.props.children}
        </Menus >
      </div>
    );
  }
}

const drawerWidth = 175;
const ITEM_HEIGHT = 48;

const useStyles = makeStyles(theme => ({
    root: {
        display: 'flex',
    },
    drawer: {
        [theme.breakpoints.up('sm')]: {
            width: drawerWidth,
            flexShrink: 0,
        },
    },
    appBar: {
        marginLeft: drawerWidth,
        [theme.breakpoints.up('sm')]: {
            width: `calc(100% - ${drawerWidth}px)`,
        }
    },
    toolbarButtons: {
        marginLeft: "auto",
        marginRight: -12
    },
    menuButton: {
        [theme.breakpoints.up('sm')]: {
            display: 'none',
        },        
        marginRight: 20,
        marginLeft: -12
    },

    toolbar: theme.mixins.toolbar,
    
    drawerPaper: {
        width: drawerWidth,
    },
    content: {
        flexGrow: 1,
        padding: theme.spacing(2),
    },
    imagenPerfil: {
      width: '60px'
    }
}));

function Menus(props) {
  const { container , children , usuario, rolSuper, rolAdmi , rolTienda , local} = props;

  const classes = useStyles();
  const [mobileOpen, setMobileOpen] = React.useState(false);

  const [anchorEl, setAnchorEl] = React.useState(null);
  
  const [openMenuItemTipos, setOpenMenuItemTipos] = React.useState(false);
  const [openMenuItemProducto, setOpenMenuItemProducto] = React.useState(false);
  const [openMenuItemIngreso, setOpenMenuItemIngreso] = React.useState(false);
  const [openMenuItemEnvio, setOpenMenuItemEnvio] = React.useState(false);
  const [openMenuItemGrafico, setOpenMenuItemGrafico] = React.useState(false);

  function handleMenuItemTipos() {
    setOpenMenuItemTipos(!openMenuItemTipos);
  }

  function handleMenuItemProducto() {
    setOpenMenuItemProducto(!openMenuItemProducto);
  }

  function handleMenuItemIngreso() {
    setOpenMenuItemIngreso(!openMenuItemIngreso);
  }

  function handleMenuItemEnvio() {
    setOpenMenuItemEnvio(!openMenuItemEnvio);
  }

  function handleMenuItemGrafico() {
    setOpenMenuItemGrafico(!openMenuItemGrafico);
  }

  const open = Boolean(anchorEl);
  
  function handleClick(event) {
    setAnchorEl(event.currentTarget);
  }

  function handleClose() {
    setAnchorEl(null);
  }

  function handleDrawerToggle() {
    setMobileOpen(!mobileOpen);
  }

  const itemsMenuSuperior = [
    { nombre:"Ingresar",icono: <InboxIcon/> ,url:"/app/login"},
    { nombre:"Salir", icono: <InboxIcon/>,url:"/app/logout"},
    { nombre:"Configurar", icono: <InboxIcon/>,url:"/app/configurar" },
    { nombre:"Perfil", icono: <InboxIcon/>,url:"/app" }
  ]
  
  function EliminarRegistrosVacios () {
    del_Registro_Vacio(localStorage.getItem("codigoLocal")).then( res => {});
  }
  
//usuarioConectado();
  const MenuLateral = (
    
    <div>
      <Hidden>
        <img src={"/img/Logo-Hampina-Wassi.png"} alt={"Logo HampinaWassi"} className="card-img-top rounded-circle py-4"/>
        <h3 className="text-center">Sistema de Ventas</h3>
      </Hidden>
      <MenuList> 

        <MenuItem button component={Link} to="/app"
            onClick={EliminarRegistrosVacios}
        >
          <ListItemText primary="Principal"/>
        </MenuItem>

        <MenuItem hidden={true} button onClick={handleMenuItemTipos}>
          <ListItemText primary="Tipos"/>
          {openMenuItemTipos ? <ExpandLess /> : <ExpandMore />}
        </MenuItem>
        <Collapse in={openMenuItemTipos} timeout="auto" unmountOnExit>
          <List component="div" disablePadding>
            <ListItem hidden={!rolAdmi} button component={Link} className="" to="/app/tipos/documento"
              onClick={EliminarRegistrosVacios}
            >
              <ListItemIcon> <StarBorder /> </ListItemIcon>
              <ListItemText primary="Documento" />
            </ListItem>
            <ListItem hidden={!rolSuper} button component={Link} className="" to="/app/tipos/local"
              onClick={EliminarRegistrosVacios}
            >
              <ListItemIcon> <StarBorder /> </ListItemIcon>
              <ListItemText primary="Local" />
            </ListItem>
            <ListItem hidden={!rolSuper} button component={Link} className="" to="/app/tipos/media"
              onClick={EliminarRegistrosVacios}
            >
              <ListItemIcon> <StarBorder /> </ListItemIcon>
              <ListItemText primary="Media" />
            </ListItem>
            <ListItem hidden={!rolSuper} button component={Link} className="" to="/app/tipos/precio"
              onClick={EliminarRegistrosVacios}
            >
              <ListItemIcon> <StarBorder /> </ListItemIcon>
              <ListItemText primary="Precio" />
            </ListItem>
            <ListItem hidden={!rolAdmi} button component={Link} className="" to="/app/tipos/producto"
              onClick={EliminarRegistrosVacios}
            >
              <ListItemIcon> <StarBorder /> </ListItemIcon>
              <ListItemText primary="Producto" />
            </ListItem>
            <ListItem hidden={!rolSuper} button component={Link} className="" to="/app/tipos/traslado"
              onClick={EliminarRegistrosVacios}
            >
              <ListItemIcon> <StarBorder /> </ListItemIcon>
              <ListItemText primary="Traslado" />
            </ListItem>
          </List>
        </Collapse>

        <MenuItem hidden={true} button component={Link} to="/app/medias/listar"
          onClick={EliminarRegistrosVacios}
        >
          <ListItemText primary="Imagenes"/>
        </MenuItem>

        <MenuItem button component={Link} className="" to="/app/productos"
          onClick={EliminarRegistrosVacios}
        >
          <ListItemText primary="Productos"/>
        </MenuItem>
        
        <MenuItem hidden={!rolAdmi} button component={Link} className="" to="/app/locales/almacen"
          onClick={EliminarRegistrosVacios}
        >
          <ListItemText primary="Almacenes"/>
        </MenuItem>

        <MenuItem hidden={!rolAdmi} button component={Link} className="" to="/app/locales/tienda"
          onClick={EliminarRegistrosVacios}
        >
          <ListItemText primary="Tiendas"/>
        </MenuItem>
        
        <MenuItem hidden={!rolAdmi} button component={Link} className="" to="/app/sucursales"
          onClick={EliminarRegistrosVacios}
        >
          <ListItemText primary="Sucursales"/>
        </MenuItem>
       
        <MenuItem hidden={!rolSuper} button component={Link} className="" to="/app/usuarios"
          onClick={EliminarRegistrosVacios}
        >
          <ListItemText primary="Usuarios"/>
        </MenuItem>
        
        <MenuItem hidden={!rolTienda} button onClick={handleMenuItemEnvio}>
          <ListItemText primary="Envios"/>
          {openMenuItemEnvio ? <ExpandLess /> : <ExpandMore />}
        </MenuItem>
        <Collapse in={openMenuItemEnvio} timeout="auto" unmountOnExit>
          <List component="div" disablePadding>
            <ListItem button component={Link} className="" to="/app/envios"
              onClick={EliminarRegistrosVacios}
            >
              <ListItemIcon> <StarBorder /> </ListItemIcon>
              <ListItemText primary="Registrar" />
            </ListItem>
            <ListItem button component={Link} className="" to="/app/envios/todos"
              onClick={EliminarRegistrosVacios}
            >
              <ListItemIcon> <StarBorder /> </ListItemIcon>
              <ListItemText primary="Listar" />
            </ListItem>
          </List>
        </Collapse>

        <MenuItem hidden={!rolTienda} button onClick={handleMenuItemIngreso}>
          <ListItemText primary="Ingresos"/>
          {openMenuItemIngreso ? <ExpandLess /> : <ExpandMore />}
        </MenuItem>
        <Collapse in={openMenuItemIngreso} timeout="auto" unmountOnExit>
          <List component="div" disablePadding>
            <ListItem button component={Link} className="" to="/app/ingresos/pendientes"
              onClick={EliminarRegistrosVacios}
            >
              <ListItemIcon> <StarBorder /> </ListItemIcon>
              <ListItemText primary="Pendientes" />
            </ListItem>
            <ListItem button component={Link} className="" to="/app/ingresos/todos"
              onClick={EliminarRegistrosVacios}
            >
              <ListItemIcon> <StarBorder /> </ListItemIcon>
              <ListItemText primary="Listar" />
            </ListItem>
          </List>
        </Collapse>

        <MenuItem hidden={!rolTienda} button onClick={handleMenuItemProducto}>    
          <ListItemText primary="Ventas"/>
          {openMenuItemProducto ? <ExpandLess /> : <ExpandMore />}
        </MenuItem>
        <Collapse in={openMenuItemProducto} timeout="auto" unmountOnExit>
          <List component="div" disablePadding>
            <ListItem button component={Link} className="" to="/app/ventas"
              onClick={EliminarRegistrosVacios}
            >
              <ListItemIcon> <StarBorder /> </ListItemIcon>
              <ListItemText primary="Registrar" />
            </ListItem>
            <ListItem button component={Link} className="" to="/app/ventas/listar"
              onClick={EliminarRegistrosVacios}
            >
              <ListItemIcon> <StarBorder /> </ListItemIcon>
              <ListItemText primary="Realizadas" />
            </ListItem>
          </List>
        </Collapse>
      
        <MenuItem hidden={true} button onClick={handleMenuItemGrafico}>
          <ListItemText primary="Graficos"/>
          {openMenuItemGrafico ? <ExpandLess /> : <ExpandMore />}
        </MenuItem>
        <Collapse in={openMenuItemGrafico} timeout="auto" unmountOnExit>
          <List component="div" disablePadding>
            <ListItem button component={Link} className="" to="/app/grafico/ingresos"
              onClick={EliminarRegistrosVacios}
            >
              <ListItemIcon> <StarBorder /> </ListItemIcon>
              <ListItemText primary="Ingresos" />
            </ListItem>
            <ListItem button component={Link} className="" to="/app/grafico/envios"
              onClick={EliminarRegistrosVacios}
            >
              <ListItemIcon> <StarBorder /> </ListItemIcon>
              <ListItemText primary="Envios" />
            </ListItem>
            <ListItem button component={Link} className="" to="/app/grafico/ventas"
              onClick={EliminarRegistrosVacios}
            >
              <ListItemIcon> <StarBorder /> </ListItemIcon>
              <ListItemText primary="Ventas" />
            </ListItem>
          </List>
        </Collapse>

        <MenuItem hidden={!rolAdmi} button component={Link} to="/app/reportes"
          onClick={EliminarRegistrosVacios}
        >
          <ListItemText primary="Reportes"/>
        </MenuItem>

        <MenuItem hidden={!rolSuper} button component={Link} to="/app/buckup"
          onClick={EliminarRegistrosVacios}
        >
          <ListItemText primary="Buckup"/>
        </MenuItem>

      </MenuList> 
    </div>
  );
  const MenuSuperior = (
      <Toolbar>
            <IconButton
                aria-label="Open drawer"
                edge="start"
                onClick={handleDrawerToggle}
                className={classes.menuButton}
            >
                <MenuIcon />
            </IconButton>

            <Typography>
              Hampina Wassi - {local.nombreLocal} - {usuario.nombreTrabajador}
            </Typography>
            <span className={classes.toolbarButtons}>
              <IconButton color="inherit" className={classes.imagenPerfil}>
                <img src={usuario.urlMediaTrabajador} alt={usuario.altMediaTrabajador} className="card-img-top rounded-circle"/>
              </IconButton>
              <IconButton color="inherit" aria-label="More Options"
                aria-controls="long-menu"
                aria-haspopup="true"
                onClick={handleClick}
              >
                <MoreVertIcon />
              </IconButton>
              <Menu
                id="long-menu"
                anchorEl={anchorEl}
                keepMounted
                open={open}
                onClose={handleClose}
                PaperProps={{
                  style: {
                    maxHeight: ITEM_HEIGHT * 4.5,
                    width: 200,
                  },
                }}
              >
              {itemsMenuSuperior.map((item, index) => ( // ITEMS__MENU__LATERAL
                <MenuItem button key={index} component={Link} to={item.url}
                  onClick={EliminarRegistrosVacios}
                >
                  <ListItemIcon>{item.icono}</ListItemIcon>
                  <ListItemText primary={item.nombre} />
                </MenuItem >
                  ))}
              </Menu>
            </span>
        </Toolbar>
  );
  var codigoUsuarioC="";
  codigoUsuarioC = localStorage.getItem('codigoUsuario');
  if(codigoUsuarioC !== null && codigoUsuarioC !== "" && rolTienda){
    return (
      <React.Fragment>
      <div className={classes.root}>
        <CssBaseline />
        <AppBar position="fixed" className={classes.appBar}>        
          {MenuSuperior}
        </AppBar>
        <nav className={classes.drawer} aria-label="Mailbox folders">
          <Hidden smUp implementation="css">
            <Drawer
              container={container}
              variant="temporary"
              open={mobileOpen}
              onClose={handleDrawerToggle}
              classes={{
                paper: classes.drawerPaper,
              }}
              ModalProps={{
                keepMounted: true,
              }}
            >
              {MenuLateral}
            </Drawer>
          </Hidden>
          <Hidden xsDown implementation="css">
            <Drawer
              classes={{
                paper: classes.drawerPaper,
              }}
              variant="permanent"
              open
            >
              {MenuLateral}
            </Drawer>
          </Hidden>
        </nav>
        <main className={classes.content}>
          <div className={classes.toolbar} />
          {children}
          <Footer textoFooter="Copyright " nombreFooter="Tunki.com"></Footer>
        </main>
      </div>
      </React.Fragment>
    );
  }else{
    return (
      <div className={classes.root}>
        <main className={classes.content}>
          <div className={classes.toolbar} />
          {children}
          <Footer textoFooter="Copyright " nombreFooter="Tunki.com"></Footer>
        </main>
      </div>    
  );}
}

export default Dashboard;