import React, { Component } from 'react'
import { get_Usuario_Conexiones } from '../../Data/UsuarioData';
import ReactTable from 'react-table';
import "react-table/react-table.css";

export class UsuarioConexiones extends Component {
    constructor(props){
        super(props);
        this.state = {
            codigoUsuario: props.codigoUsuario,
            conexionesUsuario:[]
        }
    }
    listarConexiones(){
        const { codigoUsuario } = this.state;
        get_Usuario_Conexiones(codigoUsuario)
        .then(res => {
            this.setState({ conexionesUsuario : res });
        });
    }
    componentDidMount(){
        this.listarConexiones();
    }
    render() {
        const columns = [
            {
                Header: "LOCAL",
                accessor: "nombreLocal",
                style:{
                    textAlign: "center"
                }
            },
            {
                Header: "ESTADO",
                Cell: props => {
                    if(props.original.conexionUsuario === props.original.estadoSesion){
                        if(props.original.estadoSesion){
                            return(<span>Conectado</span>)
                        }                       
                    }else{
                        return(
                        <span>Desconectado</span>)
                    }
                }
            },
            {
                Header: "F. INGRESO",
                accessor: "fechaIngreso",
                style:{
                    textAlign: "center"
                }
            },
            {
                Header: "F. SALIDA",
                accessor: "fechaSalida",
                style:{
                    textAlign: "center"
                }
            },
        ]
        return (
            <div>
                <ReactTable
                    columns = {columns}
                    noDataText = {"Cargando Datos..."}
                    data = {this.state.conexionesUsuario}
                    filterable
                    defaultPageSize={5}
                    showPaginationTop={true}
                    showPaginationBottom={false}
                >
                </ReactTable>
            </div>
        )
    }
}

export default UsuarioConexiones
