import React from 'react';
import '../Styles/Pagination.css';

var numeroRender = 0; // El numero que se dibujara la paginacion

const Paginacion = ( { totalElementos , elementosPage , cambiarPagina , paginaActual}) => {

    const cantidadItems = 9; // Cantidad de items (botones) en la paginacion

    const pageNumbers = [];
    var itemsPage = [];
    var classItem = "";
    var mostrarUnaVes = true;
    var cantidadPages = Math.trunc( totalElementos / elementosPage );
    var paginaActualPage = paginaActual;

    var cantidadRender = 0; // Cantidad de veces que se dibujara la paginacion

    if((totalElementos % elementosPage) !== 0 ){ // Si Cantidad Imparar
        cantidadPages = cantidadPages + 1;// Se aumenta uno para los Productos sobrantes que no se muestran por ser (Impar)
        cantidadRender = Math.trunc( cantidadPages / cantidadItems ); // La cantidad de veces que se puede dibujar la paginacion
        if((cantidadRender % cantidadItems) !== 0){ // Si la cantidad de veces a dibujar es impar
            cantidadRender = cantidadRender + 1;
        }

        if(cantidadRender === 0){
            cantidadRender = 1;
        }
    }

    for(var i = 1; i <= cantidadPages; i++){
        pageNumbers.push(i); // LLenar la paginacion con todas las paginas
    }

    function dibujarPaginacion () { // La numero de renderizacion de la paginacion
        itemsPage = [];
        for(var i = (cantidadItems * numeroRender) + 1; i <= (cantidadItems * (cantidadPages + 1)); i++){
            if(i <= cantidadPages){
                itemsPage.push(i);// Llenar la paginacion a dibujar con paginas
            }
        }
    }
    
    dibujarPaginacion();

    function paginaPrimera () {
        cambiarPagina(1);
        numeroRender = 0;
        dibujarPaginacion();
    }

    function paginaUltimo () {
        cambiarPagina(cantidadPages);
        numeroRender = cantidadRender - 1;
        dibujarPaginacion();
    }

    function paginaSiguiente () {
        if(paginaActualPage < cantidadPages){ //Si la pagina actual es menor al total de paginas
            cambiarPagina(paginaActualPage + 1);//Aumentara en uno
        }

        if(paginaActualPage === ( cantidadItems * ( numeroRender + 1 ))){ // Si esta en la ultima pagina de la paginacion
            if(cantidadRender > numeroRender){// Si no estamos en el ultimo numeroRender de paginacion
                numeroRender = numeroRender + 1;
                dibujarPaginacion();
            }
        }

    }

    function paginaAnterior () {
        if(paginaActualPage > 1){//Si la pagina actual es mayor que uno
            cambiarPagina(paginaActualPage - 1);// Restara en uno 
        }

        if(paginaActual === ((cantidadItems * numeroRender) + 1)){ // Si esta en el primera pagina de la paginacion
            if(numeroRender > 0){ // Si no estamos en la primer numeroRender de paginacion
                numeroRender = numeroRender - 1;
                dibujarPaginacion();
            }
        }
    }

    function mostrarContinuar (mostrar) {
        if(mostrar){
            return(
            <li className="page-item">
                <a className="page-link" href="# ">...</a>
            </li>)
        }else{
            return null;
        }
    }

    return(
        <div>
            <nav>
                <ul className="pagination justify-content-center">
                    <li className="page-item">
                        <a className="page-link" href="# " aria-label="Previous"
                            onClick = { () => paginaPrimera() }
                        >
                            <span aria-hidden="true">&laquo;</span>
                        </a>
                    </li>
                    <li className="page-item">
                        <a className="page-link" href="# " aria-label="Previous"
                            onClick = { () => paginaAnterior() }
                        >
                            <span aria-hidden="true">&lsaquo;</span>
                        </a>
                    </li>
                    {itemsPage.map(numero => {
                        if(numero === paginaActualPage){
                            classItem = "page-item active";
                        }else{
                            classItem = "page-item";
                        }
                        if(numero <= (cantidadItems * (numeroRender + 1))){
                            return (
                                <li key={numero} className={classItem}>
                                    <a href="# " className="page-link"
                                        onClick = { () => cambiarPagina(numero) }
                                    >{numero}</a>
                                </li>
                            )
                        }else{
                            if(mostrarUnaVes){
                                mostrarUnaVes = false;
                                return( mostrarContinuar(true) )
                            }else{
                                return null;
                            }
                        }
                    })}
                    <li className="page-item">
                        <a className="page-link" href="# " aria-label="Next"
                            onClick = { () => paginaSiguiente() }
                        >
                            <span aria-hidden="true">&rsaquo;</span>
                        </a>
                    </li>
                    <li className="page-item">
                        <a className="page-link" href="# " aria-label="Next"
                            onClick = { () => paginaUltimo() }
                        >
                            <span aria-hidden="true">&raquo;</span>
                        </a>
                    </li>
                </ul>
            </nav>
        </div>
    )
}

export default Paginacion
