// INICIO SESION API FROM SERVER NODE EXPRESS

import data from '../../db_data';
const BaseUrl = data + '/api/sesiones/';

// CREAR SESION EN EL SISTEMA
export function post_Sesion_Login(usuarioData){ 
    const urlFetch = BaseUrl + 'login';
    return new Promise((resolve,reject)=>{
        fetch(urlFetch,{
            method:'POST',
            body: JSON.stringify(usuarioData),
            headers: new Headers({
                'Content-type':'application/json'
            })
        })
        .then((response) => response.json())
        .then((responseJson) => {
            resolve(responseJson);
        })
        .catch((error)=>{
            reject(error);
        });
    });
}

//CERRAR SESION DE USUARIO EN SISTEMA
export function put_Sesion_Cerrar(codigoUsuario){
    const urlFetch = BaseUrl + "logout";
    return new Promise((resolve,reject)=>{
        fetch(urlFetch,{
            method:'PUT',
            body: JSON.stringify(codigoUsuario),
            headers: new Headers({
                'Content-type':'application/json'
            })
        })
        .then((response) => response.json())
        .then((responseJson) => {
            resolve(responseJson);
        })
        .catch((error)=>{
            reject(error);
        });
    });
}


