import React, { Component } from 'react'
import ReactTable from 'react-table';
import "react-table/react-table.css";
import { Modal , ModalHeader, ModalBody } from 'reactstrap';

import { get_Venta_Local } from '../../Data/VentaData';
import { get_Venta_Detalle_Codigo_Venta_Vista } from '../../Data/VentaDetalleData';

import Menu from '../../Pages/menu';

const estadoInicial = { 
    ventasRealizadas:[],
    detallesVenta:[],

    codigoLocal : localStorage.getItem("codigoLocal"),
    modalDetalleOpen: false,
}

export class VentaLista extends Component {

    constructor(props){
        super(props);
        this.state = estadoInicial;
    }

    toogleModalDetalle = () => {
        this.setState({ modalDetalleOpen : ! this.state.modalDetalleOpen });
    }

    mostrarDetalleVenta = (codigoVenta) => {
        this.listarDetallesVenta(codigoVenta);
        this.toogleModalDetalle();
    }

    listarVentasRealizadas = () => {
        get_Venta_Local(this.state.codigoLocal).then( res => {
            console.log(res);
            this.setState({ ventasRealizadas : res });
        });
    }

    listarDetallesVenta = (codigoVenta) => {
        get_Venta_Detalle_Codigo_Venta_Vista(codigoVenta).then( res => {
            this.setState({ detallesVenta : res });
        });
    }

    componentDidMount(){
        this.listarVentasRealizadas();
    }

    render() {

        const columns = [
            {
                Header: "CODIGO",
                accessor: "codigoVenta",
                style:{
                    textAlign: "center"
                }
            },
            {
                Header: "CLIENTE",
                accessor: "nombreCliente",
                style:{
                    textAlign: "center"
                }
            },
            {
                Header: "TRABAJADOR",
                accessor: "nombreTrabajador",
                style:{
                    textAlign: "center"
                }
            },
            {
                Header: "FECHA",
                style:{
                    textAlign: "center"
                },
                Cell: props =>{
                    var fecha = new Date(props.original.fechaRegistro)
                    return(
                        <label>
                            {
                                fecha.getFullYear() + "/" + (fecha.getMonth() + 1) + "/" + fecha.getDate()
                                + " -- " + fecha.getHours() + ":" + fecha.getMinutes()
                            }
                        </label>
                )}
            },
            {
                Header: "TOTAL",
                style:{
                    textAlign: "center"
                },
                Cell: props => {
                    return(
                        <label>{ "S/." +(props.original.valorTotalVenta).toFixed(2)  }</label>
                    )
                }
            },
            {
                Header: "DETALLE",
                style:{
                    textAlign: "center"
                },
                Cell: props => {
                    return(
                        <div className="container">
                            <button
                                className="btn btn-info"
                                onClick={() => { this.mostrarDetalleVenta(props.original.codigoVenta)}}
                            >Ver</button>
                        </div>
                    )
                }
            },

        ]
        const columnsDetalleVenta = [
            {
                Header:"IMAGEN",
                Cell: props =>{
                    return(
                        <div className="media_producto">
                            <img src={props.original.urlMedia} alt={props.original.descripcionProducto} className="card-img-top"/>
                        </div>
                    )
                },
                width:100,
                minWidth:100,
                maxWidth:100
            },
            {
                Header: "PRODUCTO",
                accessor:"descripcionProducto",
                style:{
                    textAlign: "center"
                }
            },
            {
                Header: "PRESENTACI√ìN",
                accessor : "descripcionPresentacion",
                style:{
                    textAlign: "center"
                }
            },
            {
                Header: "STOCK",
                accessor : "stockExistente",
                style:{
                    textAlign: "center"
                }
            },
            {
                Header: "VAL. UNIDAD",
                accessor:"valorPrecio",
                style:{
                    textAlign: "center"
                }
            },
            {
                Header: "CANT.",
                accessor: "cantidadProducto",
                style:{
                    textAlign: "center"
                }
            },
            {
                Header: "VAL. TOTAL",
                style:{
                    textAlign: "center"
                },
                Cell: props => {
                    var valorTotalVD = props.original.valorTotalVD;
                    return(
                        <input className="form-control text-center" id= "descripcionProducto" type="text" readOnly
                            value = {valorTotalVD}> 
                        </input>
                    )
                }
            },
        ]
        return (
            <Menu>
            <div className="container p-4">
                <div className="card">
                    <div className="card-header">
                        <h3> Ventas Realizadas </h3>
                    </div>
                    <div className="card-body">
                        <ReactTable
                            columns = {columns}
                            noDataText = {"Cargando Datos..."}
                            data = {this.state.ventasRealizadas}
                            defaultPageSize={5}
                        >
                        </ReactTable>
                    </div>
                </div>
                <Modal 
                    isOpen={this.state.modalDetalleOpen}
                    centered={true}
                    size={"xl"}
                    className={"pl-5"}
                >
                    <ModalHeader toggle={this.toogleModalDetalle.bind(this)}>Dellate De Venta</ModalHeader>    
                    <ModalBody>
                        <div className="container">
                            <div className="row justify-content-center text-center">
                                <div className="col-md-12">
                                    <ReactTable
                                    columns = {columnsDetalleVenta}
                                    noDataText = {"Cargando Datos..."}
                                    data = {this.state.detallesVenta}
                                    defaultPageSize={5}
                                    showPaginationTop={true}
                                    showPaginationBottom={false}
                                    >
                                    </ReactTable>
                                </div>
                            </div>
                        </div>
                    </ModalBody>
                </Modal>
            </div>
            </Menu>
        )
    }
}

export default VentaLista
