// TRABAJADOR API FROM SERVER NODE EXPRESS

import data from '../../db_data';
const BaseUrl = data + '/api/trabajadores/';

// LISTAR TRABAJADORES
export function get_Trabajador_Lista(){ 
    const urlFetch = BaseUrl;
    return new Promise((resolve,reject)=>{
        fetch(urlFetch)
        .then((response) => response.json())
        .then((responseJson) => {
            resolve(responseJson);
        })
        .catch((error)=>{
            reject(error);
        });
    });
}
 
// LISTAR TRABAJADOR POR CODIGO
export function get_Trabajador(codigoTrabajador){ 
    const urlFetch = BaseUrl + codigoTrabajador;
    return new Promise((resolve,reject)=>{
        fetch(urlFetch)
        .then((response) => response.json())
        .then((responseJson) => {
            resolve(responseJson);
        })
        .catch((error)=>{
            reject(error);
        });
    });
}

//ELIMINAR TRABAJADOR CODIGO
export function del_Trabajador(codigoTrabajador){ 
    const urlFetch = BaseUrl + codigoTrabajador;
    return new Promise((resolve,reject)=>{
        fetch(urlFetch,{
            method:'DELETE',
            headers: new Headers({
                'Content-type':'application/json'
            })
        })
        .then((response) => response.json())
        .then((responseJson) => {
            resolve(responseJson);
        })
        .catch((error)=>{
            reject(error);
        });
    });
}

//GUARDAR TRABAJADOR
export function post_Trabajador(Trabajador){ 
    const urlFetch = BaseUrl;
    return new Promise((resolve,reject)=>{
        fetch(urlFetch,{
            method:'POST',
            body: JSON.stringify(Trabajador),
            headers: new Headers({
                'Content-type':'application/json'
            })
        })
        .then((response) => response.json())
        .then((responseJson) => {
            resolve(responseJson);
        })
        .catch((error)=>{
            reject(error);
        });
    });
}

//MODIFICAR TRABAJADOR
export function put_Trabajador(Trabajador){ 
    const urlFetch = BaseUrl;
    return new Promise((resolve,reject)=>{
        fetch(urlFetch,{
            method:'PUT',
            body: JSON.stringify(Trabajador),
            headers: new Headers({
                'Content-type':'application/json'
            })
        })
        .then((response) => response.json())
        .then((responseJson) => {
            resolve(responseJson);
        })
        .catch((error)=>{
            reject(error);
        });
    });
}
