import React, { Component } from 'react';

class ExportExcel extends Component{
    render(){
        return(
            <div className="container row">
                <div>
                    <table hidden={true} id="table-to-xls">
                        <thead>
                            <tr>
                                <th>TPD ID</th>
                                <th>Codigo</th>
                                <th>Nombre</th>  
                                <th>Trabajador</th>
                                <th>Ultimo Ingreso</th>
                            </tr>                
                        </thead>
                        <tbody>
                        {
                            this.props.usuarios.map(usu =>{
                                return(
                                    <tr key={usu.id}>
                                        <td>{usu.id}</td>
                                        <td>{usu.codigoUsuario}</td>
                                        <td>{usu.nombreUsuario}</td>                                        
                                        <td>{usu.codigoTrabajador}</td>       
                                        <td>{usu.ultimoIngreso}</td>     
                                    </tr>
                                );    
                            })
                        }
                        </tbody>
                    </table>
                </div>
            </div>
        );
    }
}

export default ExportExcel