import React, { Component} from 'react';
import ReactTable from 'react-table';
import "react-table/react-table.css";

import ReactHTMLTableExcel from 'react-html-table-to-excel';

import { Modal , ModalHeader, ModalBody, Alert } from 'reactstrap';

import data from '../../../db_data';
import ExportToExcel from "../../Export/excel/media";
import "./Media.css";

import { get_Media, get_Media_Lista , post_Media , post_Media_File ,put_Media , del_Media, del_Media_File } from '../../Data/MediaData';
import { get_Tipo_Media_Nombre } from '../../Data/tiposData';

import Menu from '../../Pages/menu';

export default class Media extends Component {

    BaseUrl = data + '/api/medias';

    constructor (props){
        super(props);
        this.state = {
            media:{},

            nombreFile:"",
            file:null,
            
            medias:[],
            filtered:[],
            mediasExcel:[],
            message : {
                text:'',
                alert: ''
            },
            messageActivo:false,

            codigoMedia:"",
            nombreMedia:"",
            codigoTipoMedia:"",
            urlMedia:"",



            
            mediaGuardar : {
                codigoMedia : "",
                nombreMedia : "",
                altMedia : "",
                urlMedia : "",
                codigoTipoMedia : "",
                codigoUsuario : ""
            },

            
            tipoAccion:"post",
            modalOpen: false,
            tituloModal:"",

            modalConfirmarOpen: false,
            tipoEliminar : "",
            codigoMediaEliminar : "",
        }
    }

    toogleModalConfirmar = () => {
        this.setState({ modalConfirmarOpen : ! this.state.modalConfirmarOpen });
    }

    mostrarModal = (codigoMedia) => {
        this.setState({ codigoMediaEliminar : codigoMedia });
        this.toogleModalConfirmar();
    }

    borrarConfirmado = () => {
        const { codigoMediaEliminar } = this.state;
        this.eliminarMedia(codigoMediaEliminar);
        this.toogleModalConfirmar();
    }

    mostrarAlerta = (tiempo) => {
        setTimeout(()=>{
            this.setState({ message : { text : '' , alert : ''} });            
        },tiempo);
    }

    onFilteredChangeCustom = (value, accessor) => {
        let filtered = this.state.filtered;
        let insertNewFilter = 1;
    
        if (filtered.length) {
          filtered.forEach((filter, i) => {
            if (filter["id"] === accessor) {
              if (value === "" || !value.length) filtered.splice(i, 1);
              else filter["value"] = value;
              insertNewFilter = 0;
            }
          });
        }
    
        if (insertNewFilter) {
          filtered.push({ id: accessor, value: value });
        }
    
        this.setState({ filtered: filtered });
      };
    

    listarMedia(codigoMedia){
        if(codigoMedia !== "" && codigoMedia !== null){
            get_Media(codigoMedia).then(res => {
                this.setState({ medias : res })
            });
        }else{
            get_Media_Lista().then(res => {
                this.setState({ medias : res})
            });
        }
    }

    cambiarAlerta(message){
        this.setState({ message:message });
        this.mostrarAlerta(3000);  
    }

    setMediaGuardar(){

    }

    buscarTipoMediaNombre = (nombreBusqueda) =>{

        get_Tipo_Media_Nombre(nombreBusqueda).then((result)=>{
            let mediaGuardarTmp = JSON.parse(JSON.stringify(this.state.mediaGuardar));
            mediaGuardarTmp.codigoTipoMedia = result[0].codigoTipoMedia           
            this.setState({  mediaGuardar:mediaGuardarTmp});
        });

    }
    guadarFile = (file) => {
        post_Media_File(file).then((result)=>{
            let mediaGuardarTmp = JSON.parse(JSON.stringify(this.state.mediaGuardar));
            mediaGuardarTmp.urlMedia = "/img/" + result.filename;           
            this.setState({  mediaGuardar : mediaGuardarTmp});
        });
    }
    
    crearMediaObjecto = () =>{
        const { file , nombreMedia , codigoMedia } = this.state;
        const codigoUsuario  = localStorage.getItem('codigoUsuario');
        
        const nombreBusqueda = file.type.replace("/","-");
        this.buscarTipoMediaNombre(nombreBusqueda);
        this.guadarFile(file);

        let mediaGuardarTmp = JSON.parse(JSON.stringify(this.state.mediaGuardar));
        mediaGuardarTmp.codigoMedia = codigoMedia;
        mediaGuardarTmp.nombreMedia = nombreMedia;
        mediaGuardarTmp.altMedia = nombreMedia;
        mediaGuardarTmp.codigoUsuario = codigoUsuario;
            
        this.setState({  mediaGuardar:mediaGuardarTmp });
    }

    guardarMedia = () =>{
        const { file , nombreMedia , codigoMedia , codigoTipoMedia } = this.state;

        const codigoUsuario  = localStorage.getItem('codigoUsuario');

        const mediaGuardar = {
            codigoMedia : codigoMedia,
            nombreMedia : nombreMedia,
            altMedia : nombreMedia,
            urlMedia : "/img/" + file.name,
            codigoTipoMedia : codigoTipoMedia,
            codigoUsuario : codigoUsuario
        };
        const message = {
            text:"",
            alert:""  
        };
        switch (this.state.tipoAccion) {
            case "post":
                post_Media(mediaGuardar).then((result)=>{
                    message.alert = "success";
                    message.text = "Se guardo correctamente";
                    this.toogleModal();
                });
                break;
            case "put":
                put_Media(mediaGuardar).then((result)=>{
                    message.alert = "success";
                    message.text = "Se modifico correctamente";
                    this.toogleModal();
                });
                break;
            default:
                break;
        }

        post_Media_File(file).then((result)=>{
        });

        this.cambiarAlerta(message);

    }

    eliminarMedia(codigoMedia){
        const message = {
            text:"Algo salio mal vuelve a intertarlo",
            alert:"danger"  
        };
        if(codigoMedia !== "" && codigoMedia !== null){
            get_Media(codigoMedia).then(res=>{
                del_Media_File(res[0].urlMedia.replace("/img/","")).then(res => {
                });
            });

            del_Media(codigoMedia).then(res => {
                message.alert = "success";
                message.text = "Se elimino correctamente";
                this.listarMedia("");
            });
        }
        this.cambiarAlerta(message);
    }

    setValues = (e , field) => {       
        this.setState({ [field]: e.target.value });
    }

    setFileMedia = e => {
        let file = e.target.files[0];        
        const nombreBusqueda = file.type.replace("/","-");
        get_Tipo_Media_Nombre(nombreBusqueda).then((result)=>{
            this.setState({
                file : file,
                codigoTipoMedia : result[0].codigoTipoMedia,
                nombreFile : file.name
            });
            
        });
    }

    verificarAccion(codigoMedia,tituloModal){
        if(codigoMedia !== null && codigoMedia !== ""){
            get_Media(codigoMedia).then(res =>{
                if(res[0]){    
                    this.setState({
                        media:res[0],
                        tipoAccion:"put",        
                        tituloModal: tituloModal
                    });
                }
            });
        }else{
            this.setState({ 
                media:{},
                tipoAccion:"post",
                tituloModal: tituloModal
            });
        }
        this.toogleModal();
    }
    toogleModal(){   
        this.setState({
            modalOpen : ! this.state.modalOpen,
        });
        if(!this.state.modalOpen){
            this.listarMedia("");
        }        
    }

    exportarToExcel(){
        
    }

    componentDidMount(){
        this.listarMedia("");
    }
    render() {
        const columns = [
            {
                Header: "ID",
                accessor: "id",
                style:{
                    textAlign: "center"
                },
                show:false
            },
            {
                Header: "CODIGO",
                accessor: "codigoMedia",
                style:{
                    textAlign: "center"
                },
                show:false
            },
            {
                Header: "Imagen",
                Cell: props =>{
                    return(
                        <div className="media_file">
                            <img src={props.original.urlMedia} alt={props.original.altMedia} className="card-img-top"/>
                        </div>
                    )
                },
                sortable: false,
                filterable: false,
                width:100,
                minWidth:100,
                maxWidth:100
            },
            {
                Header: "MEDIA",
                accessor: "nombreMedia",
                style:{
                    textAlign: "center"
                }
            },
            {
                Header: "ALT",
                accessor: "altMedia",
                sortable: false,
                filterable: false,
                style:{
                    textAlign: "center"
                },
                show:false
            },
            {
                Header: "URL",
                accessor: "urlMedia",
                sortable: false,
                filterable: false,
                style:{
                    textAlign: "center"
                }
            },
            {
                Header: "TIPO",
                accessor: "nombreTipoMedia",
                sortable: false,
                filterable: false,
                style:{
                    textAlign: "center"
                }
            },
            {
                Header: "USUARIO",
                accessor: "nombreUsuario",
                sortable: false,
                filterable: false,
                style:{
                    textAlign: "center"
                },
                show:false
            },
            {
                Header:"ACCIONES",
                Cell: props => {
                    return(
                        <div className="container">
                            <div className="row justify-content-center">
                                <button className="btn btn-danger"
                                        onClick={() =>{
                                            this.mostrarModal(props.original.codigoMedia);
                                        }}
                                >Borrar</button>
                            </div>

                        </div>
                    )
                },                
                sortable: false,
                filterable: false
            }
        ]
        let buttonCard = {
            cursor: 'pointer'
        };
        return (
            <Menu>
            <div className="container p-4">
                {/*<MaterialTable></MaterialTable>*/}
                <div className="row">
                    <div className="col-md-11 mx-auto">
                        <div className="card ">
                            <div className="card-header">
                                <h3>Medias en Existencia</h3>
                            </div>
                            <div className="m-2">
                                { this.state.message.text !==''?( <Alert color={this.state.message.alert}>
                                { this.state.message.text} </Alert> ): '' }
                            </div>                            
                            <div className="row m-2">
                                <div className="col-sm" style={buttonCard}>
                                    <div className="card text-white bg-info"
                                        onClick={()=>{
                                            this.verificarAccion("","Registrar")
                                            //this.ver();
                                        }}>
                                        <div className="card-header">Registrar</div>
                                        <button className="btn card-body text-white text-justify">Archivo Media.</button>
                                    </div>
                                </div>
                                
                                <div className="col-sm" style={buttonCard}>
                                    <div className="card bg-success text-white">
                                        <div className="card-header">Exportar</div>
                                        <ReactHTMLTableExcel
                                            id="test-table-xls-button"
                                            className="btn card-body text-white text-justify"
                                            table="table-to-xls"
                                            filename="mediaExcel"
                                            sheet="hoja 1"
                                            buttonText="A Documento Excel."
                                        />
                                    </div>
                                </div>
                            </div>
                            <div className="card-body">
                                <ReactTable
                                    columns = {columns}
                                    noDataText = {"Cargando Datos..."}
                                    data = {this.state.medias}
                                    filterable
                                    filtered = {this.state.filtered}
                                    onFilteredChange = {(filtered, column, value) => {
                                        this.onFilteredChangeCustom(value, column.id || column.accessor);
                                      }}
                                      defaultFilterMethod={(filter, row, column) => {
                                        const id = filter.pivotId || filter.id;
                                        if (typeof filter.value === "object") {
                                          return row[id] !== undefined
                                            ? filter.value.indexOf(row[id]) > -1
                                            : true;
                                        } else {
                                          return row[id] !== undefined
                                            //? String(row[id]).indexOf(filter.value) > -1
                                            ? String(row[id].toLowerCase()).indexOf(filter.value.toLowerCase()) > -1                                        
                                            : true;
                                        }
                                      }}
                                    defaultPageSize={10}
                                    showPaginationTop={true}
                                    showPaginationBottom={false}
                                >
                                {(state, filtredData, instance)=>{
                                    this.reactTable = state.pageRows.map(
                                        medias => { return medias._original });
                                    return(
                                        <div>
                                            {filtredData()}
                                            <ExportToExcel medias = { this.reactTable }/>
                                        </div>
                                    )
                                }}
                                </ReactTable>
                            </div>
                            <Modal 
                                isOpen={this.state.modalOpen}
                                centered={true}
                                size={"xm"}
                            >
                                <ModalHeader toggle={this.toogleModal.bind(this)}>{this.state.tituloModal}</ModalHeader>    

                                <ModalBody>
                                    <div className="card ">
                                        <div className="card-header text-center">
                                            <h3>Media</h3>
                                        </div>
                                        <div className="card-body ">
                                            {/*<form>*/}
                                                <div className="form-group">
                                                    <label for="nombreMedia"> Nombre Archivo: </label>
                                                    <input className="form-control" id= "nombreMedia" type="text" placeholder="Ejemplo: Foto Producto"
                                                        value = {this.state.media.nombreMedia}
                                                        onChange = { e => this.setValues(e,'nombreMedia') }
                                                    />
                                                </div>
                                                <div className="form-group">
                                                    <label for="mediaProducto"> Archivo: </label>
                                                    <div class="custom-file">
                                                        <input type="file" class="custom-file-input" id="customFileLang" lang="es"
                                                        
                                                            onChange = { e => this.setFileMedia(e) }
                                                        />
                                                        <label data-browse="Buscar" class="custom-file-label" for="customFileLang">{ this.state.nombreFile}</label>
                                                    </div>
                                                </div>
                                                <div className="text-center">
                                                    <button className="btn btn-primary"
                                                        onClick={this.guardarMedia}
                                                    > REGISTRAR </button>
                                                </div>
                                            {/*</form>*/}
                                        </div>
                                    </div>
                                </ModalBody>
                            </Modal>

                            <Modal 
                                    isOpen={this.state.modalConfirmarOpen}
                                    centered={true}
                                    size={"xm"}
                            >
                                <ModalHeader toggle={this.toogleModalConfirmar.bind(this)}>Alerta</ModalHeader>    
                                <ModalBody>
                                    <div className="container">
                                        <div className="row justify-content-center text-center">
                                            <div className="col-md-12 mb-4">
                                                <span>Se eliminar√° el Registro, ¬øDesea Continuar? </span>
                                            </div>
                                            <div className="col-md-3">
                                                <button className="btn btn-danger"
                                                    onClick={this.borrarConfirmado}
                                                >Eliminar</button>
                                            </div>
                                            <div className="col-md-3">
                                                <button className="btn btn-info"
                                                    onClick = {this.toogleModalConfirmar.bind(this)}
                                                >Cancelar</button>
                                            </div>
                                        </div>
                                    </div>
                                </ModalBody>
                            </Modal>

                        </div>
                    </div>
                </div>
            </div>
            </Menu>
        )
    }
}
