import React, { Component } from 'react'
import ReactTable from 'react-table';
import "react-table/react-table.css";
import "./Traslado.css";

import Divider from '@material-ui/core/Divider';

//import { get_Producto_Precio_Venta_Local_Lista } from '../../Data/productoData';
import { get_Producto_Local_Venta_Lista, get_Producto_Venta } from '../../Data/productoData';


import { post_Traslado_Vacio , put_Traslado , del_Traslado } from '../../Data/TrasladoData';
import { get_Local, get_Local_Tipo_Lista } from '../../Data/LocalData';
import { post_Traslado_Detalle , post_Traslado_Detalle_Temporal ,
        put_Traslado_Detalle_Temporal , del_Traslado_Detalle_Envio,
        del_Traslado_Detalle_CodigoTD } from '../../Data/TrasladoDetalleData';
import { Modal , ModalHeader, ModalBody } from 'reactstrap';

import Menu from '../../Pages/menu';

import { DeleteForeverOutlined , DoneOutlineRounded , RemoveShoppingCartOutlined } from '@material-ui/icons';

const estadoInicial = {

    productosVenta : [],
    sugerenciasProducto:[],

    detallesEnvio: [],

    locales:[],
    local:{}, // Local origen

    nombreProductoBuscar:"",
    tipoBuscadorProducto:"descripcionProducto",

    nombreTiendaDestino : "",

    codigoTraslado : "",

    traslado:{
        id : "",
        codigoTraslado : "",
        codigoUsuario : localStorage.getItem("codigoUsuario"),
        codigoTipoTraslado : "" ,
        origenTraslado : localStorage.getItem("codigoLocal"),
        destinoTraslado : "",
        fechaVencimiento : "",
        valorTotal : "",
        observacionOrigen : "",
        observacionDestino : "",
        fechaRegistro : "",
        codigoUsuarioConfirmacion : "",
        confirmacion : 0,
        fechaConfirmacion : "",
    },

    codigoTDTemporal : "",

    fechaVencimiento : "",
    observacionOrigen : "",
    valorTotalEnvio:0,

    confirmarCrearTraslado: false,

    confirmarEliminar : false,
    tipoAccion : false,

    detalle: {},

    totalProducto : 0,

    mensajeAlerta : {
        textoMensaje : "",
        tipoBotonConfirmar : "",
        textoBotonConfirmar : "",
        mostrarConfirmar : false,
        tipoBotonCancelar : "",
        textoBotonCancelar : "",
        mostrarCancelar : false,
    }
}

export class Envio extends Component {

    constructor(props){
        super(props);
        this.state = estadoInicial;
    }

    obtenerFechaHoy(){
        var fechaHoy = new Date();
        var a√±oDate = fechaHoy.getFullYear();
        var mesDate = fechaHoy.getMonth() + 1;
        var diaDate = fechaHoy.getDate();

        var fecha = a√±oDate + "-";
        if(mesDate < 10){
            fecha = fecha + "0" + mesDate;
        }else{
            fecha = fecha +""+ mesDate;
        }
        fecha = fecha + "-";
        if(diaDate < 10){
            fecha = fecha + "0" + diaDate;
        }else{
            fecha = fecha + "" + diaDate;
        }

        this.setState({
            traslado : {
                ...this.state.traslado,
                fechaVencimiento : fecha
            },
            fechaVencimiento : fecha
        });
    }

    buscarIndexProductos = (producto) => {
        const { productosVenta } = this.state;
        const index = productosVenta.indexOf(producto);
        return index;
    }

    toogleModalConfirmar = () => {
        this.setState({ modalConfirmarOpen : ! this.state.modalConfirmarOpen });
    }

    crearMensajeAlerta = (mensajeAlertaParam) => {
        const { mensajeAlerta } = this.state;
        mensajeAlerta["textoMensaje"]=mensajeAlertaParam.textoMensaje;
        mensajeAlerta["tipoBotonConfirmar"]=mensajeAlertaParam.tipoBotonConfirmar;
        mensajeAlerta["textoBotonConfirmar"]=mensajeAlertaParam.textoBotonConfirmar;
        mensajeAlerta["tipoBotonCancelar"]=mensajeAlertaParam.tipoBotonCancelar;
        mensajeAlerta["textoBotonCancelar"]=mensajeAlertaParam.textoBotonCancelar; 
        mensajeAlerta["mostrarCancelar"]=mensajeAlertaParam.mostrarCancelar;
        this.setState({ mensajeAlerta });
    }

    verificarEliminar = (detalle, tipoAccion) => {
        const { mensajeAlerta } = this.state;

        switch (tipoAccion) {
            case "ELIMINAR-DETALLE":
                    mensajeAlerta["textoMensaje"]="Se eliminar√° el Registro, ¬øDesea Continuar?";
                    mensajeAlerta["tipoBotonConfirmar"]="btn btn-danger";
                    mensajeAlerta["textoBotonConfirmar"]="Eliminar";
                    mensajeAlerta["mostrarConfirmar"]=true;
                    mensajeAlerta["tipoBotonCancelar"]="btn btn-info";
                    mensajeAlerta["textoBotonCancelar"]="Cancelar"; 
                    mensajeAlerta["mostrarCancelar"]=true;
                break;
            case "ANULAR-TRASLADO":
                    mensajeAlerta["textoMensaje"]="Se anular√° el Envio, ¬øDesea Continuar?";
                    mensajeAlerta["tipoBotonConfirmar"]="btn btn-danger";
                    mensajeAlerta["textoBotonConfirmar"]="Anular";
                    mensajeAlerta["mostrarConfirmar"]=true;
                    mensajeAlerta["tipoBotonCancelar"]="btn btn-info";
                    mensajeAlerta["textoBotonCancelar"]="Cancelar";
                    mensajeAlerta["mostrarCancelar"]=true;
                break;
                default:
                    break;
        }
        this.setState({ detalle : detalle , tipoAccion : tipoAccion , mensajeAlerta : mensajeAlerta});
        this.toogleModalConfirmar();
    }

    botonConfirmado = () => {
        const { tipoAccion } = this.state;
        if(tipoAccion === "ELIMINAR-DETALLE"){
            this.borrarDetalleTrasladoTemporal();
            this.toogleModalConfirmar();
        }
        if(tipoAccion === "ANULAR-TRASLADO"){
            this.anularTraslado();
        }
        if(tipoAccion === "CONFIRMAR-ENVIO"){
            this.confirmarGuardarEnvio();
            this.toogleModalConfirmar();
        }
    }

    setTraslado = (e) => {

        const { traslado } = this.state;
        var { fechaVencimiento , observacionOrigen , nombreTiendaDestino } = this.state;
        traslado[e.target.id] = e.target.value;

        if(e.target.id === "fechaVencimiento"){
            fechaVencimiento = e.target.value;
        }
        if(e.target.id === "observacionOrigen"){
            observacionOrigen = e.target.value;
        }
        if(e.target.id === "destinoTraslado"){
            nombreTiendaDestino = e.target.options[e.target.selectedIndex].text;
        }

        this.setState({ traslado , fechaVencimiento , observacionOrigen , nombreTiendaDestino}); 
    }

    onChangeTipoBuscadorProducto = (event) => {
        const tipoBuscadorProducto = event.target.value;
        this.setState(() => ({ tipoBuscadorProducto }));
    }

    onChangeBuscadorProducto = (event) => {
        const textoBuscar =  event.target.value;
        let sugerenciasProducto = [];

        if(textoBuscar.length > 0){
            const regex = new RegExp(`^${textoBuscar}`, 'i');          
            sugerenciasProducto = this.state.productosVenta.sort().filter(producto => regex.test(producto[this.state.tipoBuscadorProducto]));
        }
        this.setState(()=>({ sugerenciasProducto , nombreProductoBuscar : textoBuscar }));
    }

    mostrarSugerenciasProductoLista () {
        const { sugerenciasProducto } =  this.state;
        if(sugerenciasProducto.length === 0){
            return null;
        }
        return (
            <div className="AutoCompleteText">
                <ul>
                    {sugerenciasProducto.map((item) => 
                        <li key={item.id}
                            onClick= {()=>
                                this.seleccionarProducto(item)
                            }
                        >
                            {item.descripcionProducto + " - " + item.detalleProducto}
                        </li>
                    )}
                </ul>
            </div>
        );
    }

    setValorTotalEnvio = () => {
        var valorTotalEnvio = 0;
        this.state.detallesEnvio.forEach(detalle => {
            valorTotalEnvio = valorTotalEnvio + detalle.valorTotalTD; 
        });
        this.setState({ valorTotalEnvio });
    }

    
    buscarIndexDetalles = (detalle) => {
        const { detallesEnvio } = this.state;
        const index = detallesEnvio.indexOf(detalle);
        return index;
    }


    crearTrasladoVacio(){
        const codigosCreacion = {
            codigoUsuario : localStorage.getItem("codigoUsuario"),
            codigoLocal : localStorage.getItem("codigoLocal")
        }
        post_Traslado_Vacio(codigosCreacion).then( res => {
            this.setState({
                codigoTraslado : res[0].codigoTrasladoGenerado,

            });
        });
    }

    verificarProductoAgregar = (Producto) => {
        const { detallesEnvio } = this.state;
        var existe = false;
        detallesEnvio.forEach(det => {
            if(det.codigoProducto === Producto.codigoProducto){
                existe = true;
            }
        });
        return existe;
    }

    seleccionarProducto (Producto) {
        const existeProducto = this.verificarProductoAgregar(Producto);
        if(!existeProducto){
            get_Producto_Venta(Producto.codigoProducto).then(res => {
                if(res[0].stockExistente > 0){
                    this.crearDetalleTrasladoTemporal(res[0] , 1);
                    this.setState(() => ({
                        nombreProductoBuscar : "",
                        sugerenciasProducto: []
                    }));
                }else{ alert("Sin stock Existente") }
            });
        }else{ alert("Producto Ya Fue Registrado") }

    }

    crearDetalleTraslado = (Detalle) => {
        put_Traslado_Detalle_Temporal(Detalle).then(res => {  });
    }
    
    crearDetalleTrasladoTemporal = (Producto , cantidad) => {

        const detalleTrasladoBD = {
            codigoTraslado : this.state.codigoTraslado,
            codigoProducto : Producto.codigoProducto,
            codigoPrecio : Producto.codigoPrecio
        }

        post_Traslado_Detalle_Temporal(detalleTrasladoBD).then(res =>{
            this.setState({
                codigoTDTemporal : res[0][0].codigoTD
            }, () =>{
                const detalleTraslado = {
                    urlMedia : Producto.urlMedia,
                    descripcionProducto : Producto.descripcionProducto,
                    descripcionPresentacion : Producto.descripcionPresentacion,
                    detalleProducto : Producto.detalleProducto,
                    nombreTipoProducto : Producto.nombreTipoProducto,
                    valorPrecio : Producto.valorPrecio,
                    cantidadProducto : cantidad,
                    stockExistente: Producto.stockExistente,
                    valorTotalTD : Producto.valorPrecio * cantidad,
                    codigoTD : this.state.codigoTDTemporal,
                    codigoProducto : Producto.codigoProducto,
                    codigoPrecio : Producto.codigoPrecio,
                    codigoTraslado : this.state.codigoTraslado,
                    codigoLocal : localStorage.getItem("codigoLocal")
                }
                this.setState({
                    detallesEnvio : this.state.detallesEnvio.concat(detalleTraslado),
                    totalProducto : this.state.totalProducto + detalleTraslado.cantidadProducto
                }, () => {
                    this.setValorTotalEnvio();
                });
            });
        });
    }

    editarDetalleTrasladoTemporal = (e , detalle) => { // SE RECIBE EL DETALLE DE TRASLADO
       
        const { totalProducto } = this.state;
        var totalPro = totalProducto;
        totalPro = totalPro - parseInt(detalle.cantidadProducto);

        const index = this.buscarIndexDetalles(detalle); // SE OBTIENE LA UBICACION DEL DETALLE
        const detalleEditar = Object.assign( {} , this.state.detallesEnvio[index] ); // SE CREA UN DETALLE DE VENTA A PARTIR DE LA LISTA DE DETALLES
        const cantidadProducto = e.target.value;
        detalleEditar.cantidadProducto = cantidadProducto; //Se asigna la cantidad de envio
        detalleEditar.valorTotalTD = detalle.valorPrecio * cantidadProducto;
        const detallesEnvio = Object.assign( [] , this.state.detallesEnvio);
        detallesEnvio[index] = detalleEditar;
        
        totalPro = totalPro + parseInt(detalleEditar.cantidadProducto);

        this.setState({ detallesEnvio : detallesEnvio , totalProducto: totalPro }, () => {
            this.setValorTotalEnvio();
        });
    }

    borrarDetalleTrasladoTemporal = () =>{
        const { detalle } = this.state;
        const index = this.buscarIndexDetalles(detalle);
        var detallesEnvio = Object.assign( [] , this.state.detallesEnvio);
        detallesEnvio.splice(index,1);
        this.setState({ detallesEnvio : detallesEnvio , totalProducto: this.state.totalProducto - detalle.cantidadProducto}, () => {
            this.setValorTotalEnvio();
        });
        del_Traslado_Detalle_CodigoTD(detalle.codigoTD).then( res => {
        });
    }

    listarProductosVenta(codigoLocal){
        get_Producto_Local_Venta_Lista(codigoLocal)
        .then( res => {
            this.setState({ productosVenta : res });
        });
    }

    listarLocales(){
        get_Local_Tipo_Lista("TIENDA" , this.state.traslado.origenTraslado).then( res => {
            this.setState({ locales : res });
        });
    }

    obtenerLocal(codigoLocal){
        get_Local(codigoLocal).then( res => {
            if(res[0]){
                this.setState({
                    local : res[0]
                });
            }
        });
    }

    llenarListas(codigoLocal){
        
        this.listarProductosVenta(codigoLocal);
        this.listarLocales();
    }

    guardarDetalleTraslado = (detalleTraslado) => {
        post_Traslado_Detalle(detalleTraslado).then(_=>{});
    }

    confirmarGuardarEnvio = () => {
        
        const { traslado , detallesEnvio } = this.state;
        traslado["valorTotal"] = this.state.valorTotalEnvio;
        traslado["codigoTraslado"] = this.state.codigoTraslado;
        traslado["codigoTipoTraslado"] = this.props.codigoTipoTraslado;
        
        detallesEnvio.forEach( detalle =>{
            this.crearDetalleTraslado(detalle);
        });

        put_Traslado(traslado).then( res => {
            if(res){
                this.resetEstado();
            }
        });
    }

    guardarEnvio = () => {
        const { detallesEnvio , mensajeAlerta } = this.state;
        var confirmarGuardar = true;

        mensajeAlerta["textoMensaje"]="Seleccione un Destino Correcto";
        mensajeAlerta["mostrarConfirmar"] = false;
        mensajeAlerta["tipoBotonCancelar"] = "btn btn-danger";
        mensajeAlerta["textoBotonCancelar"] = "Aceptar";
        mensajeAlerta["mostrarCancelar"] = true;
       

        if(this.state.traslado.destinoTraslado === this.state.traslado.origenTraslado){
            confirmarGuardar = false;
        }

        if(this.state.traslado.destinoTraslado === null || this.state.traslado.destinoTraslado === ""){
            confirmarGuardar = false;
        }

        if(confirmarGuardar){
            if(detallesEnvio.length > 0){
                mensajeAlerta["textoMensaje"]="Se enviar√° " + this.state.totalProducto + " Producto(s) a la " + this.state.nombreTiendaDestino;
                mensajeAlerta["tipoBotonConfirmar"] = "btn btn-success";
                mensajeAlerta["textoBotonConfirmar"] = "Confirmar";
                mensajeAlerta["mostrarConfirmar"] = true;
                mensajeAlerta["tipoBotonCancelar"] = "btn btn-danger";
                mensajeAlerta["textoBotonCancelar"] = "Cancelar";
                mensajeAlerta["mostrarCancelar"] = true;                
            }else{
                mensajeAlerta["textoMensaje"]="No se registro Productos Para el Envio";
                mensajeAlerta["mostrarConfirmar"] = false;
                mensajeAlerta["tipoBotonCancelar"] = "btn btn-danger";
                mensajeAlerta["textoBotonCancelar"] = "Aceptar";
                mensajeAlerta["mostrarCancelar"] = true;
            }
        }
        this.setState({ mensajeAlerta , tipoAccion : "CONFIRMAR-ENVIO" });
        this.toogleModalConfirmar();    
    }

    eliminarEnvio = (codigoTraslado) => {
        del_Traslado(codigoTraslado).then( res => {
        });
        del_Traslado_Detalle_Envio(codigoTraslado).then( res => {
        });
        this.resetEstado();
    }

    anularTraslado = () => {
        if(this.state.codigoTraslado.length > 0){
            this.eliminarEnvio(this.state.codigoTraslado);
        }
    }

    resetEstado = () => {
        this.setState(estadoInicial);
    }


    confirmarCrearEnvio = () => {
        window.addEventListener('beforeunload',(e) => {
            this.anularTraslado();
            e.returnValue = "Se cancelo el Envio, Desea Salir?";
        });
        this.setState({
            confirmarCrearTraslado : ! this.state.confirmarCrearTraslado
        },() => {
            //const codigoLocal = localStorage.getItem("codigoLocal");
            this.obtenerLocal(localStorage.getItem("codigoLocal"));
            this.llenarListas(localStorage.getItem("codigoLocal"));
            this.crearTrasladoVacio();
        });
    }

    componentDidMount(){
        this.obtenerFechaHoy();
    }

    render() {
        if(!this.state.confirmarCrearTraslado){
            return(
                <Menu>
                <div className="container p-5">
                    <div className="row justify-content-center">
                        <div className="col-md-4 mx-auto">
                            <button
                                className="form-control btn btn-success"
                                onClick={this.confirmarCrearEnvio}
                            >Confirmar Crear Envio</button>
                        </div>
                    </div>
                </div>
                </Menu>
            )
        }else{
            const { nombreProductoBuscar } =  this.state;
            const columns = [
                {
                    Header: "ITEM",
                    style:{
                        textAlign: "center"
                    },
                    Cell: props =>{
                        return(
                            <label>{props.original.codigoTD}</label>
                        )
                    },
                    show: false
                },
                {
                    Header:"IMAGEN",
                    Cell: props =>{
                        return(
                            <div className="">
                                <img src={props.original.urlMedia} alt={props.original.descripcionProducto} className="card-img-top media_producto"/>
                            </div>
                        )
                    },
                    maxWidth: 200
                },
                {
                    Header: "PRODUCTO",
                    style:{
                        textAlign: "center"
                    },
                    Cell: props => {
                        return(
                            <div className="mt-2">
                                <div>{props.original.descripcionProducto}</div>
                                <div>{props.original.detalleProducto}</div>
                                <div>{props.original.nombreTipoProducto}</div>
                                <div>{props.original.descripcionPresentacion}</div>
                                <div>Stock: {props.original.stockExistente}</div>
                                <div>Precio: S/.{(props.original.valorPrecio).toFixed(2)}</div>
                            </div>
                        )
                    },
                    width : 250,
                    minWidth: 150,
                    maxWidth: 450
                },
                {
                    Header: "CANTIDAD",
                    style:{
                        textAlign: "center"
                    },
                    Cell: props => {
                        var valorTotalTD = props.original.valorTotalTD;
                        return(
                            <div className="container text-center">
                                <div className="row justify-content-center mt-3">
                                    <div className="col-11 col-sm-10 col-md-9 form-group">
                                        <input className="form-control text-center" id= "cantidadProducto" type="number" min="1"
                                            max={""+props.original.stockExistente+""} placeholder={props.original.cantidadProducto}
                                            value={props.original.cantidadProducto}
                                            onChange = { e => { this.editarDetalleTrasladoTemporal( e , props.original)}}
                                        />   
                                    </div>
                                    <div className="col-11 col-sm-10 col-md-9 form-group">
                                        <button type="button" className="btn btn-danger btn-circle"
                                            onClick = {() => {this.verificarEliminar(props.original,"ELIMINAR-DETALLE")}}
                                        ><DeleteForeverOutlined/></button>   
                                    </div>
                                    <div hidden={true} className="col-10 form-group">
                                        <input className="form-control text-center" id= "descripcionProducto" type="text" readOnly
                                            value = {"S/. " + (valorTotalTD).toFixed(2)}> 
                                        </input>
                                    </div>
                                </div>
                            </div>                                                    
                        )
                    }
                },

            ]
            return (
                <Menu>
                <div className="container">
                    <div className="row justify-content-center">
                        <div className="col-md-7 mb-3">
                            <div className="">
                                <div className="card">
                                    <div className="card-header">
                                        <h3>Envio de Productos</h3>
                                    </div>
                                    <div className="card-body">
                                        <div className="row justify-content-center mx-1">
                                            <div className="col-10 col-sm-5 col-md-4 form-group">
                                                <select className="form-control custom-select"
                                                    onChange={this.onChangeTipoBuscadorProducto}
                                                >
                                                    <option value="descripcionProducto">DESCRIPCI√ìN</option>
                                                    <option value="codigoProducto">CODIGO</option>
                                                    <option value="detalleProducto">DETALLE</option>
                                                </select>
                                            </div>
                                            <div className="col-10 col-sm-7 col-md-8 form-group">
                                                <input className="form-control" type="text"
                                                    placeholder="Busqueda de Producto"
                                                    value={nombreProductoBuscar}
                                                    onChange={this.onChangeBuscadorProducto}
                                                    autoComplete={"off"}
                                                ></input>
                                                {this.mostrarSugerenciasProductoLista()}
                                            </div>
                                            <div className="col-md-12 mt-3">
                                                <ReactTable
                                                    columns = {columns}
                                                    noDataText = {"Cargando Datos..."}
                                                    data = {this.state.detallesEnvio}
                                                    filterable={false}
                                                    defaultPageSize={5}
                                                    showPaginationTop={true}
                                                    showPaginationBottom={false}
                                                >
                                                </ReactTable>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="container col-md-4">
                            <div className="tituloDetalleEnvio form-group">
                                <h3>ENVIO {(this.state.codigoTraslado).substring(4,10)}</h3>
                            </div>
                            <div className="row">
                                <div className="col-md-12 form-group">
                                    <label for="codigoLocal"> ORIGEN: </label>
                                    <input className="form-control" id= "codigoLocal" type="text" value={this.state.local.nombreLocal}/>
                                </div>                          
                            </div>
                            <Divider className="mb-3" />
                            <div className="row">                           
                                <div className="col-md-12 form-group">
                                    <label fro="destinoTraslado">DESTINO: </label>
                                    <select className="form-control" id="destinoTraslado"
                                        onChange={ e => this.setTraslado(e)}
                                    >
                                        <option value="">Seleccione una Tienda</option>
                                        {this.state.locales.map( loc => (
                                            <option key={loc.id} value={loc.codigoLocal}>{loc.nombreLocal}</option>
                                        ))}

                                    </select>
                                </div>
                            </div>
                            <Divider className="mb-3" />
                            <div className="row">                           
                                <div hidden={true} className="col-md-6 form-group">
                                    <label fro="fechaVencimiento">F. Vencimiento: </label>
                                    <input id= "fechaVencimiento" className="form-control" type="date"
                                        value={this.state.fechaVencimiento}
                                        onChange = { e => this.setTraslado(e)}
                                    />
                                </div>
                                <div className="col-md-6 form-group">
                                    <label fro="fechaVencimiento">Total Productos: </label>
                                    <input id= "fechaVencimiento" className="form-control" type="text"
                                        value={this.state.totalProducto}
                                    />
                                </div>
                                <div className="col-md-6 form-group">
                                    <label fro="fechaVencimiento">Total Envio: </label>
                                    <input id= "fechaVencimiento" className="form-control" type="text"
                                        value={"S/. " + (this.state.valorTotalEnvio).toFixed(2)}
                                    />
                                </div>
                                <div className="col-md-12 form-group">
                                    <label fro="observacionOrigen">Observaci√≥n: </label>
                                    <input id ="observacionOrigen" className="form-control" type="text"
                                        placeholder="Estado de Productos"
                                        value={this.state.observacionOrigen}
                                        onChange = { e => this.setTraslado(e)}
                                    />
                                </div>
                            </div>
                            <Divider className="mb-3" />
                            <div className="container mt-3">
                                <div className="row justify-content-center text-center">
                                    <div className="col-6 col-sm-6 col-md-6">
                                        <button className="btn btn-info btn-circle btn-envio"
                                            onClick={this.guardarEnvio}
                                        ><DoneOutlineRounded/></button>
                                    </div>
                                    <div className="col-6 col-sm-6 col-md-6">
                                        <button className="btn btn-danger btn-circle btn-envio"
                                            onClick = {() => {this.verificarEliminar("","ANULAR-TRASLADO")}}
                                        ><RemoveShoppingCartOutlined/></button>
                                    </div>
                                </div>
                            </div>

                        </div>
                    </div>
                    <Modal 
                        isOpen={this.state.modalConfirmarOpen}
                        centered={true}
                        size={"xs"}
                    >
                        <ModalHeader toggle={this.toogleModalConfirmar.bind(this)}>Alerta</ModalHeader>    
                        <ModalBody>
                            <div className="">
                                <div className="row justify-content-center text-center">
                                    <div className="col-md-12 mb-4">
                                        {/*<span>Se eliminar√° el Registro, ¬øDesea Continuar?</span>*/}
                                        <span>{this.state.mensajeAlerta.textoMensaje}</span>
                                    </div>
                                    <div className="col-3 col-sm-3 col-md-3" hidden={!this.state.mensajeAlerta.mostrarConfirmar}>
                                        <button className={this.state.mensajeAlerta.tipoBotonConfirmar}
                                            onClick={this.botonConfirmado}
                                        >{this.state.mensajeAlerta.textoBotonConfirmar}</button>
                                    </div>
                                    <div className="col-3 col-sm-3 col-md-3" hidden={!this.state.mensajeAlerta.mostrarCancelar}>
                                        <button className={this.state.mensajeAlerta.tipoBotonCancelar}
                                            onClick = {this.toogleModalConfirmar.bind(this)}
                                        >{this.state.mensajeAlerta.textoBotonCancelar}</button>
                                    </div>
                                </div>
                            </div>
                        </ModalBody>
                    </Modal>
                </div>
                </Menu>
            )
        }
    }
}

export default Envio
