
import React, { Component } from 'react'
import { Modal , ModalHeader, ModalBody } from 'reactstrap';
import ReactTable from 'react-table';
import "react-table/react-table.css";

import { get_Venta_Reporte } from '../../Data/VentaData';
import { get_Traslado_Reporte, get_Traslado_Reporte_Pendiente } from '../../Data/TrasladoData';
import { get_Venta_Detalle_Codigo_Venta_Vista } from '../../Data/VentaDetalleData';

import ReactHTMLTableExcel from 'react-html-table-to-excel';

import ExportToExcelTraslados from "../../Export/excel/ReporteTraslado";

import ExportToExcelVentas from "../../Export/excel/ReporteVenta";

import Menu from '../../Pages/menu';

const estadoInicial = {
    fechaInicio: new Date(),
    fechaFin: new Date(),
    
    ventasReporte : [],
    detallesVenta:[],

    trasladosReporte: [],


    valorTotalVendido : 0,
    valorTotalEnviado : 0,

    modalVentaOpen : false,
    modalTrasladoOpen : false,
    modalConfirmarOpen: false,
}

export class Reporte extends Component {

    constructor(){
        super();
        this.state = estadoInicial;
    }

    toogleModalConfirmar = () => {
        this.setState({ modalConfirmarOpen : ! this.state.modalConfirmarOpen });
    }

    toogleModalVenta = () => {
        this.setState({ modalVentaOpen : ! this.state.modalVentaOpen });
    }

    toogleModalTraslado = () => {
        this.setState({ modalTrasladoOpen : ! this.state.modalTrasladoOpen });
    }

    setFecha = (event) => {
        this.setState({
            [event.target.id] : [event.target.value]
        });
    }

    obtenerFechaHoy(){
        var fechaHoy = new Date();
        
        var a√±oDate = fechaHoy.getFullYear();
        var mesDate = fechaHoy.getMonth() + 1;
        var diaDate = fechaHoy.getDate();

        var fecha = a√±oDate + "-";
        if(mesDate < 10){
            fecha = fecha + "0" + mesDate; 
        }else{
            fecha = fecha +""+ mesDate;
        }
        fecha = fecha + "-";
        if(diaDate < 10){
            fecha = fecha + "0" + diaDate; 
        }else{
            fecha = fecha + "" + diaDate; 
        }

        this.setState({
            fechaInicio : fecha,
            fechaFin : fecha
        });            
    }

    generarReporteVenta = () => {
        const { fechaInicio, fechaFin } = this.state;
        //const fechas = fechaInicio + " 00:00:01" + "_" + fechaFin + " 23:59:59";
        const fechas = fechaInicio.concat( " 00:00:01" , "_" ,fechaFin ," 23:59:59");

        get_Venta_Reporte(fechas).then( res => {
            var totalVendido = 0;
            res.forEach(vr => {
                totalVendido = totalVendido + vr.valorTotalVenta
            });
            this.setState({ 
                valorTotalVendido : totalVendido,
                ventasReporte : res,
                modalVentaOpen : !this.state.modalVentaOpen
            });
        }); 

    }

    generarReporteTraslado = (tipoReporteTraslado) => {
        const { fechaInicio, fechaFin } = this.state;
        //const fechas = fechaInicio.toString() + " 00:00:01" + "_" + fechaFin.toString() + " 23:59:59";
        const fechas = fechaInicio.concat(" 00:00:01" , "_" , fechaFin , " 23:59:59");

        if(tipoReporteTraslado === ""){
            get_Traslado_Reporte(fechas).then( res => {
                var totalEnviado = 0;
                res.forEach(vr => {
                    totalEnviado = totalEnviado + vr.valorTotal
                });
                this.setState({ 
                    valorTotalEnviado : totalEnviado,
                    trasladosReporte : res,
                    modalTrasladoOpen : !this.state.modalTrasladoOpen
                });
            }); 
        }else{
            get_Traslado_Reporte_Pendiente(fechas).then( res => {
                var totalEnviado = 0;
                res.forEach(vr => {
                    totalEnviado = totalEnviado + vr.valorTotal
                });
                this.setState({ 
                    valorTotalEnviado : totalEnviado,
                    trasladosReporte : res,
                    modalTrasladoOpen : !this.state.modalTrasladoOpen
                });
            }); 
        }


    }

    listarDetallesVentaVista(codigoVenta){
        get_Venta_Detalle_Codigo_Venta_Vista(codigoVenta)
         .then(res => {
             this.setState({ detallesVenta : res });
         });
    }

    mostrarDetalleVenta = (codigoVenta) => {
        this.listarDetallesVentaVista(codigoVenta);
        this.toogleModalConfirmar();
    }

    componentDidMount(){
        this.obtenerFechaHoy();
    }

    render() {
        let buttonCard = {
            cursor: 'pointer'
        };

        const columnsTraslado =[
            {
                Header: "PENDIENTES",
                style:{
                    textAlign: "center"
                },
                Cell: props =>{
                    return(
                        <div className="container">
                            <div className="row">
                                <div className="col-md-12">
                                    {props.original.detalles} Paquete(s)
                                </div>
                                <div className="col-md-12">
                                    De {props.original.nombreLocal}
                                </div>
                                <div className="col-md-12">
                                    Registrado Por:
                                </div>
                                <div className="col-md-12">
                                    {props.original.nombreTrabajador}
                                </div>
                            </div>
                        </div>
                    )
                },
            },
            {
                Header : "OBSE. ORIGEN",
                accessor : "observacionOrigen",
                style : {
                    textAlign : "center"
                }
            },
            {
                Header : "OBSE. DESTINO",
                accessor : "observacionDestino",
                style : {
                    textAlign : "center"
                }
            },
            {
                Header: "ESTADO",
                style:{
                    textAlign: "center"
                },
                Cell: props =>{
                    var classCard = "";
                    var nombreCard = "";
                    if(props.original.confirmacion){
                        classCard = "card bg-success";
                        nombreCard = "CONFIRMADO";
                    }else{
                        classCard = "card bg-danger";
                        nombreCard = "SIN CONFIRMAR";
                    }
                    return(
                        <div className="container">
                            <div className={classCard}>
                                <div className="card-header">
                                    <span>{nombreCard}</span>
                                </div>
                                <div className="card-body">
                                    <label for="fechaRegistro">Enviado el:  </label>
                                    <span>
                                        {
                                            new Intl.DateTimeFormat( 'default',{
                                                year: 'numeric',
                                                month:'numeric',
                                                day:'numeric',
                                                hour:'numeric',
                                                minute:'numeric'
                                            }).format(Date.parse(props.original.fechaRegistro))
                                        }
                                    </span>
                                </div>
                            </div>
                        </div>
                    )
                },
            },
        ]
        
        const columns = [
            {
                Header: "CODIGO",
                accessor: "codigoVenta",
                style:{
                    textAlign: "center"
                }
            },
            {
                Header: "LOCAL",
                accessor: "nombreLocal",
                style:{
                    textAlign: "center"
                }
            },
            {
                Header: "TRABAJADOR",
                accessor: "nombreTrabajador",
                style:{
                    textAlign: "center"
                }
            },
            {
                Header: "FECHA",
                style:{
                    textAlign: "center"
                },
                Cell: props =>{
                    var fecha = new Date(props.original.fechaRegistro)
                    return(
                        <label>
                            {
                                fecha.getFullYear() + "/" + (fecha.getMonth() + 1) + "/" + fecha.getDate()
                                + " -- " + fecha.getHours() + ":" + fecha.getMinutes()
                            }
                        </label>
                )}
            },
            {
                Header: "TOTAL",
                style:{
                    textAlign: "center"
                },
                Cell: props => {
                    return(
                        <label>{ "S/." +(props.original.valorTotalVenta).toFixed(2)  }</label>
                    )
                }
            },
            {
                Header: "DETALLE",
                style:{
                    textAlign: "center"
                },
                Cell: props => {
                    return(
                        <div className="container">
                            <button
                                className="btn btn-info"
                                onClick={() => { this.mostrarDetalleVenta(props.original.codigoVenta)}}
                            >Ver</button>
                        </div>
                    )
                }
            },

        ]

        const columnsDetalleVenta = [
            {
                Header:"IMAGEN",
                Cell: props =>{
                    return(
                        <div className="media_producto">
                            <img src={props.original.urlMedia} alt={props.original.descripcionProducto} className="card-img-top"/>
                        </div>
                    )
                },
                width:100,
                minWidth:100,
                maxWidth:100
            },
            {
                Header: "PRODUCTO",
                accessor:"descripcionProducto",
                style:{
                    textAlign: "center"
                }
            },
            {
                Header: "PRESENTACI√ìN",
                accessor : "descripcionPresentacion",
                style:{
                    textAlign: "center"
                }
            },
            {
                Header: "STOCK",
                accessor : "stockExistente",
                style:{
                    textAlign: "center"
                }
            },
            {
                Header: "VAL. UNIDAD",
                accessor:"valorPrecio",
                style:{
                    textAlign: "center"
                }
            },
            {
                Header: "CANT.",
                accessor: "cantidadProducto",
                style:{
                    textAlign: "center"
                }
            },
            {
                Header: "VAL. TOTAL",
                style:{
                    textAlign: "center"
                },
                Cell: props => {
                    var valorTotalVD = props.original.valorTotalVD;
                    return(
                        <input className="form-control text-center" id= "descripcionProducto" type="text" readOnly
                            value = {valorTotalVD}> 
                        </input>
                    )
                }
            },
        ]


        return (
            <Menu>
            <div>
                <div className="container">
                    <div className="row">
                        <div className="col-md-12">
                            <div className="row form-group">
                                <div className="col-md-6 form-group">
                                    <label for="fechaInicio"> Fecha Inicio: </label>
                                    <input className="form-control" id= "fechaInicio" data-date-format="YYYY MM DD HH:mm:ss"
                                        type="date" value={this.state.fechaInicio}
                                        onChange={(e) => this.setFecha(e)}
                                    />
                                </div>
                                <div className="col-md-6 form-group">
                                    <label for="fechaFin"> Fecha Fin: </label>
                                    <input className="form-control" id= "fechaFin" data-date-format="YYYY MM DD HH:mm:ss"
                                        type="date" value={this.state.fechaFin}
                                        onChange={(e) => this.setFecha(e)}
                                    />
                                </div>
                            </div>
                        </div>
                        <div className="col-md-6" style={buttonCard}>
                            <div className="card text-white bg-success">
                                <div className="card-header">REPORTE DE</div>
                                <button className="btn card-body text-white text-justify"
                                    onClick={this.generarReporteVenta}
                                >VENTAS</button>
                            </div>
                        </div>
                        <div className="col-md-6">
                            <div className="card text-white bg-info">
                                <div className="card-header">REPORTE DE ENVIOS</div>
                                <div className="row">
                                    <div className="col-md-6">
                                        <button className="btn card-body text-white text-justify"
                                            onClick={ () => {this.generarReporteTraslado("")}}
                                        >CONFIRMADOS</button>
                                    </div>
                                    <div className="col-md-6">
                                        <button className="btn card-body text-white text-justify"
                                            onClick={ () => {this.generarReporteTraslado("PENDIENTES")}}
                                        >PENDIENTES</button>
                                    </div>
                                </div>

                            </div>
                        </div>
                    </div>
                    <Modal 
                        isOpen={this.state.modalVentaOpen}
                        centered={true}
                        size={"lg"}
                    >
                        <ModalHeader toggle={this.toogleModalVenta.bind(this)}>Ventas</ModalHeader>    

                        <ModalBody>
                            <div className="row justify-content-center">
                                <div className="col-md-12  mb-4">
                                    <ReactTable
                                    columns = {columns}
                                    noDataText = {"Cargando Datos..."}
                                    data = {this.state.ventasReporte}
                                    filterable
                                    defaultPageSize={this.state.ventasReporte.length}
                                    showPaginationTop={true}
                                    showPaginationBottom={false}
                                    >
                                    {(state, filtredData, instance)=>{
                                        this.reactTable = state.pageRows.map(
                                            ventas => { return ventas._original });
                                            return(
                                            <div>
                                                {filtredData()}
                                                <ExportToExcelVentas ventas={this.reactTable}/>
                                            </div>
                                    )}}
                                    </ReactTable>
                                    
                                </div>
                                <div className="col-md-3">
                                    <label for="totalVentaReporte"> Total Vendido: </label>
                                </div>
                                <div className="col-md-3">
                                    <label > S/. {(this.state.valorTotalVendido).toFixed(2)}</label>
                                </div>
                                <div className="col-md-4">
                                    <label > EN {this.state.ventasReporte.length} VENTA(s)</label>
                                </div>
                            </div>
                            <div className="row justify-content-center mt-3">
                                <div className="col-md-2">
                                    <div className="col-md" style={buttonCard}>
                                        <div className="card bg-success text-white">
                                            <ReactHTMLTableExcel
                                                id="test-table-xls-button"
                                                className="btn text-white text-justify"
                                                table="table-to-xls-venta"
                                                filename="ReporteVenta"
                                                sheet="hoja 1"
                                                buttonText="Excel"
                                            />
                                        </div>
                                    </div>
                                </div>
                                <div className="col-md-2">
                                    <button className="btn btn-danger"
                                    >PDF</button>
                                </div>
                            </div>
                        </ModalBody>
                    </Modal>


                    <Modal 
                        isOpen={this.state.modalTrasladoOpen}
                        centered={true}
                        size={"lg"}
                    >
                        <ModalHeader toggle={this.toogleModalTraslado.bind(this)}>Traslados</ModalHeader>    

                        <ModalBody>
                            <div className="row justify-content-center">
                                <div className="col-md-12  mb-4">
                                    <ReactTable
                                    columns = {columnsTraslado}
                                    noDataText = {"Cargando Datos..."}
                                    data = {this.state.trasladosReporte}
                                    filterable
                                    defaultPageSize={this.state.trasladosReporte.length}
                                    showPaginationTop={true}
                                    showPaginationBottom={false}
                                    >
                                    {(state, filtredData, instance)=>{
                                    this.reactTable = state.pageRows.map(
                                        traslados => { return traslados._original });
                                        return(
                                            <div>
                                                {filtredData()}
                                                <ExportToExcelTraslados traslados={this.reactTable}/>
                                            </div>
                                    )}}
                                    </ReactTable>
                                    
                                </div>
                                <div className="col-md-3">
                                    <label for="totalVentaReporte"> Total Enviado: </label>
                                </div>
                                <div className="col-md-3">
                                    <label > S/. {(this.state.valorTotalEnviado).toFixed(2)} </label>
                                </div>
                            </div>
                            <div className="row justify-content-center mt-3">
                                <div className="col-md-2">
                                    <div className="col-md" style={buttonCard}>
                                        <div className="card bg-success text-white">
                                            <ReactHTMLTableExcel
                                                id="test-table-xls-button"
                                                className="btn text-white text-justify"
                                                table="table-to-xls-traslado"
                                                filename="ReporteEnvio"
                                                sheet="hoja 1"
                                                buttonText="Excel"
                                            />
                                        </div>
                                    </div>
                                </div>
                                <div className="col-md-2">
                                    <button className="btn btn-danger"
                                    >PDF</button>
                                </div>
                            </div>
                        </ModalBody>
                    </Modal>
                    <Modal 
                        isOpen={this.state.modalConfirmarOpen}
                        centered={true}
                        size={"xm"}
                    >
                        <ModalHeader toggle={this.toogleModalConfirmar.bind(this)}>Alerta</ModalHeader>    
                        <ModalBody>
                            <div className="container">
                                <div className="row justify-content-center text-center">
                                    <div className="col-md-12">
                                        <ReactTable
                                        columns = {columnsDetalleVenta}
                                        noDataText = {"Cargando Datos..."}
                                        data = {this.state.detallesVenta}
                                        defaultPageSize={5}
                                        showPaginationTop={true}
                                        showPaginationBottom={false}
                                        >
                                        </ReactTable>
                                    </div>
                                </div>
                            </div>
                        </ModalBody>
                    </Modal>
                </div>
            </div>
            </Menu>
        )
    }
}

export default Reporte
