import React, { Component } from 'react';

class ExportExcel extends Component{
    render(){
        return(
            <div className="container row">

                <div>
                    <table hidden={true} id="table-to-xls">
                        <thead>
                            <tr>
                                <th>TPD ID</th>
                                <th>Codigo</th>
                                <th>Traslado</th>  
                            </tr>                
                        </thead>
                        <tbody>
                        {
                            this.props.tpTraslado.map(tpTra =>{
                                return(
                                    <tr key={tpTra.id}>
                                        <td>{tpTra.id}</td>
                                        <td>{tpTra.codigoTipoTraslado}</td>
                                        <td>{tpTra.nombreTipoTraslado}</td>   
                                    </tr>
                                );    
                            })
                        }
                        </tbody>
                    </table>
                </div>
            </div>
        );
    }
}

export default ExportExcel