import React  from 'react'

const cliente  = ( props ) => {
    return (
    <div className="card ">
        <div className="card-header text-center">
            <h3>Cliente Nuevo</h3>
        </div>
        <div className="card-body ">
            <div className="form-group">
                <label for="detalleProducto"> DOCUMENTO: </label>
                <select className="form-control" id= "detalleProducto"> 
                    {props.tiposDocumentos.map((tpDocu) => {
                        return(
                            <option value={tpDocu.codigoTipoDocumento} key={tpDocu.id}>{tpDocu.nombreTipoDocumento}</option>   
                        )
                    })}                    
                </select>
            </div>
                <div className="form-group">
                    <label for="numeroDocumento"> NUMERO: </label>
                    <input className="form-control" id= "numeroDocumento" type="text" 
                        onChange={e => props.setCliente(e)}
                    />
                </div>
                <div  hidden={true} className="form-group">
                    <label for="telefonoCliente"> TELEFONO: </label>
                    <input className="form-control" id= "telefonoCliente" type="text"
                        onChange={e => props.setCliente(e)}
                     />
                </div>
                <div  hidden={true} className="form-group hide">
                    <label for="correoCliente">   CORREO: </label>
                    <input className="form-control" id= "correoCliente" type="text" 
                        onChange={e => props.setCliente(e)}
                    />
                </div>
                <div  hidden={true} className="form-group hide">
                    <label for="direccionCliente"> DIRECCI√ìN: </label>
                    <input className="form-control" id= "direccionCliente" type="text" 
                        onChange={e => props.setCliente(e)}
                    />
                </div>
                <div className="form-group">
                    <label for="nombreCliente"> NOMBRE: </label>
                    <input className="form-control" id= "nombreCliente" type="text"
                        onChange={e => props.setCliente(e)}
                    />
                </div>
                <div  hidden={true} className="form-group hide">
                    <label for="enRepresentacion"> EN REPRESENTACION: </label>
                    <input className="form-control" id= "enRepresentacion" type="text"
                        onChange={e => props.setCliente(e)}
                    />
                </div>
                <div hidden={true} className="form-group hide">
                    <label for="razonSocial"> RAZON SOCIAL: </label>
                    <input className="form-control" id= "razonSocial" type="text"
                        onChange={e => props.setCliente(e)}
                    />
                </div>
                <div className="text-center">
                    <button className="btn btn-primary"
                        onClick={props.registrarCliente}
                    > REGISTRAR </button>
                </div>
            {/*</form>*/}
        </div>
    </div>
    )
}

export default cliente
