import React, { Component} from 'react';
import ReactTable from 'react-table';
import "react-table/react-table.css";

import ReactHTMLTableExcel from 'react-html-table-to-excel';

import { Modal , ModalHeader, ModalBody, Alert } from 'reactstrap';

import data from '../../../db_data';
import ExportToExcel from "../../Export/excel/tipoProducto";

import { post_Tipo_Producto, put_Tipo_Producto, del_Tipo_Producto , get_Tipo_Producto , get_Tipo_Producto_Lista} from '../../Data/tiposData';

import Menu from '../../Pages/menu';

export default class tipoProductoLista extends Component {

    BaseUrl = data + '/api/tipos/producto';

    constructor (props){
        super(props);
        this.state = {
            tipoProducto:[],
            filtered:[],
            tipoProductoExcel:[],
            message : {
                text:'',
                alert: ''
            },
            messageActivo:false,

            codigoTipoProducto: "",
            nombreTipoProducto: "",
            descripcionTipoProducto:"",
            tipoAccion:"post",
            modalOpen: false,
            tituloModal:"",

            modalConfirmarOpen: false,
            tipoEliminar : "",
            codigoTPEliminar : "",
        }
    }
    
    toogleModalConfirmar = () => {
        this.setState({ modalConfirmarOpen : ! this.state.modalConfirmarOpen });
    }

    mostrarModal = (codigoTipoProducto) => {
        this.setState({ codigoTPEliminar : codigoTipoProducto });
        this.toogleModalConfirmar();
    }

    borrarConfirmado = () => {
        const { codigoTPEliminar } = this.state;
        this.eliminarTipoProducto(codigoTPEliminar);
        this.toogleModalConfirmar();
    }

    onFilteredChangeCustom = (value, accessor) => {
        let filtered = this.state.filtered;
        let insertNewFilter = 1;
    
        if (filtered.length) {
          filtered.forEach((filter, i) => {
            if (filter["id"] === accessor) {
              if (value === "" || !value.length) filtered.splice(i, 1);
              else filter["value"] = value;
              insertNewFilter = 0;
            }
          });
        }
    
        if (insertNewFilter) {
          filtered.push({ id: accessor, value: value });
        }
    
        this.setState({ filtered: filtered });
      };
    

    listarTipoProducto(codigoTipoProducto){
        if(codigoTipoProducto !== "" && codigoTipoProducto !== null){
            get_Tipo_Producto(codigoTipoProducto).then(res => {
                this.setState({ tipoProducto : res })
            });
        }else{
            get_Tipo_Producto_Lista().then(res => {
                this.setState({ tipoProducto : res})
            });
        }
    }
    cambiarAlerta(message){
        this.setState({ message:message });
        this.mostrarAlerta(3000);  
    }
    guardarVerificacion = () =>{
        if(this.state.nombreTipoProducto){
            if(this.state.descripcionTipoProducto){
                this.guardarTipoProducto();
            }else{
                alert("Ingrese una Descripcion");
            }
        }else{
            alert("Ingrese el Nombre");
        }
    }

    guardarTipoProducto = () =>{
        const { nombreTipoProducto, descripcionTipoProducto , codigoTipoProducto } = this.state;
        const tipoProducto = {nombreTipoProducto, descripcionTipoProducto, codigoTipoProducto };
        const message = {
            text:"",
            alert:""  
        };        
        switch (this.state.tipoAccion) {
            case "post":
                post_Tipo_Producto(tipoProducto).then((result)=>{
                    message.alert = "success";
                    message.text = "Se guardo correctamente";
                    this.toogleModal();
                });
                break;
            case "put":
                put_Tipo_Producto(tipoProducto).then((result)=>{
                    message.alert = "success";
                    message.text = "Se modifico correctamente";
                    this.toogleModal();
                });
                break;
            default:
                break;
        }
        this.cambiarAlerta(message);
    }

    eliminarTipoProducto(codigoTipoProducto){
        const message = {
            text:"",
            alert:""  
        };
        if(codigoTipoProducto !== "" && codigoTipoProducto !== null){
            del_Tipo_Producto(codigoTipoProducto).then(res => {
                message.alert = "danger";
                message.text = "Se elimino correctamente";
                this.listarTipoProducto("");         
            });
        }
        this.cambiarAlerta(message);
    }

    setValues = (e , field) => {       
        this.setState({ [field]: e.target.value });
    }

    verificarAccion(codigoTipoProducto,tituloModal){
        if(codigoTipoProducto !== null && codigoTipoProducto !== ""){
            get_Tipo_Producto(codigoTipoProducto).then(res =>{
                if(res[0]){    
                    this.setState({
                        nombreTipoProducto:res[0].nombreTipoProducto,
                        codigoTipoProducto:res[0].codigoTipoProducto,
                        descripcionTipoProducto:res[0].descripcionTipoProducto,
                        tipoAccion:"put",        
                        tituloModal: tituloModal
                    });
                }
            });
        }else{
            this.setState({ 
                nombreTipoProducto:"",
                codigoTipoProducto:"",
                tipoAccion:"post",
                tituloModal: tituloModal
            });
        }
        this.toogleModal();
    }
    toogleModal(){   
        this.setState({
            modalOpen : ! this.state.modalOpen,
        });
        if(!this.state.modalOpen){
            this.listarTipoProducto("");
        }        
    }

    mostrarAlerta = (tiempo) => {
        setTimeout(()=>{
            this.setState({ message : { text : '' , alert : ''} });            
        },tiempo);
    }

    componentDidMount(){
        this.listarTipoProducto("");
    }
    render() {
        const columns = [
            {
                Header: "ID",
                accessor: "id",
                style:{
                    textAlign: "center"
                },
                show:false
            },
            {
                Header: "CODIGO",
                accessor: "codigoTipoProducto",
                style:{
                    textAlign: "center"
                }
            },
            {
                Header: "Producto",
                accessor: "nombreTipoProducto",
                style:{
                    textAlign: "center"
                }
            },
            {
                Header: "DESCRIPCION",
                accessor: "descripcionTipoProducto",
                style:{
                    textAlign: "center"
                }
            },
            {
                Header:"ACCIONES",
                Cell: props => {
                    return(
                        <div className="container">
                            <div className="row justify-content-center">
                                <div className="col-md-5">
                                    <button className="btn btn-danger"
                                        onClick={() =>{
                                            this.mostrarModal(props.original.codigoTipoProducto);
                                        }}
                                    >Borrar</button>
                                </div>
                                <div className="col-md-5 ml-auto">
                                    <button className="btn btn-warning"
                                    onClick={
                                        e => this.verificarAccion(props.original.codigoTipoProducto,"Modificar")                                           
                                    }>Editar</button>
                                </div>
                            </div>
                        </div>
                    )
                },                
                sortable: false,
                filterable: false
            }
        ]
        let buttonCard = {
            cursor: 'pointer'
        };
        return (
            <Menu>
            <div className="container p-4">
                {/*<MaterialTable></MaterialTable>*/}
                <div className="row">
                    <div className="col-md-11 mx-auto">
                        <div className="card ">
                            <div className="card-header">
                                <h3>Tipo de Productos en Existencia</h3>
                            </div>
                            <div className="m-2 text-center">
                                { this.state.message.text !==''?( <Alert color={this.state.message.alert}>{ this.state.message.text}</Alert> ) : '' }
                            </div>                            
                            <div className="row m-2">
                                <div className="col-sm" style={buttonCard}>
                                    <div className="card text-white bg-info"
                                        onClick={()=>{
                                            this.verificarAccion("","Registrar")
                                            //this.ver();
                                    }}>
                                    <div className="card-header">Registrar</div>
                                        <button className="btn card-body text-white text-justify">Tipo de Producto.</button>
                                    </div>
                                </div>
                                
                                <div className="col-sm" style={buttonCard}>
                                    <div className="card bg-success text-white">
                                        <div className="card-header">Exportar</div>
                                        <ReactHTMLTableExcel
                                            id="test-table-xls-button"
                                            className="btn card-body text-white text-justify"
                                            table="table-to-xls"
                                            filename="tipoProductoExcel"
                                            sheet="hoja 1"
                                            buttonText="A Documento Excel."
                                        />
                                    </div>
                                </div>
                            </div>
                            <div className="card-body">
                                <ReactTable
                                    columns = {columns}
                                    noDataText = {"Cargando Datos..."}
                                    data = {this.state.tipoProducto}
                                    filterable
                                    filtered = {this.state.filtered}
                                    onFilteredChange = {(filtered, column, value) => {
                                        this.onFilteredChangeCustom(value, column.id || column.accessor);
                                      }}
                                      defaultFilterMethod={(filter, row, column) => {
                                        const id = filter.pivotId || filter.id;
                                        if (typeof filter.value === "object") {
                                          return row[id] !== undefined
                                            ? filter.value.indexOf(row[id]) > -1
                                            : true;
                                        } else {
                                          return row[id] !== undefined
                                            ? String(row[id]).indexOf(filter.value) > -1
                                            : true;
                                        }
                                      }}
                                    defaultPageSize={5}
                                    showPaginationTop={true}
                                    showPaginationBottom={false}
                                >
                                {(state, filtredData, instance)=>{
                                    this.reactTable = state.pageRows.map(
                                        tipoProducto => {return tipoProducto._original});
                                    return(
                                        <div>
                                            {filtredData()}
                                            <ExportToExcel tpProducto={this.reactTable}/>
                                        </div>
                                    )
                                }}
                                </ReactTable>
                            </div>
                            <Modal 
                                isOpen={this.state.modalOpen}
                                centered={true}
                                size={"xm"}
                            >
                                <ModalHeader toggle={this.toogleModal.bind(this)}>{this.state.tituloModal}</ModalHeader>    

                                <ModalBody>
                                    <div className="card ">
                                        <div className="card-header text-center">
                                            <h3>Tipo de Producto</h3>
                                        </div>
                                        <div className="card-body ">
                                            {/*<form>*/}
                                                <div className="form-group">
                                                    <label for="nombreTipoProducto"> Nombre: </label>
                                                    <input className="form-control" id= "nombreTipoProducto" type="text" placeholder="Ejemplo: JPGE"
                                                        value = {this.state.nombreTipoProducto} 
                                                        onChange = { e => this.setValues(e,'nombreTipoProducto') }
                                                    />
                                                </div>
                                                <div className="form-group">
                                                    <label for="descripcionTipoProducto"> Descripcion: </label>
                                                    <input className="form-control" id= "descripcionTipoProducto" type="text" placeholder="Ejemplo: Archivo tipo .jpge"
                                                        value = {this.state.descripcionTipoProducto} 
                                                        onChange = { e => this.setValues(e,'descripcionTipoProducto') }
                                                    />
                                                </div>
                                                <div className="text-center">
                                                    <button className="btn btn-primary"
                                                        onClick={this.guardarVerificacion}
                                                    > REGISTRAR </button>
                                                </div>
                                            {/*</form>*/}
                                        </div>
                                    </div>
                                </ModalBody>
                            </Modal>
                            <Modal 
                                    isOpen={this.state.modalConfirmarOpen}
                                    centered={true}
                                    size={"xm"}
                            >
                                <ModalHeader toggle={this.toogleModalConfirmar.bind(this)}>Alerta</ModalHeader>    
                                <ModalBody>
                                    <div className="container">
                                        <div className="row justify-content-center text-center">
                                            <div className="col-md-12 mb-4">
                                                <span>Se eliminar√° el Registro, ¬øDesea Continuar? </span>
                                            </div>
                                            <div className="col-md-3">
                                                <button className="btn btn-danger"
                                                    onClick={this.borrarConfirmado}
                                                >Eliminar</button>
                                            </div>
                                            <div className="col-md-3">
                                                <button className="btn btn-info"
                                                    onClick = {this.toogleModalConfirmar.bind(this)}
                                                >Cancelar</button>
                                            </div>
                                        </div>
                                    </div>
                                </ModalBody>
                            </Modal>
                        </div>
                    </div>
                </div>
            </div>
            </Menu>
        )
    }
}
