// VentaDetalle API FROM SERVER NODE EXPRESS

import data from '../../db_data';
const BaseUrl = data + '/api/ventas/';

// ----------------------------------------------------------------------------------------------------------------

//MOSTRAR VENTAS POR LOCAL
export function get_Suma_Venta_Mensual(nombreMes){ 
    const urlFetch = BaseUrl + "suma/" + nombreMes;
    return new Promise((resolve,reject)=>{
        fetch(urlFetch)
        .then((response) => response.json())
        .then((responseJson) => {
            resolve(responseJson);
        })
        .catch((error)=>{
            reject(error);
        });
    });
}


