//ELIMINAR DETALLES DE VENTA POR CODIGO DE VENTA
export function del_Venta(codigoVenta){ 
    const urlFetch = BaseUrl + codigoVenta;
    return new Promise((resolve,reject)=>{
        fetch(urlFetch,{
            method:'DELETE',
            headers: new Headers({
                'Content-type':'application/json'
            })
        })
        .then((response) => response.json())
        .then((responseJson) => {
            resolve(responseJson);
        })
        .catch((error)=>{
            reject(error);
        });
    });
}

