//CREAR VENTA VACIO
export function get_Venta_Vacio(codigoUsuario,codigoLocal){ 
    const urlFetch = BaseUrl + "vacio/" + codigoUsuario + "_" + codigoLocal;
    return new Promise((resolve,reject)=>{
        fetch(urlFetch)
        .then((response) => response.json())
        .then((responseJson) => {
            resolve(responseJson);
        })
        .catch((error)=>{
            reject(error);
        });
    });
}


//MODIFICAR DETALLE DE VENTA POR CODIGO
export function put_Venta(Venta){ 
    const urlFetch = BaseUrl;
    return new Promise((resolve,reject)=>{
        fetch(urlFetch,{
            method:'PUT',
            body: JSON.stringify(Venta),
            headers: new Headers({
                'Content-type':'application/json'
            })
        })
        .then((response) => response.json())
        .then((responseJson) => {
            resolve(responseJson);
        })
        .catch((error)=>{
            reject(error);
        });
    });
}
