import React, { Component } from 'react'
import ReactTable from 'react-table';
import "react-table/react-table.css";

export class ProductoLista extends Component {

    constructor(props){
        super(props);
        this.state = {
            productos : props.productos
        }
    }

    render() {
        const columns = [
            {
                Header: "Producto",
                Cell: props =>{
                    return(
                        <div>
                           <span> {props.original.descripcionProducto + " - " +props.original.detalleProducto}</span>
                           <br></br>
                           <span><b>{" Stock: "}</b>{props.original.stockExistente}</span>
                        </div>
                    )
                }
            }
        ]
        
        return (
            <div className="container">
                <div className="card ">
                    <div className="card-header">
                        <h3>Productos en Existencia</h3>
                    </div>
                    <div className="card-body">
                        <ReactTable
                            columns = {columns}
                            noDataText = {"Cargando Datos..."}
                            data = {this.state.productos}
                            filterable
                            defaultPageSize={5}
                            showPaginationTop={true}
                            showPaginationBottom={false}
                        >
                        </ReactTable>
                    </div>   
                </div>             
            </div>
        )
    }
}

export default ProductoLista
