// SUCURSAL API FROM SERVER NODE EXPRESS

import data from '../../db_data';
const BaseUrl = data + '/api/sucursales/';

// LISTAR SUCURSALES
export function get_Sucursal_Lista(){ 
    const urlFetch = BaseUrl;
    return new Promise((resolve,reject)=>{
        fetch(urlFetch)
        .then((response) => response.json())
        .then((responseJson) => {
            resolve(responseJson);
        })
        .catch((error)=>{
            reject(error);
        });
    });
}

// LISTAR SUCURSALES POR CODIGO
export function get_Sucursal(codigoSucursal){ 
    const urlFetch = BaseUrl + codigoSucursal;
    return new Promise((resolve,reject)=>{
        fetch(urlFetch)
        .then((response) => response.json())
        .then((responseJson) => {
            resolve(responseJson);
        })
        .catch((error)=>{
            reject(error);
        });
    });
}

//ELIMINAR SUCURSAL CODIGO
export function del_Sucursal(codigoSucursal){ 
    const urlFetch = BaseUrl + codigoSucursal;
    return new Promise((resolve,reject)=>{
        fetch(urlFetch,{
            method:'DELETE',
            headers: new Headers({
                'Content-type':'application/json'
            })
        })
        .then((response) => response.json())
        .then((responseJson) => {
            resolve(responseJson);
        })
        .catch((error)=>{
            reject(error);
        });
    });
}

//GUARDAR SUCURSAL
export function post_Sucursal(sucursal){ 
    const urlFetch = BaseUrl;
    return new Promise((resolve,reject)=>{
        fetch(urlFetch,{
            method:'POST',
            body: JSON.stringify(sucursal),
            headers: new Headers({
                'Content-type':'application/json'
            })
        })
        .then((response) => response.json())
        .then((responseJson) => {
            resolve(responseJson);
        })
        .catch((error)=>{
            reject(error);
        });
    });
}

//MODIFICAR SUCURSAL
export function put_Sucursal(sucursal){ 
    const urlFetch = BaseUrl;
    return new Promise((resolve,reject)=>{
        fetch(urlFetch,{
            method:'PUT',
            body: JSON.stringify(sucursal),
            headers: new Headers({
                'Content-type':'application/json'
            })
        })
        .then((response) => response.json())
        .then((responseJson) => {
            resolve(responseJson);
        })
        .catch((error)=>{
            reject(error);
        });
    });
}
