import React from 'react';
import { BrowserRouter , Route , Switch} from 'react-router-dom';

import Login from './components/Pages/login';
import Logout from './components/Pages/logout';
import Error404 from './components/Pages/404'
import Profile from './components/Pages/profile'

import tipoDocumento from './components/Vista/Tipos/tipoDocumento';
import tipoLocal from './components/Vista/Tipos/tipoLocal';
import tipoMedia from './components/Vista/Tipos/tipoMedia';
import tipoPrecio from './components/Vista/Tipos/tipoPrecio';
import tipoProducto from './components/Vista/Tipos/tipoProducto';
import tipoTraslado from './components/Vista/Tipos/tipoTraslado';

import Medias from './components/Vista/Media/Media';
import Productos from './components/Vista/Producto/Producto';
import Ventas from './components/Vista/Venta/Venta';
import ListaVentas from './components/Vista/Venta/VentaLista';

import Locales from './components/Vista/Local/Local';
import Sucursales from './components/Vista/Sucursal/Sucursal';
import Usuarios from './components/Vista/Usuario/Usuario';
import Envios from './components/Vista/Envio/Envio';
import Ingresos from './components/Vista/Ingreso/Ingreso';

import Reportes from './components/Vista/Reporte/Reporte';

import './app.css';

function App() {
  return (
    <BrowserRouter>
        <Switch>
          <Route exact path="/app" component={Profile}/>
          <Route path="/app/login" component={Login}/>
          <Route path="/app/logout" component={Logout}/>

          <Route path="/app/tipos/documento/" component={tipoDocumento}/>
          <Route path="/app/tipos/local" component={tipoLocal}/>
          <Route path="/app/tipos/media" component={tipoMedia}/>
          <Route path="/app/tipos/precio" component={tipoPrecio}/>
          <Route path="/app/tipos/producto" component={tipoProducto}/>
          <Route path="/app/tipos/traslado" component={tipoTraslado}/>
          
          <Route path="/app/productos" component={Productos}/>
          <Route path="/app/medias/listar" component={Medias}/>

          <Route path="/app/ventas/listar" component={ListaVentas}/>
          <Route path="/app/ventas" component={Ventas}/>

          <Route path="/app/sucursales" component={Sucursales}/>

          <Route path="/app/locales/tienda" component={ () => <Locales nombreTipoLocal={"TIENDA"}></Locales>} />
          <Route path="/app/locales/almacen" component={ () => <Locales nombreTipoLocal={"ALMACEN"}></Locales>} />
          <Route path="/app/usuarios" component={Usuarios}/>

          <Route path="/app/envios/todos" component={ () =>  <Ingresos titulo={"Lista de Envios"} tipoVista={"enviados"} acciones={true} ></Ingresos>}/>
          <Route path="/app/envios" component={ () =>  <Envios codigoTipoTraslado={"TP-TRA-001"}></Envios>}/>  


          <Route path="/app/ingresos/pendientes" component={() => <Ingresos titulo={"Ingresos Pendientes"} tipoVista={"pendientes"} acciones={true}></Ingresos>}/>
          <Route path="/app/ingresos/todos" component={() => <Ingresos titulo={"Lista de Ingresos"} tipoVista={"ingresados"} acciones={true}></Ingresos>}/>


          <Route path="/app/reportes" component={Reportes}/>

          <Route path="/app*" component={Error404} />
        </Switch>
    </BrowserRouter>
  );
}

export default App;
