import React, { Component } from 'react';

class ExportExcel extends Component{
    render(){
        return(
            <div className="container row">
                <div>
                    <table hidden={true} id="table-to-xls">
                        <thead>
                            <tr>
                                <th>TPD ID</th>
                                <th>Codigo</th>
                                <th>Local</th>  
                            </tr>                
                        </thead>
                        <tbody>
                        {
                            this.props.tpLocal.map(tpLoc =>{
                                return(
                                    <tr key={tpLoc.id}>
                                        <td>{tpLoc.id}</td>
                                        <td>{tpLoc.codigoTipoLocal}</td>
                                        <td>{tpLoc.nombreTipoLocal}</td>      
                                    </tr>
                                );    
                            })
                        }
                        </tbody>
                    </table>
                </div>
            </div>
        );
    }
}

export default ExportExcel