// TRASLADO API FROM SERVER NODE EXPRESS

import data from '../../db_data';
const BaseUrl = data + '/api/traslados/';

// ----------------------------------------------------------------------------------------------------------------


//MOSTRAR TRASLADOS PENDIENTES POR LOCAL
export function get_Traslado_Pendiente_Local(codigoLocal){ 
    const urlFetch = BaseUrl + "pendientes/" + codigoLocal;
    return new Promise((resolve,reject)=>{
        fetch(urlFetch)
        .then((response) => response.json())
        .then((responseJson) => {
            resolve(responseJson);
        })
        .catch((error)=>{
            reject(error);
        });
    });
}

