//CREAR TRASLADO VACIO
export function post_Traslado_Vacio(codigosCreacion){ 
    const urlFetch = BaseUrl + "vacio";
    return new Promise((resolve,reject)=>{
        fetch(urlFetch,{
            method:'POST',
            body: JSON.stringify(codigosCreacion),
            headers: new Headers({
                'Content-type':'application/json'
            })
        })
        .then((response) => response.json())
        .then((responseJson) => {
            resolve(responseJson);
        })
        .catch((error)=>{
            reject(error);
        });
    });
}

