// MEDIA API FROM SERVER NODE EXPRESS

import data from '../../db_data';
const BaseUrl = data + '/api/medias/';

// ----------------------------------------------------------------------------------------------------------------
// LISTAR MEDIAS
export function get_Media_Lista(){ 
    const urlFetch = BaseUrl ;
    return new Promise((resolve,reject)=>{
        fetch(urlFetch)
        .then((response) => response.json())
        .then((responseJson) => {
            resolve(responseJson);
        })
        .catch((error)=>{
            reject(error);
        });
    });
}

// MOSTRAR MEDIA CODIGO
export function get_Media(codigoMedia){ 
    const urlFetch = BaseUrl + codigoMedia;
    return new Promise((resolve,reject)=>{
        fetch(urlFetch)
        .then((response) => response.json())
        .then((responseJson) => {
            resolve(responseJson);
        })
        .catch((error)=>{
            reject(error);
        });
    });
}

// MOSTRAR MEDIA URL
export function get_Media_Url(urlMedia){ 
    const urlFetch = BaseUrl + "url/" + urlMedia;
    return new Promise((resolve,reject)=>{
        fetch(urlFetch)
        .then((response) => response.json())
        .then((responseJson) => {
            resolve(responseJson);
        })
        .catch((error)=>{
            reject(error);
        });
    });
}


//ELIMINAR MEDIA CODIGO
export function del_Media(codigoMedia){ 
    const urlFetch = BaseUrl + codigoMedia;
    return new Promise((resolve,reject)=>{
        fetch(urlFetch,{
            method:'DELETE',
            headers: new Headers({
                'Content-type':'application/json'
            })
        })
        .then((response) => response.json())
        .then((responseJson) => {
            resolve(responseJson);
        })
        .catch((error)=>{
            reject(error);
        });
    });
}

//GUARDAR MEDIA
export function post_Media(media){ 
    const urlFetch = BaseUrl;
    return new Promise((resolve,reject)=>{
        fetch(urlFetch,{
            method:'POST',
            body: JSON.stringify(media),
            headers: new Headers({
                'Content-type':'application/json'
            })
        })
        .then((response) => response.json())
        .then((responseJson) => {
            resolve(responseJson);
        })
        .catch((error)=>{
            reject(error);
        });
    });
}

