//GUARDAR FILE MEDIA
export function post_Media_File(fileMedia){
    const fd = new FormData();
    fd.append('fileMedia',fileMedia);
    const urlFetch = BaseUrl + 'file';
    return new Promise((resolve, reject)=>{
        fetch(urlFetch,{
            method:'POST',
            body: fd
        })
        .then((response) => response.json())
        .then((responseJson) => {
            resolve(responseJson);
        })
        .catch((error)=>{
            reject(error);
        });
    });
}

