import React, { Component } from 'react';

class ExportExcel extends Component{
    render(){
        return(
            <div className="container row">
                <div>
                    <table hidden={true} id="table-to-xls">
                        <thead>
                            <tr>
                                <th>TPD ID</th>
                                <th>Codigo</th>
                                <th>Precio</th>  
                                <th>Descripcion</th>
                            </tr>                
                        </thead>
                        <tbody>
                        {
                            this.props.tpPrecio.map(tpPre =>{
                                return(
                                    <tr key={tpPre.id}>
                                        <td>{tpPre.id}</td>
                                        <td>{tpPre.codigoTipoPrecio}</td>
                                        <td>{tpPre.nombreTipoPrecio}</td>                                        
                                        <td>{tpPre.descripcionTipoPrecio}</td>       
                                    </tr>
                                );    
                            })
                        }
                        </tbody>
                    </table>
                </div>
            </div>
        );
    }
}

export default ExportExcel