// API REGISTRO FROM SERVER NODE EXPRESS

import data from '../../db_data';
const BaseUrl = data + '/api/registros/';

// ----------------------------------------------------------------------------------------------------------------

//ELIMINAR REGISTROS VACIOS POR CODIGO DE LOCAL
export function del_Registro_Vacio(codigoLocal){ 
    const urlFetch = BaseUrl + "vacio/" + codigoLocal;
    return new Promise((resolve,reject)=>{
        fetch(urlFetch,{
            method:'DELETE',
            headers: new Headers({
                'Content-type':'application/json'
            })
        })
        .then((response) => response.json())
        .then((responseJson) => {
            resolve(responseJson);
        })
        .catch((error)=>{
            reject(error);
        });
    });
}