import React, { Component } from 'react';
class ExportExcel extends Component{
    render(){
        return(
            <div className="container row">
                <div>
                    <table hidden={true} id="table-to-xls-traslado">
                        <thead>
                            <tr>
                                <th>CODIGO</th>
                                <th>ORIGEN</th>
                                <th>TRAB. REGISTRO</th>
                                <th>Obs.</th>
                                <th>DESTINO</th>
                                <th>TRAB. CONFIRMADO</th>
                                <th>Obs.</th>
                                <th>F. ENVIO</th>
                                <th>F. INGRESO</th>
                                <th>ESTADO</th> 
                            </tr>                                           
                        </thead>
                        <tbody>
                        {
                            this.props.traslados.map(tra =>{
                                return(
                                    <tr key={tra.id}>
                                        <td>{tra.codigoTraslado}</td>
                                        <td>{tra.nombreLocalOrigen}</td>
                                        <td>{tra.nombreTrabajadorRegistro}</td>
                                        <td>{tra.observacionOrigen}</td>
                                        <td>{tra.nombreLocalDestino}</td>
                                        <td>{tra.nombreTrabajadorConfirmacion}</td>
                                        <td>{tra.observacionDestino}</td>
                                        <td>{tra.fechaRegistro}</td>
                                        <td>{tra.fechaConfirmacion}</td>
                                        <td>{tra.confirmacion}</td>
                                    </tr>
                                );    
                            })
                        }
                        </tbody>
                    </table>
                </div>
            </div>
        );
    }
}

export default ExportExcel