import React, { Component } from 'react'
import ReactTable from 'react-table';
import "react-table/react-table.css";
import Divider from '@material-ui/core/Divider';
import { Modal , ModalHeader, ModalBody } from 'reactstrap';


import { get_Producto_Local_Venta_Lista, get_Producto_Venta } from '../../Data/productoData';
import { post_Venta_Detalle_Temporal, get_Venta_Detalle_Codigo_Venta_Vista, put_Venta_Detalle_Temporal, del_Venta_Detalle_CodigoVD, del_Venta_Detalle_Codigo_Venta } from '../../Data/VentaDetalleData';
import { get_Venta_Vacio, put_Venta, del_Venta } from '../../Data/VentaData';
import { get_Tipo_Documento_Lista } from '../../Data/tiposData';
import { get_Cliente_Lista, post_Cliente } from '../../Data/Cliente';

import ClienteForm from '../Cliente/ClienteForm';

import "./Venta.css";

import Menu from '../../Pages/menu';

import { DeleteForeverOutlined , DoneOutlineRounded , RemoveShoppingCartOutlined , PersonAddRounded } from '@material-ui/icons';

const estadoInicial = {
    productos:[],
    productosBusqueda:[],

    sugerenciasProducto:[],

    codigoVenta:"",
    codigoProducto:"",
    nombreProductoBuscar:"",
    tipoBuscadorProducto:"descripcionProducto",
    
    venta : {
        codigoVenta:"",
        codigoUsuario:"",
        codigoCliente:"",
        codigoLocal:"",
        valorTotalVenta:0,
        valorModificado:0,
        valorDescuento:0,
        ventaDetalles:[],
    },

    detalle : {
        codigoVD:"",
        codigoVenta:"",
        codigoProducto:"",
        codigoPrecio:"",
        cantidadProducto:"",
        valorTotalVD:"",
        valorModificado:"",
        valorDescuento:""
    },
    detallesVentas : [],
    detalles: [],

    tiposDocumentos:[],

    sugerenciasCliente:[],
    clientes:[],
    cliente:{
        codigoCliente:"",
        codigoTipoDocumento:"",
        numeroDocumento:"",
        telefonoCliente:"",
        correoCliente:"",
        direccionCliente:"",
        nombreCliente:"",
        enRepresentacion:"",
        razonSocial:"",
    },

    confirmarCrearVenta : false,
    confirmarEliminar : false,

    tipoAccion : "",

    modalClienteOpen :false,
    modalConfirmarOpen: false,

    creacionCliente : false,
    tituloModal:"",

    nombreCliente:"",

    codigoVDTemporal : "",

    montoRecibido : 0.00,
    valorTotalVenta : 0.00,

    mensajeAlerta : {
        textoMensaje : "",
        tipoBotonConfirmar : "",
        textoBotonConfirmar : "",
        mostrarConfirmar : false,
        tipoBotonCancelar : "",
        textoBotonCancelar : "",
        mostrarCancelar : false,
    }

}

export class venta extends Component {

    constructor(props){
        super(props);
        this.state = estadoInicial;
        this.onChangeBuscadorProducto = this.onChangeBuscadorProducto.bind(this);
    }

    setMontoRecibido = (montoRecibido) => {
        this.setState({ montoRecibido })
    }

    setCliente = (e) => {
        const { cliente } = this.state;
        cliente[e.target.id] = e.target.value;
        if(e.target.id === "nombreCliente"){
            var nombreCliente = e.target.value;
            this.setState({ nombreCliente });
        }
        this.setState({ cliente });
    }

    registrarCliente = () => {
        const { cliente } = this.state;
        if(cliente.nombreCliente.length > 0){
            post_Cliente(cliente)
            .then(res => {
                this.setState({ 
                    creacionCliente : true,
                    nombreCliente: res[0].nombreCliente,
                    cliente : res[0]
                });
            });   
        }
        this.listarClientes();
        this.toogleModalCliente();
    }

    limpiarCliente = () => {
        const { cliente } = this.state;
        cliente.codigoCliente = "";
        cliente.codigoTipoDocumento= "";
        cliente.numeroDocumento= "";
        cliente.telefonoCliente= "";
        cliente.correoCliente= "";
        cliente.direccionCliente= "";
        cliente.nombreCliente= "";
        cliente.enRepresentacion= "";
        cliente.razonSocial= "";

        this.setState({ cliente });
    }

    toogleModalCliente = () => {
        if(!this.state.creacionCliente){
            this.limpiarCliente();
        }
        this.setState({
            tituloModal : "REGISTRAR",
            modalClienteOpen : ! this.state.modalClienteOpen,
        });
    }

    toogleModalConfirmar = () => {
        this.setState({ modalConfirmarOpen : ! this.state.modalConfirmarOpen });
    }

    onChangeTipoBuscadorProducto = (event) => {
        const tipoBuscadorProducto = event.target.value;
        this.setState(() => ({ tipoBuscadorProducto }));
    }

    onChangeBuscadorProducto = (event) => {
        const textoBuscar =  event.target.value;
        let sugerenciasProducto = [];

        if(textoBuscar.length > 0){
            //console.log(this.state.productos);
            const regex = new RegExp(`^${textoBuscar}`, 'i');          
            sugerenciasProducto = this.state.productos.sort().filter(producto => regex.test(producto[this.state.tipoBuscadorProducto]));
        }
        this.setState(()=>({ sugerenciasProducto , nombreProductoBuscar : textoBuscar }));
    }

    onChangeBuscadorCliente = (event) => {
        const textoBuscar =  event.target.value;
        let sugerenciasCliente = [];
        this.setCliente(event);
        if(textoBuscar.length > 0){
            const regex = new RegExp(`^${textoBuscar}`, 'i');          
            sugerenciasCliente = this.state.clientes.sort().filter(cliente => regex.test(cliente["nombreCliente"]));
        }
        this.setState(()=>({ sugerenciasCliente }));
    }

    setValorTotalVenta = () => {
        var valorTotalVenta = 0;
        this.state.detalles.forEach(detalle => {
            valorTotalVenta = valorTotalVenta + detalle.valorTotalVD; 
        });
        this.setState({ valorTotalVenta });
    }

    borrarDetalleVenta = (codigoVD) =>{
        del_Venta_Detalle_CodigoVD(codigoVD)
        .then(res => {
            this.listarDetallesVentasVista();
        });
    }

    buscarIndexDetalles = (detalle) => {
        const { detalles } = this.state;
        const index = detalles.indexOf(detalle);
        return index;
    }

    buscarIndexProductos = (producto) => {
        const { productos } = this.state;
        const index = productos.indexOf(producto);
        return index;
    }

    borrarDetalleVentaTemporal = () =>{

        const { detalle } = this.state;  
        const index = this.buscarIndexDetalles(detalle);
        var detalles = Object.assign( [] , this.state.detalles);
        detalles.splice(index,1);
        this.setState({ 
            detalles : detalles,

        }, () => {
            this.setValorTotalVenta();
        });
        del_Venta_Detalle_CodigoVD(detalle.codigoVD).then( res => { });
    }

    borrarProductoDeSugeriancia = (producto) => {
        const index = this.buscarIndexProductos(producto);
        var productos = Object.assign([], this.state.productos);
        productos.splice(index,1);
        this.setState({ productos : productos });
    }

    botonConfirmado = () => {
        const { tipoAccion } = this.state;

        if(tipoAccion === "ELIMINAR-DETALLE"){
            this.borrarDetalleVentaTemporal();
            this.toogleModalConfirmar();
        }
        if(tipoAccion === "ANULAR-VENTA"){
            this.anularVenta();
        }
        if(tipoAccion === "CONFIRMAR-VENTA"){
            this.confirmarGuardarVenta();
            this.toogleModalConfirmar();
        }
    }

    verificarEliminar = (detalle, tipoAccion) => {
        const { mensajeAlerta } = this.state;
        switch (tipoAccion) {
            case "ELIMINAR-DETALLE":
                    mensajeAlerta["textoMensaje"]="Se eliminar√° el Registro, ¬øDesea Continuar?";
                    mensajeAlerta["tipoBotonConfirmar"]="btn btn-danger";
                    mensajeAlerta["textoBotonConfirmar"]="Eliminar";
                    mensajeAlerta["mostrarConfirmar"]=true;
                    mensajeAlerta["tipoBotonCancelar"]="btn btn-info";
                    mensajeAlerta["textoBotonCancelar"]="Cancelar"; 
                    mensajeAlerta["mostrarCancelar"]=true;
                break;
            case "ANULAR-VENTA":
                    mensajeAlerta["textoMensaje"]="Se anular√° la Venta, ¬øDesea Continuar?";
                    mensajeAlerta["tipoBotonConfirmar"]="btn btn-danger";
                    mensajeAlerta["textoBotonConfirmar"]="Anular";
                    mensajeAlerta["mostrarConfirmar"]=true;
                    mensajeAlerta["tipoBotonCancelar"]="btn btn-info";
                    mensajeAlerta["textoBotonCancelar"]="Cancelar";
                    mensajeAlerta["mostrarCancelar"]=true;
                break;
            default:
                break;
        }
        this.setState({ detalle : detalle , tipoAccion : tipoAccion , mensajeAlerta : mensajeAlerta});
        this.toogleModalConfirmar();
    }

    crearDetalleVenta = (Detalle) => {
        put_Venta_Detalle_Temporal(Detalle)
            .then(res => {
        });
    }

    agregarProductos = (producto) =>{
        this.setState({
            productos : this.state.productos.concat(producto),
        });    
    }

    crearDetalleVentaTemporal = (Producto , cantidad) => {
        const detalleVentaBD = {
            codigoVenta : this.state.codigoVenta,
            codigoProducto : Producto.codigoProducto,
            codigoPrecio : Producto.codigoPrecio,

        }
        post_Venta_Detalle_Temporal(detalleVentaBD).then( res => {
            this.setState({
                codigoVDTemporal :  res[0][0].codigoVD
            }, () => {
                const detalleVenta = {
                    urlMedia : Producto.urlMedia,
                    descripcionProducto : Producto.descripcionProducto,
                    detalleProducto : Producto.detalleProducto, // Nuevo
                    descripcionPresentacion : Producto.descripcionPresentacion,
                    nombreTipoProducto : Producto.nombreTipoProducto, // Nuevo 
                    valorPrecio : Producto.valorPrecio,
                    cantidadProducto : cantidad,
                    stockExistente: Producto.stockExistente,
                    valorTotalVD : Producto.valorPrecio * cantidad,
                    codigoVD : this.state.codigoVDTemporal,
                    codigoProducto : Producto.codigoProducto,
                    codigoPrecio : Producto.codigoPrecio,
                    codigoVenta : this.state.codigoVenta,
                    codigoLocal : localStorage.getItem("codigoLocal"),
                    valorModificado : 0,
                    valorDescuento : 0,
                }
                this.setState({
                    detalles : this.state.detalles.concat(detalleVenta),
                }, () => {
                    this.setValorTotalVenta();
                });        
            });
        });
    }

    editarDetalleVentaTemporal = (e ,detalle) => { // SE RECIBE EL DETALLE DE VENTA
        const index = this.buscarIndexDetalles(detalle); // SE OBTIENE LA UBICACION DEL DETALLE
        const detalleEditar = Object.assign( {} , this.state.detalles[index] ); // SE CREA UN DETALLE DE VENTA A PARTIR DE LA LISTA DE DETALLES
        const cantidadProducto = e.target.value;
        detalleEditar.cantidadProducto = cantidadProducto; //
        detalleEditar.valorTotalVD = detalle.valorPrecio * cantidadProducto;
        const detalles = Object.assign( [] , this.state.detalles);
        detalles[index] = detalleEditar;
        this.setState({ detalles : detalles }, () => {
            this.setValorTotalVenta();
        });
    }

    verificarProductoAgregar = (Producto) => {
        const { detalles } = this.state;
        var existe = false;
        detalles.forEach(det => {
            if(det.codigoProducto === Producto.codigoProducto){
                existe = true;
            }
        });
        return existe;
    }

    seleccionarProducto (Producto) {
        const existeProducto = this.verificarProductoAgregar(Producto);
        if(!existeProducto){         
            get_Producto_Venta(Producto.codigoProducto).then(res => {
                if(res[0].stockExistente > 0){
                    this.crearDetalleVentaTemporal(res[0],1);  
                    this.setState(() => ({
                        nombreProductoBuscar: "",
                        sugerenciasProducto: [],
                    }));
                }else{
                    alert("Sin Stock Disponible");
                }
            });
        }else{
            alert("Producto Ya Fue Registrado");
        }
    }

    seleccionarCliente (Cliente) {
        const clienteSeleccion = Cliente;   
        this.setState(() => ({
            nombreCliente:clienteSeleccion.nombreCliente,
            cliente : clienteSeleccion,
            sugerenciasCliente : [],
        }));
    }
    
    mostrarSugerenciasProductoLista () {
        const { sugerenciasProducto } =  this.state;
        if(sugerenciasProducto.length === 0){
            return null;
        }
        return (
            <div className="AutoCompleteText">
                <ul>
                    {sugerenciasProducto.map((item) => 
                        <li key={item.codigoProducto} onClick={()=>this.seleccionarProducto(item)}>
                            {item.descripcionProducto + " - " + item.detalleProducto}
                        </li>
                    )}
                </ul>
            </div>
        );
    }

    mostrarSugerenciasClienteLista () {
        const { sugerenciasCliente } =  this.state;
        if(sugerenciasCliente.length === 0){
            return null;
        }
        return (
            <div className="AutoCompleteText">
                <ul>
                    {sugerenciasCliente.map((item) => 
                        <li key={item.id}
                            onClick= {()=>
                                this.seleccionarCliente(item)
                            }
                        >
                            {item.nombreCliente}
                        </li>
                    )}
                </ul>
            </div>
        );
    }

    crearVentaVacio(){
        const codigoUsuario = localStorage.getItem("codigoUsuario");
        const codigoLocal = localStorage.getItem("codigoLocal");

        get_Venta_Vacio( codigoUsuario , codigoLocal)
        .then(res => {
            this.setState({
                codigoVenta : res[0].codigoVentaGenerado
            });
        });
    }

    listarProductosPrecioLocal(codigoLocal){
        get_Producto_Local_Venta_Lista(codigoLocal).then(res => {
            this.setState({ productos : res });
        });
    }

    listarDetallesVentasVista(){
        get_Venta_Detalle_Codigo_Venta_Vista(this.state.codigoVenta)
         .then(res => {
             this.setState({ detallesVentas : res });
         });
    }

    listarTiposDocumentos(){
        get_Tipo_Documento_Lista()
        .then(res=> {
            this.setState({ tiposDocumentos : res });
        });
    }

    listarClientes(){
        get_Cliente_Lista()
        .then(res => {
            this.setState({ clientes : res });
        });
    }

    llenarListas = () => {
        const codigoLocal = localStorage.getItem("codigoLocal");
        this.listarProductosPrecioLocal(codigoLocal);
        this.listarTiposDocumentos();
        this.listarClientes();
    }

    resetEstado = () => {
        this.setState(estadoInicial);
        window.removeEventListener('beforeunload',(e) => {
            this.anularVenta();
            e.returnValue = "Se cancelo la vent, Desea Salir?";
        });
    }

    confirmarGuardarVenta = () => {
        const { detalles } = this.state;
        detalles.forEach(detalle => {
            this.crearDetalleVenta(detalle);
        });
        const ventaGuardar = {
            codigoVenta : this.state.codigoVenta,
            codigoUsuario : localStorage.getItem("codigoUsuario"),
            codigoCliente : this.state.cliente.codigoCliente,
            codigoLocal :localStorage.getItem("codigoLocal"),
            valorTotalVenta : this.state.valorTotalVenta,
            valorModificado : this.state.valorTotalVenta,
            valorDescuento : 0,
            montoRecibido : this.state.montoRecibido
        }
        put_Venta(ventaGuardar).then(res => {
            if(res){
                this.resetEstado();
            }
        });
    }

    guardarVenta = () => {
        const { detalles, cliente , mensajeAlerta} = this.state;

        if(detalles.length > 0 ){
            if(cliente.codigoCliente.length > 0){
                if(this.state.montoRecibido >= this.state.valorTotalVenta){
                    mensajeAlerta["textoMensaje"]="Se vender√° ".concat(" Producto(s) con Valor Total S/. " , this.state.valorTotalVenta.toFixed(2));
                    mensajeAlerta["tipoBotonConfirmar"] = "btn btn-success";
                    mensajeAlerta["textoBotonConfirmar"] = "Confirmar";
                    mensajeAlerta["mostrarConfirmar"] = true;
                    mensajeAlerta["tipoBotonCancelar"] = "btn btn-danger";
                    mensajeAlerta["textoBotonCancelar"] = "Cancelar";
                    mensajeAlerta["mostrarCancelar"] = true;
                }
                else{
                    mensajeAlerta["textoMensaje"]="El monto recibido es menor al valor total de Venta.";
                    mensajeAlerta["mostrarConfirmar"] = false;
                    mensajeAlerta["tipoBotonCancelar"] = "btn btn-danger";
                    mensajeAlerta["textoBotonCancelar"] = "Aceptar";
                    mensajeAlerta["mostrarCancelar"] = true;
                }
            }else{
                mensajeAlerta["textoMensaje"]="No se registro un Cliente";
                mensajeAlerta["mostrarConfirmar"] = false;
                mensajeAlerta["tipoBotonCancelar"] = "btn btn-danger";
                mensajeAlerta["textoBotonCancelar"] = "Aceptar";
                mensajeAlerta["mostrarCancelar"] = true;
            }
        }else{
            mensajeAlerta["textoMensaje"]="No se registro Productos para la Venta";
            mensajeAlerta["mostrarConfirmar"] = false;
            mensajeAlerta["tipoBotonCancelar"] = "btn btn-danger";
            mensajeAlerta["textoBotonCancelar"] = "Aceptar";
            mensajeAlerta["mostrarCancelar"] = true;
        }
        this.setState({ mensajeAlerta , tipoAccion : "CONFIRMAR-VENTA" });
        this.toogleModalConfirmar();   
    }

    eliminarVenta = (codigoVenta) => {
        del_Venta(codigoVenta).then(res => {
        });
        del_Venta_Detalle_Codigo_Venta(codigoVenta).then( res => {
        });
        this.resetEstado();
    }

    anularVenta = () => {
        if(this.state.codigoVenta.length > 0){
            this.eliminarVenta(this.state.codigoVenta);  
        }
    }

    confirmarCrearVenta = () => {
        window.addEventListener('beforeunload',(e) => {
            this.anularVenta();
            e.returnValue = "Se cancelo la venta, Desea Salir?";
        });
        this.setState({
            confirmarCrearVenta : ! this.state.confirmarCrearVenta
        },() => {
            this.llenarListas();
            this.crearVentaVacio();
        });
    }

    render() {
        if(!this.state.confirmarCrearVenta){
            return(
                <Menu>
                <div className="container p-5">
                    <div className="row justify-content-center">
                        <div className="col-md-4 mx-auto">
                            <button
                                className="form-control btn btn-success"
                                onClick={this.confirmarCrearVenta}
                            >Confirmar Crear Venta</button>
                        </div>
                    </div>
                </div>
                </Menu>
            )
        }else{
            const { nombreProductoBuscar } =  this.state
            const columns = [
                {
                    Header: "ITEM",
                    style:{
                        textAlign: "center"
                    },
                    Cell: props =>{
                        return(
                            <label>{props.original.codigoVD}</label>
                        )
                    },
                    show:false
                },
                {
                    Header:"IMAGEN",
                    Cell: props =>{
                        return(
                            <div className="">
                                <img src={props.original.urlMedia} alt={props.original.descripcionProducto} className="card-img-top media_producto"/>
                            </div>
                        )
                    },
                    maxWidth: 200
                },
                {
                    Header: "PRODUCTO",
                    style:{
                        textAlign: "center"
                    },
                    Cell: props => {
                        return(
                            <div className="mt-2 infoProducto">
                                <div>{props.original.descripcionProducto}</div>
                                <div>{props.original.detalleProducto}</div>
                                <div>{props.original.nombreTipoProducto}</div>
                                <div>{props.original.descripcionPresentacion}</div>
                                <div>Stock: {props.original.stockExistente}</div>
                                <div>Precio: S/.{(props.original.valorPrecio).toFixed(2)}</div> 
                            </div>
                        )
                    },
                    width : 250,
                    minWidth: 150,
                    maxWidth: 450
                },
                {
                    Header: "SUB. TOTAL",
                    style:{
                        textAlign: "center"
                    },
                    Cell: props => {
                        return(
                            <div className="container">
                                <div className="row justify-content-center mt-1">
                                    <div className="col-11 form-group">
                                        <input className="form-control text-center" id= "cantidadProducto" type="number" min="1"
                                            max={""+props.original.stockExistente+""} placeholder={props.original.cantidadProducto}
                                            value={props.original.cantidadProducto}
                                            onChange = { e => { this.editarDetalleVentaTemporal( e , props.original)}}
                                        />   
                                    </div>
                                    <div className="col-11 form-group">
                                        <input className="form-control text-center" id= "descripcionProducto" type="text" readOnly
                                            value = {"S/. " + (props.original.valorTotalVD).toFixed(2)}> 
                                        </input>
                                    </div>
                                    <div className="col-11">
                                        <button className="btn btn-danger"
                                            onClick = {() => {this.verificarEliminar(props.original,"ELIMINAR-DETALLE")}}
                                        > <DeleteForeverOutlined/> </button>
                                    </div>
                                </div>
                            </div>                                                    
                        )
                    }
                },
            ]

            return (
                <Menu>
                <div className="container p-4">
                    <div className="row">
                        <div className="container col-md-8">
                            <div className="container">
                                <div className="card">
                                    <div className="card-header">
                                        <h3>Venta de Productos</h3>
                                    </div>
                                    <div className="card-body">
                                        <div className="row">
                                            <div className="col-md-4 form group">
                                                <select className="form-control custom-select"
                                                    onChange={this.onChangeTipoBuscadorProducto}
                                                >
                                                    <option value="descripcionProducto">NOMBRE</option>
                                                    <option value="detalleProducto">MARCA</option>
                                                    <option value="codigoProducto">CODIGO</option>
                                                </select>
                                            </div>
                                            <div className="col-md-8">
                                                <input className="form-control" type="text"
                                                    placeholder="Busqueda de Producto"
                                                    value={nombreProductoBuscar}
                                                    onChange={this.onChangeBuscadorProducto}
                                                    autoComplete={"off"}
                                                ></input>
                                                {this.mostrarSugerenciasProductoLista()}
                                            </div>
                                            <div className="col-md-12 mt-3">
                                                <ReactTable
                                                    columns = {columns}
                                                    noDataText = {"Cargando Datos..."}
                                                    data = {this.state.detalles}
                                                    filterable={false}
                                                    defaultPageSize={5}
                                                    showPaginationTop={false}
                                                    showPaginationBottom={false}
                                                >
                                                </ReactTable>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div className="container mt-3">
                                <div className="row justify-content-center text-center">
                                    <div className="col-md-4">
                                        <button className="btn btn-info"
                                            onClick={this.guardarVenta}
                                        > <DoneOutlineRounded/> </button>
                                    </div>
                                    <div className="col-md-4">
                                        <button className="btn btn-danger"
                                            onClick = {() => {this.verificarEliminar("","ANULAR-VENTA")}}
                                        > <RemoveShoppingCartOutlined/> </button>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="container col-md-4">
                            <div className="bg-primary">
                                <h1>S/. {(this.state.valorTotalVenta).toFixed(2)}</h1>
                            </div>
                            <div className="row">
                                <div className="col-md-6 form-group">
                                    <label htmlFor="documentoVenta"> DOCUMENTO: </label>
                                    <select className="form-control" id= "documentoVenta"> 
                                        <option>Boleta</option>
                                        <option>Factura</option>
                                    </select>
                                </div>
                                <div className="col-md-6 form-group">
                                    <label htmlFor="tipoPagoVenta"> TIPO PAGO: </label>
                                    <select className="form-control" id= "tipoPagoVenta"> 
                                        <option>Efectivo</option>
                                        <option>Tarjeta</option>
                                    </select>
                                </div>
                                <div className="col-md-6 form-group">
                                    <label htmlFor="serieVenta"> SERIE: </label>
                                    <input className="form-control" id= "serieVenta" type="text" placeholder="000"/>
                                </div>
                                <div className="col-md-6 form-group">
                                    <label htmlFor="numeroVenta"> N¬∞ DE VENTA: </label>
                                    <input className="form-control" id= "numeroVenta" type="text" value={this.state.codigoVenta} readOnly/>
                                </div>
                                <div className="col-md-12 form-group">
                                    <label htmlFor="nombreCliente"> CLIENTE: </label>
                                    <div className="row">
                                        <div className="col-md-9">
                                            <input className="form-control" id= "nombreCliente" type="text"
                                                //value={this.state.cliente.nombreCliente}
                                                value={this.state.nombreCliente}
                                                onChange={this.onChangeBuscadorCliente}
                                                autoComplete={"off"}
                                                />
                                            {this.mostrarSugerenciasClienteLista()}
                                        </div>
                                        <div className="col-md-3">
                                            <button className="form-control btn btn-info"
                                                onClick={this.toogleModalCliente}
                                            > <PersonAddRounded/> </button>
                                        </div>
                                    </div>
                                </div>
                                <div className="col-md-12 form-group">
                                    <label htmlFor="montoRecibido"> MONTO RECIBIDO S/. </label>
                                    <input className="form-control" id= "montoRecibido" type="number" placeholder="0"
                                        onChange={e => {this.setMontoRecibido(e.target.value)}}
                                        value={this.state.montoRecibido}
                                    />
                                </div>
                            </div>
                            <Divider className="mb-3" />
                            <div className="row">                           
                                <div className="col-md-6 form-group">
                                    <label>SUB TOTAL</label>
                                </div>
                                <div className="col-md-6 form-group">
                                    <label>
                                    S/. { (parseFloat(this.state.valorTotalVenta) - (parseFloat(this.state.valorTotalVenta) * 0.18)).toFixed(2) }
                                    </label>
                                </div>
                                <div className="col-md-6 form-group">
                                    <label>I.G.V (18%)</label>
                                </div>
                                <div className="col-md-6 form-group">
                                    <label>
                                        S/. {(parseFloat(this.state.valorTotalVenta) * 0.18).toFixed(2) }
                                    </label>
                                </div>
                                <div className="col-md-6 form-group">
                                    <label>TOTAL</label>
                                </div>
                                <div className="col-md-6 form-group">
                                    <label>
                                        S/. {(parseFloat(this.state.valorTotalVenta)).toFixed(2)}
                                    </label>
                                </div>
                                <div className="col-md-6 form-group">
                                    <label>VUELTO: </label>
                                </div>
                                <div className="col-md-6 form-group">
                                    <label>
                                        S/. {(parseFloat(this.state.montoRecibido - this.state.valorTotalVenta)).toFixed(2)}
                                    </label>
                                </div>
                            </div>
                            <Modal 
                                    isOpen={this.state.modalClienteOpen}
                                    centered={true}
                                    size={"xm"}
                            >
                                <ModalHeader toggle={this.toogleModalCliente.bind(this)}>{this.state.tituloModal}</ModalHeader>    
                                <ModalBody>
                                    <ClienteForm 
                                        tiposDocumentos = {this.state.tiposDocumentos}
                                        setCliente = {this.setCliente}
                                        registrarCliente = {this.registrarCliente}
                                    ></ClienteForm>
                                </ModalBody>
                            </Modal>

                            <Modal 
                                isOpen={this.state.modalConfirmarOpen}
                                centered={true}
                                size={"xs"}
                            >
                                <ModalHeader toggle={this.toogleModalConfirmar.bind(this)}>Alerta</ModalHeader>    
                                <ModalBody>
                                    <div className="">
                                        <div className="row justify-content-center text-center">
                                            <div className="col-md-12 mb-4">
                                                {/*<span>Se eliminar√° el Registro, ¬øDesea Continuar?</span>*/}
                                                <span>{this.state.mensajeAlerta.textoMensaje}</span>
                                            </div>
                                            <div className="col-3 col-sm-3 col-md-3" hidden={!this.state.mensajeAlerta.mostrarConfirmar}>
                                                <button className={this.state.mensajeAlerta.tipoBotonConfirmar}
                                                    onClick={this.botonConfirmado}
                                                >{this.state.mensajeAlerta.textoBotonConfirmar}</button>
                                            </div>
                                            <div className="col-3 col-sm-3 col-md-3" hidden={!this.state.mensajeAlerta.mostrarCancelar}>
                                                <button className={this.state.mensajeAlerta.tipoBotonCancelar}
                                                    onClick = {this.toogleModalConfirmar.bind(this)}
                                                >{this.state.mensajeAlerta.textoBotonCancelar}</button>
                                            </div>
                                        </div>
                                    </div>
                                </ModalBody>
                            </Modal>
                        </div>
                    </div>
                </div>
                </Menu>
            )
        }
    }
}

export default venta
