//ELIMINAR DETALLE DE VENTA POR CODIGO
export function del_Venta_Detalle_CodigoVD(codigoVD){ 
    const urlFetch = BaseUrl + 'codigovd/' + codigoVD;
    return new Promise((resolve,reject)=>{
        fetch(urlFetch,{
            method:'DELETE',
            headers: new Headers({
                'Content-type':'application/json'
            })
        })
        .then((response) => response.json())
        .then((responseJson) => {
            resolve(responseJson);
        })
        .catch((error)=>{
            reject(error);
        });
    });
}

