import React, { Component } from 'react';
class ExportExcel extends Component{
    render(){
        return(
            <div className="container row">
                <div>
                    <table hidden={true} id="table-to-xls">
                        <thead>
                            <tr>
                                <th>TPD ID</th>
                                <th>Codigo</th>
                                <th>Media</th>  
                                <th>Descripcion</th>  
                            </tr>                
                        </thead>
                        <tbody>
                        {
                            this.props.tpMedia.map(tpMed =>{
                                return(
                                    <tr key={tpMed.id}>
                                        <td>{tpMed.id}</td>
                                        <td>{tpMed.codigoTipoMedia}</td>
                                        <td>{tpMed.nombreTipoMedia}</td>                                        
                                        <td>{tpMed.descripcionTipoMedia}</td>       
                                    </tr>
                                );    
                            })
                        }
                        </tbody>
                    </table>
                </div>
            </div>
        );
    }
}

export default ExportExcel