import React, { Component } from 'react';

class ExportExcel extends Component{
    render(){
        return(
            <div className="container row">
                <div>
                    <table hidden={true} id="table-to-xls">
                        <thead>
                            <tr>
                                <th>MED ID</th>
                                <th>Codigo</th>
                                <th>Sucursal</th>
                                <th>Tipo</th>
                                <th>Local</th>
                                <th>Direccion</th>
                                <th>Telefono</th>
                                <th>Horario</th>
                                <th>IP</th>
                                <th>MAC</th>
                            </tr>                                           
                        </thead>
                        <tbody>
                        {
                            this.props.locales.map(loc =>{
                                return(
                                    <tr key={loc.id}>
                                        <td>{loc.id}</td>
                                        <td>{loc.codigoLocal}</td>
                                        {/*<td>{loc.codigoSucursal}</td>*/}
                                        <td>{loc.nombreSucursal}</td>
                                        {/*<td>{loc.codigoTipoLocal}</td>*/}
                                        <td>{loc.nombreTipoLocal}</td>
                                        <td>{loc.nombreLocal}</td>
                                        <td>{loc.direccionLocal}</td>
                                        <td>{loc.numeroTelefono}</td>
                                        <td>{loc.horarioAtencion}</td>
                                        <td>{loc.ipLocal}</td>
                                        <td>{loc.macLocal}</td>
                                    </tr>
                                );    
                            })
                        }
                        </tbody>
                    </table>
                </div>
            </div>
        );
    }
}

export default ExportExcel