import React, { Component } from 'react';
import { post_Sesion_Login } from '../Data/inicioSesionData.js'
import { get_Local_Lista } from '../Data/LocalData.js';
import { Redirect } from 'react-router-dom';
import { Alert } from 'reactstrap';

export class login extends Component {
    constructor(props){
        super(props);
        this.state = {
            nombreUsuario : "",
            contraUsuario : "",
            codigoLocal : "",
            idUsuario : "",
            locales : [],
            formValidado : false ,
            classInputNombre:"",
            classInputContra:"",
            classSelectLocal:"",
            message : {
                text:'',
                alert: ''
            }
        }
        this.login = this.login.bind(this);
        this.onChange = this.onChange.bind(this);
    }

    onChangeLocal = () => {
        
    }

    login(){
        if(this.state.codigoLocal.length > 0){
            if(this.state.nombreUsuario.length > 0){
                if(this.state.contraUsuario.length > 0){
                    post_Sesion_Login(this.state).then((result)=>{
                        switch(result.codEstado){
                            case "402" :
                                this.setState({ 
                                    message : { text:"ERROR EN CREAR SESION", alert:"danger"}
                                });
                            break;
                            case "701" :
                                this.setState({ 
                                    message : { text:"USUARIO NO EXISTE", alert:"danger"}
                                });
                            break;
                            case "702" :
                                this.setState({ 
                                    message : { text:"CONTRASE√ëA INCORRECTA", alert:"danger"}
                                });
                            break;
                            case "703" :
                                this.setState({ 
                                    message : { text:"USUARIO CONECTADO", alert:"danger"}
                                });
                            break;
                            case "800":
                                localStorage.setItem('codigoUsuario', result.codigoUsuario);
                                localStorage.setItem('tokenUsuario', result.token);
                                localStorage.setItem('codigoLocal',this.state.codigoLocal);
                                this.setState({ 
                                    message : { text:"SESION CORRECTA", alert:"success"}
                                });
                            break;
                            default:
                                this.setState({ 
                                    message : { text:"SIN CODIGO DE ESTADO", alert:"danger"}
                                });
                            break;
                        }
                    });
                } else{ this.msgAlert="danger"; this.msgText="INGRESE CONTRASE√ëA"; }
            } else{ this.msgAlert="danger"; this.msgText="INGRESE USUARIO"; }
        } else{ this.msgAlert="danger"; this.msgText="SELECCIONE LOCAL"; }
        this.setState({ 
            message : { text : this.msgText, alert : this.msgAlert}
        });
    }
    onChange(e){
        this.setState({ [e.target.id] : e.target.value });
    }

    componentDidMount(){
        get_Local_Lista(this.state).then((result)=>{
            this.setState({locales: result});
        });   
    }
    render() {
        const codigoUsuario  = localStorage.getItem('codigoUsuario');
        if(codigoUsuario !== "" && codigoUsuario !== null){
            return (<Redirect to={{ pathname : '/app' }}/>)
        }
        return (
            <div className="container p-4">
                <div className="row">
                    <div className="col-md-4 mx-auto">
                        <div className="card text-center">
                            <div className="card-header">
                                <h3>SIGNIN</h3>
                            </div>
                            <div className="container py-2">
                                { this.state.message.text !==''?( <Alert color={this.state.message.alert}>
                                { this.state.message.text} </Alert> ): '' }
                            </div>
                            <div className="card-body">
                                <img src="/img/Logo-World-Connect.png" alt="Logo WCS" className="card-img-top mx-auto m-2 rounded-circle w-50"/>
                                <div>
                                    <div className="form-group">
                                        <select className={"form-control custom-select " + this.state.classSelectLocal} id="codigoLocal" onChange={this.onChange}>
                                            <option value = "">Seleccione un local</option>
                                            {this.state.locales.map((local) => 
                                            ( <option value={local.codigoLocal} key={local.id}>{local.nombreLocal}</option>))}
                                        </select>
                                    </div>
                                    <div className="form-group">
                                        <input type="text" className={"form-control " + this.state.classInputNombre} name="nombreUsuario" id="nombreUsuario" placeholder="Usuario" maxLength="50" onChange={this.onChange}/>
                                    </div>
                                    <div className="form-group">
                                        <input type="password" className={"form-control " + this.state.classInputContra} name="contraUsuario" id="contraUsuario" placeholder="Password" maxLength="250" onChange={this.onChange}/>        
                                    </div>
                                    <div className="form-group">
                                        <button className="btn btn-success btn-block" onClick={this.login}> 
                                            LOGIN
                                        </button>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default login;
