import React, { Component} from 'react';
import ReactTable from 'react-table';
import "react-table/react-table.css";

import ReactHTMLTableExcel from 'react-html-table-to-excel';

import { Modal , ModalHeader, ModalBody, Alert } from 'reactstrap';

import data from '../../../db_data';
import ExportToExcel from "../../Export/excel/Local";

import { get_Local_Tipo_Lista ,  get_Local , del_Local , post_Local, put_Local } from '../../Data/LocalData';

import { get_Tipo_Local_Lista } from '../../Data/tiposData';
import { get_Sucursal_Lista } from '../../Data/SucursalData';


import ProductoLista from "../Producto/ProductoLista";

//import { get_Producto_Precio_Venta_Local_Lista } from '../../Data/productoData';
import { get_Producto_Local_Venta_Lista } from '../../Data/productoData';

import Menu from '../../Pages/menu'

export default class LocalLista extends Component {

    BaseUrl = data + '/api/locales';

    constructor (props){
        super(props);
        this.state = {
            local:{
                codigoLocal : "",
                codigoSucursal : "",
                codigoTipoLocal : "",
                nombreLocal : "",
                direccionLocal : "",
                numeroTelefono : "",
                horarioAtencion : "",
                ipLocal : "",
                macLocal : "",

                nombreTipoLocal:"Seleccione Tipo",
                nombreSucursal:"Seleccione Sucursal",
            },
        
            locales:[],
            localExcel:[],
            filtered:[],
        
            sucursales:[],
            tiposLocales:[],
        
            productos:[],
            modalListaProductosOpen : false,

            message : {
                text:'',
                alert: ''
            },
            messageActivo:false,
            tipoAccion:"post",
            modalFormOpen: false,
            tituloModal:"",
            nombreTipoLocal: props.nombreTipoLocal,

            modalConfirmarOpen: false,
            tipoEliminar : "",
            codigoLocalEliminar : "",

        };
        this.setLocal = this.setLocal.bind(this);
        this.toogleModalListaProducto = this.toogleModalListaProducto.bind(this);
    }

    toogleModalConfirmar = () => {
        this.setState({ modalConfirmarOpen : ! this.state.modalConfirmarOpen });
    }

    mostrarModal = (codigoLocalEliminar) => {
        this.setState({ codigoLocalEliminar : codigoLocalEliminar });
        this.toogleModalConfirmar();
    }

    borrarConfirmado = () => {
        const { codigoLocalEliminar } = this.state;
        this.eliminiarLocal(codigoLocalEliminar);
        this.toogleModalConfirmar();
    }

    setLocal(e){
        const { local } = this.state;
        local[e.target.id] = e.target.value;
        this.setState({ local });
    }

    onFilteredChangeCustom = (value, accessor) => {
        let filtered = this.state.filtered;
        let insertNewFilter = 1;
    
        if (filtered.length) {
          filtered.forEach((filter, i) => {
            if (filter["id"] === accessor) {
              if (value === "" || !value.length) filtered.splice(i, 1);
              else filter["value"] = value;
              insertNewFilter = 0;
            }
          });
        }
    
        if (insertNewFilter) {
          filtered.push({ id: accessor, value: value });
        }
    
        this.setState({ filtered: filtered });
    };

    mostrarAlerta = (tiempo) => {
        setTimeout(()=>{
            this.setState({ message : { text : '' , alert : ''} });            
        },tiempo);
    }
    cambiarAlerta(message){
        this.setState({ message:message });
        this.mostrarAlerta(3000);  
    }

    toogleModalListaProducto(){
        this.setState({
            modalListaProductosOpen : ! this.state.modalListaProductosOpen
        });
    }

    toogleModalForm(){   
        this.setState({
            modalFormOpen : ! this.state.modalFormOpen,
        });
        if(!this.state.modalFormOpen){
            this.listarLocal(this.state.nombreTipoLocal);
        }     
    }

    verificarAccion(codigoLocal,tituloModal){
        if(codigoLocal !== null && codigoLocal !== ""){
            get_Local(codigoLocal).then(res =>{
                if(res[0]){
                    this.setState({
                        local:res[0],
                        tipoAccion:"put",        
                        tituloModal: tituloModal
                    });
                }
            });
        }else{
            this.setState({ 
                tipoAccion:"post",
                tituloModal: tituloModal
            });
        }
        this.toogleModalForm();
    }


    eliminiarLocal = (codigoLocal) => {
        del_Local(codigoLocal)
        .then(res => {
            this.listarLocal(this.state.nombreTipoLocal);
        });
    }

    guardarLocal = () =>{
        const { local } = this.state;
        const message = {
            text:"Algo salio mal vuelve a intertarlo",
            alert:"danger"  
        };        
        switch (this.state.tipoAccion) {
            case "post":
                post_Local(local).then((result)=>{
                    message.alert = "success";
                    message.text = "Se guardo correctamente";
                    this.toogleModalForm();
                });
                break;
            case "put":
                put_Local(local).then((result)=>{
                    message.alert = "success";
                    message.text = "Se modifico correctamente";
                    this.toogleModalForm();
                });
                break;
            default:
                break;
        }
        this.cambiarAlerta(message);
    }

    listarLocal(nombreTipoLocal){
        if(nombreTipoLocal !== "" && nombreTipoLocal !== null){
            get_Local_Tipo_Lista(nombreTipoLocal, "").then(res => {
                this.setState({ locales : res })
            });
        }
    }

    listarSucursal(){
        get_Sucursal_Lista()
        .then( res => {
            this.setState({ sucursales : res });
        });
    }

    listarTipoLocal(){
        get_Tipo_Local_Lista()
        .then( res => {
            this.setState({ tiposLocales : res });
        });
    }

    listarProducto(codigoLocalSeleccionado){
        if(!this.state.modalListaProductosOpen){
            get_Producto_Local_Venta_Lista(codigoLocalSeleccionado)
            .then(res => {
                this.setState({ productos : res }, () => this.toogleModalListaProducto());
            });
        }
    }

    llenarListas(){
        this.listarLocal(this.state.nombreTipoLocal);
        this.listarSucursal();
        this.listarTipoLocal();
        //this.listarProducto(codigoLocal);
    }

    componentDidMount(){
        this.llenarListas();
    }
    render() {
        const columns = [
            {
                Header: "ID",
                accessor: "id",
                style:{
                    textAlign: "center"
                },
                show:false
            },
            {
                Header: "CODIGO LOCAL",
                accessor: "codigoLocal",
                style:{
                    textAlign: "center"
                },
                show:false
            },
            {
                Header: "CODIGO SUCURSAL",
                accessor: "codigoSucursal",
                style:{
                    textAlign: "center"
                },
                show:false
            },
            {
                Header: "CODIGO TIPO LOCAL",
                accessor: "codigoTipoLocal",
                style:{
                    textAlign: "center"
                },
                show:false
            },
            {
                Header: "MAC LOCAL",
                accessor: "macLocal",
                style:{
                    textAlign: "center"
                },
                show:false
            },
            {
                Header: "IP LOCAL",
                accessor: "ipLocal",
                style:{
                    textAlign: "center"
                },
                show:false
            },
            {
                Header: "LOCAL",
                style:{
                    textAlign: "center"
                },
                Cell : props => {
                    return (
                        <div className = "container">
                            <span>{props.original.nombreTipoLocal + " : " + props.original.nombreSucursal}</span>
                            <span>{props.original.nombreLocal + " : " + props.original.direccionLocal}</span>
                            <span>{props.original.numeroTelefono  + " : " + props.original.horarioAtencion}</span>
                        </div>
                    )
                }
            },
            {
                Header:"ACCIONES",
                Cell: props => {
                    return(
                        <div className="container">
                            <div className="row justify-content-center">
                                <div className="col-md-4">
                                    <button className="btn btn-danger"
                                        onClick={() => this.mostrarModal(props.original.codigoLocal)}
                                    >Borrar</button>
                                </div>
                                <div className="col-md-4 ml-auto">
                                    <button className="btn btn-warning"
                                        onClick = {() => this.verificarAccion(props.original.codigoLocal,"MODIFICAR")}
                                    >Editar</button>
                                </div>
                                <div className="col-md-4 ml-auto">
                                    <button className="btn btn-info"
                                        onClick = {() => this.listarProducto(props.original.codigoLocal)}
                                    >Productos</button>
                                </div>
                            </div>

                        </div>
                    )
                },                
                sortable: false,
                filterable: false
            }
        ]
        let buttonCard = {
            cursor: 'pointer'
        };
        return (
            <Menu>
            <div className="container p-4">
                {/*<MaterialTable></MaterialTable>*/}
                <div className="row">
                    <div className="col-md-11 mx-auto">
                        <div className="card ">
                            <div className="card-header">
                                <h3>{this.state.nombreTipoLocal}(S) EN EXISTENCIA</h3>
                            </div>    
                            <div className="m-2 text-center">
                                { this.state.message.text !==''?( <Alert color={this.state.message.alert}>{ this.state.message.text}</Alert> ) : '' }
                            </div>                      
                            <div className="row m-2">
                                <div className="col-sm" style={buttonCard}>
                                    <div className="card text-white bg-info">
                                        <div className="card-header">Registrar</div>
                                        <button className="btn card-body text-white text-justify"
                                            onClick={()=>{
                                                this.verificarAccion("","REGISTRAR")
                                            }}
                                        >Tipo de Local.</button>
                                    </div>
                                </div>
                                
                                <div className="col-sm" style={buttonCard}>
                                    <div className="card bg-success text-white">
                                        <div className="card-header">Exportar</div>
                                        <ReactHTMLTableExcel
                                            id="test-table-xls-button"
                                            className="btn card-body text-white text-justify"
                                            table="table-to-xls"
                                            filename="LocalExcel"
                                            sheet="hoja 1"
                                            buttonText="A Documento Excel."
                                        />
                                    </div>
                                </div>
                            </div>
                            <div className="card-body">
                                <ReactTable
                                    columns = {columns}
                                    noDataText = {"Cargando Datos..."}
                                    data = {this.state.locales}
                                    filterable
                                    filtered = {this.state.filtered}
                                    onFilteredChange = {(filtered, column, value) => {
                                        this.onFilteredChangeCustom(value, column.id || column.accessor);
                                      }}
                                      defaultFilterMethod={(filter, row, column) => {
                                        const id = filter.pivotId || filter.id;
                                        if (typeof filter.value === "object") {
                                          return row[id] !== undefined
                                            ? filter.value.indexOf(row[id]) > -1
                                            : true;
                                        } else {
                                          return row[id] !== undefined
                                            ? String(row[id]).indexOf(filter.value) > -1
                                            : true;
                                        }
                                      }}
                                    defaultPageSize={5}
                                    showPaginationTop={true}
                                    showPaginationBottom={false}
                                >
                                {(state, filtredData, instance)=>{
                                    this.reactTable = state.pageRows.map(
                                        local => {return local._original});
                                    return(
                                        <div>
                                            {filtredData()}
                                            <ExportToExcel locales = {this.reactTable}/>
                                        </div>
                                    )
                                }}
                                </ReactTable>
                            </div>

                            <Modal 
                                isOpen={this.state.modalListaProductosOpen}
                                centered={true}
                            >
                                <ModalHeader toggle={this.toogleModalListaProducto.bind(this)}>Productos en Existencia</ModalHeader>    

                                <ModalBody>
                                    <ProductoLista productos = { this.state.productos }></ProductoLista>
                                </ModalBody>
                            </Modal>

                            <Modal 
                                isOpen={this.state.modalFormOpen}
                                centered={true}
                                size={"xm"}
                            >
                                <ModalHeader toggle={this.toogleModalForm.bind(this)}>{this.state.tituloModal}</ModalHeader>    

                                <ModalBody>
                                    <div className="card ">
                                        <div className="card-header text-center">
                                            <h3>Local</h3>
                                        </div>
                                        <div className="card-body ">
                                            <div className="row">
                                                <div className="col-md-6 form-group">
                                                    <label for="codigoSucursal"> Sucursal: </label>
                                                    <select className={"form-control custom-select "} id="codigoSucursal" onChange={this.setLocal}>
                                                        <option value = {this.state.local.codigoSucursal}>
                                                                {this.state.local.nombreSucursal}
                                                        </option>
                                                        {this.state.sucursales.map( sucursal =>
                                                            <option key={sucursal.id} value={sucursal.codigoSucursal}>{sucursal.nombreSucursal}</option>
                                                        )}
                                                    </select>
                                                </div>
                                                <div className="col-md-6 form-group">
                                                    <label for="codigoTipoLocal"> Local: </label>
                                                    <select className={"form-control custom-select "} id="codigoTipoLocal" onChange={this.setLocal}>
                                                        <option value = {this.state.local.codigoTipoLocal}>
                                                                {this.state.local.nombreTipoLocal}
                                                        </option>
                                                        {this.state.tiposLocales.map( tpLoc =>
                                                            <option key={tpLoc.id} value={tpLoc.codigoTipoLocal}>{tpLoc.nombreTipoLocal}</option>
                                                        )}
                                                    </select>
                                                </div>
                                                <div className="col-md-6 form-group">
                                                    <label for="nombreLocal"> Nombre: </label>
                                                    <input id= "nombreLocal" className="form-control" type="text" value={this.state.local.nombreLocal}
                                                        onChange={this.setLocal}
                                                    />
                                                </div>
                                                <div className="col-md-6 form-group">
                                                    <label for="direccionLocal"> Direccion: </label>
                                                    <input id= "direccionLocal" className="form-control" type="text" value={this.state.local.direccionLocal}
                                                        onChange={this.setLocal}
                                                    />
                                                </div>
                                                <div className="col-md-6 form-group">
                                                    <label for="numeroTelefono"> Telefono: </label>
                                                    <input id= "numeroTelefono" className="form-control" type="text" value={this.state.local.numeroTelefono}
                                                        onChange={this.setLocal}
                                                    />
                                                </div>
                                                <div className="col-md-6 form-group">
                                                    <label for="horarioAtencion"> Horario: </label>
                                                    <input id= "horarioAtencion" className="form-control" type="text" value={this.state.local.horarioAtencion}
                                                        onChange={this.setLocal}
                                                    />
                                                </div>
                                                <div hidden={true} className="col-md-6 form-group">
                                                    <label for="ipLocal"> IP: </label>
                                                    <input id= "ipLocal" className="form-control" type="text" value={this.state.local.ipLocal}
                                                        onChange={this.setLocal}
                                                    />
                                                </div>
                                                <div hidden={true} className="col-md-6 form-group">
                                                    <label for="macLocal"> MAC: </label>
                                                    <input id= "macLocal" className="form-control" type="text" value={this.state.local.macLocal}
                                                        onChange={this.setLocal}
                                                    />
                                                </div>
                                            </div> 
                                            <div className="text-center">
                                                <button className="btn btn-primary"
                                                    onClick={this.guardarLocal}
                                                > REGISTRAR </button>
                                            </div>
                                        </div>
                                    </div>
                                </ModalBody>
                            </Modal>
                            <Modal 
                                    isOpen={this.state.modalConfirmarOpen}
                                    centered={true}
                                    size={"xm"}
                            >
                                <ModalHeader toggle={this.toogleModalConfirmar.bind(this)}>Alerta</ModalHeader>    
                                <ModalBody>
                                    <div className="container">
                                        <div className="row justify-content-center text-center">
                                            <div className="col-md-12 mb-4">
                                                <span>Se eliminar√° el Registro, ¬øDesea Continuar? </span>
                                            </div>
                                            <div className="col-md-3">
                                                <button className="btn btn-danger"
                                                    onClick={this.borrarConfirmado}
                                                >Eliminar</button>
                                            </div>
                                            <div className="col-md-3">
                                                <button className="btn btn-info"
                                                    onClick = {this.toogleModalConfirmar.bind(this)}
                                                >Cancelar</button>
                                            </div>
                                        </div>
                                    </div>
                                </ModalBody>
                            </Modal>
                        </div>
                    </div>
                </div>
            </div>
            </Menu>
        )
    }
}
