// GET ROL USUARIO API FROM SERVER NODE EXPRESS

import data from '../../db_data';
const BaseUrl = data + '/api/rol/usuario/';

// MOSTRAR USUARIO CODIGO
export function get_Rol_Usuario(codigoUsuario){
    let urlFetch = BaseUrl + codigoUsuario;
    return new Promise((resolve,reject)=>{
        fetch(urlFetch)
        .then((usuario) => usuario.json())
        .then((usuarioJson) => {
            resolve(usuarioJson);
        })
        .catch((error)=>{
            reject(error);
        });
    });
}